import util from '../graphUtilities.js';
import drawing from '../drawing.js';
import data from '../data.js';

export default {
    circleChart: function(categoricalColumn, settings, isDonut) {
        settings = util.setDefaultSettings(settings);

        // key: level
        // value: array of row numbers that have this level
        var rowsForEachLevel = data.findRowsForEachLevel(categoricalColumn);
        var levelProportions = this.findLevelProportions(rowsForEachLevel);

        var graph = util.startPieChart(settings);

        var cumulativeProportion = 0;
        var colorIndex = 0;
        for (var level in levelProportions) {
            var start = cumulativeProportion;
            var end = cumulativeProportion + levelProportions[level];
            var levelNameNoSpaces = level.split(' ').join('');

            graph = drawing.addCircleWedge(graph, {
                id: 'piewedge-' + levelNameNoSpaces,
                classes: 'pie-wedge',
                startPercentage: start,
                endPercentage: end,
                radius: settings.radius,
                fillColor: drawing.colors(colorIndex)
            });

            colorIndex++;

            var namePosition = drawing.getCircleCoordinatesForPercent((start + end) / 2, 1.3 * settings.radius);

            var percentage = (100 * levelProportions[level]).toFixed(1);
            var percentageToShow = percentage + "%";

            graph = drawing.addToolTip(graph, {
                x: namePosition[0],
                y: namePosition[1],
                level: levelNameNoSpaces,
                width: 100,
                height: 60,
                text: level + "<br>" + percentageToShow,
                type: 'circle',
            });

            cumulativeProportion += levelProportions[level];
        }

        if (isDonut) {
            graph = drawing.addCircle(graph, {
                cx: 0,
                cy: 0,
                r: 0.7 * settings.radius,
                fillColor: '#fff'
            });
        }

        return graph;
    },
    pieChart: function(categoricalColumn, settings) {
        return this.circleChart(categoricalColumn, settings, false);
    },
    donutChart: function(categoricalColumn, settings) {
        return this.circleChart(categoricalColumn, settings, true);
    },
    findLevelProportions: function(rowsForEachLevel) {
        var totalCount = 0;
        for (var level in rowsForEachLevel) {
            var levelCount = rowsForEachLevel[level].length;
            totalCount += levelCount;
            rowsForEachLevel[level] = levelCount;
        }

        for (var level in rowsForEachLevel) {
            rowsForEachLevel[level] = rowsForEachLevel[level] / totalCount;
        }

        return rowsForEachLevel;
    }
}



