<template>
    <div>
        <h3 v-if="!options.usingTwoSamples">Variable: <select v-model="options.oneSample.column"
            class="pick-test-variable"
            id="column-input"
            v-on:change="$emit('columnChange')">
                <option v-for="(column, index) in numericalColumnIndices" v-bind:value="index">
                    {{ variables[column] }}
                </option>
            </select></h3>

        <h3 v-if="options.usingTwoSamples">Variables: <select v-model="options.twoSample.columns[0]"
            class="pick-test-variable"
            id="column-input"
            v-on:change="$emit('columnChange')">
                <option v-for="(column, index) in numericalColumnIndices" v-bind:value="index">
                    {{ variables[column] }}
                </option>
            </select>
            and
            <select v-model="options.twoSample.columns[1]"
                class="pick-test-variable"
                id="column-input"
                v-on:change="$emit('columnChange')">
                <option v-for="(column, index) in numericalColumnIndices" v-bind:value="index">
                    {{ variables[column] }}
                </option>
            </select></h3>
    </div>
</template>

<script>

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testMean;
            },
            set: function(newValue) {
                this.$store.dispatch('updateMeanTestOptions', newValue);
            }
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        }
    },
}

</script>



