<template>
    <div>
        <h3>Parameters</h3>

        <table align="center" class="parameters">
            <tr>
                <td>Number of trials</td>
                <td>
                    <input id="binomial-n" type="number" min="0"
                        v-model.number="options.binomial.n" />
                </td>
                <td></td>
            </tr>
            <tr>
                <td>Probability of success</td>
                <td>
                    <input id="binomial-p" type="number" step="0.0001" min="0" max="1"
                        v-model.number="options.binomial.p" />
                </td>
                <td></td>
            </tr>
            <tr>
                <td>Number of successes (x)</td>
                <td>
                    <input id="x-pdf" type="number"
                        v-model.number="options.pdf.x" />
                </td>
                <td>
                    <input type="checkbox" id="all-possible"
                        v-model="showAllX"
                        @click="clearX" />
                    <label for="all-possible">Show all</label>
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result)">
            <h3>Answer</h3>
            <div class="answer-holder" v-if="!invalidProbability && !invalidTrials && !showAllX">
                <div>
                    The probability that there will be exactly {{ options.pdf.x }} successes in {{ options.binomial.n }} trials, if each trial has a probability of success of {{ options.binomial.p }}, is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <div class="answer-holder" v-if="!invalidProbability && !invalidTrials && showAllX">
                <table align="center" class="results-table">
                    <tr>
                        <th>Successes</th>
                        <th>Probability</th>
                    </tr>
                    <tr v-for="(probability, index) in allResults">
                        <td>{{ index }}</td>
                        <td>{{ probability.toPrecision(5) }}</td>
                    </tr>
                </table>
            </div>

            <div v-if="invalidProbability" class="error-message">
                The probability of success must be between 0 and 1.
            </div>
            <div v-if="invalidTrials" class="error-message">
                The number of trials and number of successes must be integers.
            </div>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

export default {
    data: function() {
        return {
            showAllX: false
        }
    },
    methods: {
        clearX: function() {
            this.options.pdf.x = 0;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        invalidProbability: function() {
            if (this.options.binomial.p < 0 || this.options.binomial.p > 1) {
                return true;
            }
            return false;
        },
        invalidTrials: function() {
            if (!(Number.isInteger(this.options.binomial.n)) || !(Number.isInteger(this.options.pdf.x))) {
                return true;
            }
            return false;
        },
        result: function() {
            return distributions.binomialPDF(this.options.pdf.x, this.options.binomial.n, this.options.binomial.p);
        },
        allResults: function() {
            var probabilities = [];
            for (var i=0; i <= this.options.binomial.n; i++) {
                probabilities.push(distributions.binomialPDF(i, this.options.binomial.n, this.options.binomial.p));
            }
            return probabilities;
        }
    }
}

</script>



