<template>
    <div class="panel-tab">
    <div class="panel-block">
        <span class="panel-block-title">Descriptive Statistics</span>

        <!--<div>
            <span v-for="columnIndex in numericalColumnIndices">
                <input type='checkbox'
                        :value='columnIndex'
                        v-model='visibleVariableIndices'> {{ variables[columnIndex] }}
            </span>
        </div>-->

        <div class="data-btn-holder btn-group">
            <button class="btn" @click="openVariableForm">
                Show Variables
            </button><button class="btn" @click="openStatsForm">
                Show Statistics
            </button>
        </div>

        <StatsTableSelectVariables v-if="variableFormVisible"
            @close='closeForm'>
        </StatsTableSelectVariables>
        <StatsTableSelectStatistics v-if="statsFormVisible"
            @close='closeForm'>
        </StatsTableSelectStatistics>
        <div class="grayed-out" v-if="variableFormVisible || statsFormVisible" @click="closeForm"></div>
        <!--<StatsTableVariableSelector>
        </StatsTableVariableSelector>-->

        <DescriptiveStatisticsTable
            :statistics="statisticsToShow">
        </DescriptiveStatisticsTable>
    </div>
</div>
</template>

<script>
import DescriptiveStatisticsTable from './DescriptiveStatisticsTable.vue';
import StatsTableSelectVariables from './StatsTableSelectVariables.vue';
import StatsTableSelectStatistics from './StatsTableSelectStatistics';
//import StatsTableVariableSelector from './StatsTableVariableSelector.vue';

export default {
    /*props: {
        stats: {
            type: Object,
            required: true
        },
        variable: {
            type: String,
            required: true
        }
    },*/
    data: function() {
        return {
            activePopupName: null,
            statsFormVisible: false,
            variableFormVisible: false
        };
    },
    methods: {
        popupExplanation(row) {
            this.activePopupName = row.popupID;
        },
        closePopup() {
            this.activePopupName = null;
        },
        openStatsForm() {
            this.statsFormVisible = true;
        },
        openVariableForm() {
            this.variableFormVisible = true;
        },
        closeForm() {
            this.statsFormVisible = false;
            this.variableFormVisible = false;
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        }
    },
    computed: {
        descriptiveStatistics: function() {
            return this.$store.getters.descriptiveStatistics;
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        /*hiddenVariableIndices: {
            get: function() {
                return this.$store.getters.options.statisticsPage.hiddenVariableIndices;
            },
            set: function(newArray) {
                this.$store.dispatch('updateStatisticsPageOptions', {
                    hiddenVariableIndices: newArray
                });
            }
        },
        visibleVariableIndices: {
            get: function() {
                var hidden = this.hiddenVariableIndices;
                var visible = [];
                for (var i=0; i < this.numericalColumnIndices.length; i++) {
                    var index = this.numericalColumnIndices[i];
                    if (hidden.indexOf(index) < 0) {
                        visible.push(index);
                    }
                }
                return visible;
            },
            set: function(visible) {
                var hidden = [];
                for (var i=0; i < this.numericalColumnIndices.length; i++) {
                    var index = this.numericalColumnIndices[i];
                    if (visible.indexOf(index) < 0) {
                        hidden.push(index);
                    }
                }
                this.hiddenVariableIndices = hidden;
            }
        },*/
        visibleVariableIndices: function() {
            var hidden = this.$store.getters.options.statisticsPage.hiddenVariableIndices;
            var visible = [];
            for (var i=0; i < this.numericalColumnIndices.length; i++) {
                var index = this.numericalColumnIndices[i];
                if (hidden.indexOf(index) < 0) {
                    visible.push(index);
                }
            }
            return visible;
        },
        statisticsToShow: function() {
            var statisticsToShow = {};
            for (var i=0; i < this.visibleVariableIndices.length; i++) {
                var variableName = this.variables[this.visibleVariableIndices[i]];
                statisticsToShow[variableName] = this.descriptiveStatistics[variableName];
            }

            return statisticsToShow;
        },
        currentStats: function() {
            /*var stats = {};
            stats[this.$props.variable] = this.$props.stats[this.$props.variable];
            return stats;*/
            return this.descriptiveStatistics;
        }
    },
    components: {
        DescriptiveStatisticsTable,
        StatsTableSelectVariables,
        StatsTableSelectStatistics
        //StatsTableVariableSelector
    }
}

</script>



