import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as mutations from './mutations'
import * as getters from './getters'
import dataLoader from '@/helpers/dataLoader.js';

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        dataAsRows: [],
        dataAsColumns: [],
        //columnTypes: [],
        variables: [],
        numericalColumnIndices: [],
        numericalColumnNames: [],
        categoricalColumnIndices: [],
        categoricalColumnNames: [],
        descriptiveStatistics: {},
        correlationMatrix: [],
        covarianceMatrix: [],
        //dataByCategory: {},
        categoryStatsByLevel: {},
        categoryStatsByVariable: {},
        correlationMatrixByLevel: {},
        covarianceMatrixByLevel: {},
        options: {
            decimalPlaces: 3,
            fixedCategoricalColumns: [],
            statisticsPage: {
                activeTab: 0,
                availableStatistics: [],
                hiddenStatistics: [
                    'meanSE',
                    'sumOfSquares',
                    'range',
                    'variance',
                    'coefficientOfVariationSample',
                    'coefficientOfVariationPopulation',
                    'skewness',
                    'kurtosis',
                    'interquartileRange'
                ],
                hiddenVariableIndices: []
            },
            categoricalStatistics: {
                organizeByLevel: false,
                separatedBy: 0,
                currentlyViewingVariable: 0,
                currentlyViewingLevel: 0
            },
            scatterplot: {
                activeTab: 0,
                columns: [0,1],
                includeRegressionLine: false,
                includeTitles: true,
                lineReg: {
                    slope: null,
                    intercept: null
                },
                width: 600,
                height: 400,
                includePadding: true,
                showXTicks: true,
                showYTicks: true,
                includeMinorXTicks: true,
                includeMinorYTicks: true,
                dotSize: 4,
                includeTooltips: true,
                splitBasedOnCategory: false,
                categoricalColumnForSplitting: [null]
            },
            histogram: {
                column: 0,
                numBins: 12,
                width: 600,
                height: 400,
                includePadding: true,
                includeTitles: true,
                showXTicks: true,
                showYTicks: true,
                includeMinorXTicks: true,
                includeMinorYTicks: true,
                includeTooltips: true,
                splitBasedOnCategory: false,
                categoricalIndexForSplitting: 0
            },
            stemAndLeaf: {
                column: 0,
                powerOfTen: -1
            },
            boxplot: {
                columnsToShow: [0],
                width: 600,
                height: 400,
                includePadding: true
            },
            barchart: {
                column: 0,
                orderByProportion: false,
                includeCumulativeLine: false,
                includePadding: true
            },
            piechart: {
                column: 0,
                width: 400,
                height: 300,
                radius: 200,
                isDonut: false
            },
            test: {
                samples: -1,
                type: -1,
                comparing: -1,
                readyToBegin: false,
                hypothesis: {
                    compareTo: null,
                    comparison: null
                }
            },
            testMean: {
                usingStats: false,
                usingZ: false,
                usingTwoSamples: false,
                alpha: 0.05,
                hypothesis: {
                    comparison: 'neq',
                    value: null
                },
                oneSample: {
                    column: 0,
                    alpha: 0.05,
                    xBar: null,
                    n: null,
                    popStdDev: null,
                    sampleStdDev: null,
                },
                twoSample: {
                    columns: [0,1],
                    xBar: [null, null],
                    n: [null, null],
                    popStdDev: [null, null],
                    sampleStdDev: [null, null]
                }
            },
            testProportion: {
                usingTwoSamples: false,
                alpha: 0.05,
                hypothesis: {
                    comparison: 'neq',
                    value: null
                },
                oneSample: {
                    column: 0,
                    x: null,
                    n: null
                },
                twoSample: {
                    columns: [0,1],
                    x: [null, null],
                    n: [null, null]
                }
            },
            testGOF: {
                usingExpectedFrequencies: false,
                experimentName: 'Experiment Name',
                numberOfGroups: 2,
                totalFrequency: 0,
                groupNames: ['Group 1', 'Group 2'],
                observedFrequencies: [null, null],
                expectedFrequencies: [null, null],
                expectedPercentages: [null, null]
            },
            distributions: {
                activeTab: 'pdf',
                pdf: {
                    distribution: '',
                    x: null,
                    //result: null
                },
                cdf: {
                    distribution: '',
                    x: null,
                    calculateBetween: false,
                    start: null,
                    end: null,
                    //result: null
                },
                inverse: {
                    distribution: '',
                    x: null,
                    //result: null
                },
                normal: {
                    mean: null,
                    sd: null
                },
                t: {
                    df: null
                },
                chisquare: {
                    df: null
                },
                f: {
                    df1: null,
                    df2: null
                },
                binomial: {
                    n: null,
                    p: null
                },
                poisson: {
                    lambda: null
                },
                geometric: {
                    p: null
                }
            }
        },
        /*testResults: {
            testStatistic: {
                type: "z",
                value: 0
            },
            p: 0,
            se: 0,
            ci: [0,0],
            comparisonValue: 0
        }*/
    },
    mutations,
    actions,
    getters
})



