import util from '../graphUtilities.js';
import drawing from '../drawing.js';
import statistics from '../statistics.js';
//import data from '../data.js';

export default {
    boxplot: function(dataAsColumns, settings) {
        // if multiple columns, create side-by-side boxplots
        settings = util.setDefaultSettings(settings);
        settings.showAxisTitles = false;
        settings.showXTicks = true;
        settings.showYTicks = false;
        settings.includeMinorXTicks = false;
        settings.includeMinorYTicks = false;
        settings.dotSize = 2;
        settings.fontSize = '0.8em';

        var numberOfVariables = dataAsColumns.length;
        var fiveNumberSummaries = [];
        var currentFNS = this.findFiveNumberSummary(dataAsColumns[0], settings.separateOutliers);
        fiveNumberSummaries.push(currentFNS);

        var overallMin = currentFNS.min;
        var overallMax = currentFNS.max;

        for (var i=1; i < numberOfVariables; i++) {
            currentFNS = this.findFiveNumberSummary(dataAsColumns[i], settings.separateOutliers);
            if (overallMin > currentFNS.min) {
                overallMin = currentFNS.min;
            }
            if (overallMax < currentFNS.max) {
                overallMax = currentFNS.max;
            }
            fiveNumberSummaries.push(currentFNS);
        }

        var axisResolution = util.findAxisResolution([[overallMin, overallMax], [1.75, numberOfVariables + 1.25]], false);
        var graph = util.startGraph(axisResolution, settings);

        for (var j=0; j < numberOfVariables; j++) {
            graph = this.addBoxplot(graph, fiveNumberSummaries[j], j, axisResolution, settings);
        }
        for (var k=0; k < numberOfVariables; k++) {
            graph = this.addBoxplotTooltip(graph, fiveNumberSummaries[k], k, axisResolution, settings);
        }

        graph = util.endGraph(graph);
        return graph;
    },
    findFiveNumberSummary: function(dataList, separateOutliers) {
        var five = statistics.fiveNumberSummary(dataList.slice(1));

        var outliers = [];
        five.minFence = five.min;
        five.maxFence = five.max;

        if (separateOutliers) {
            var iqr = five.q3 - five.q1;
            var lowerFence = five.q1 - 1.5 * iqr;
            var upperFence = five.q3 + 1.5 * iqr;

            for (var i=1; i < dataList.length; i++) {
                if (dataList[i] < lowerFence || dataList[i] > upperFence) {
                    outliers.push(dataList[i]);
                }
            }

            // min and max not including outliers
            if (outliers.length > 0) {
                var newMin = five.max;
                var newMax = five.min;

                for (var j=1; j < dataList.length; j++) {
                    if (dataList[j] < newMin && outliers.indexOf(dataList[j]) < 0) {
                        newMin = dataList[j];
                    }
                    if (dataList[j] > newMax && outliers.indexOf(dataList[j]) < 0) {
                        newMax = dataList[j];
                    }
                }

                five.minFence = newMin;
                five.maxFence = newMax;
            }
        }

        return {
            variableName: dataList[0],
            minFence: five.minFence,
            min: five.min,
            q1: five.q1,
            med: five.med,
            q3: five.q3,
            max: five.max,
            maxFence: five.maxFence,
            outliers: outliers
        }
    },
    addBoxplot: function(graph, fiveNumberSummary, variableIndex, axisResolution, settings) {

        var min = util.positionForCoordinates([fiveNumberSummary.minFence, variableIndex + 2], axisResolution, settings);
        var q1 = util.positionForCoordinates([fiveNumberSummary.q1, variableIndex + 2], axisResolution, settings);
        var med = util.positionForCoordinates([fiveNumberSummary.med, variableIndex + 2], axisResolution, settings);
        var q3 = util.positionForCoordinates([fiveNumberSummary.q3, variableIndex + 2], axisResolution, settings);
        var max = util.positionForCoordinates([fiveNumberSummary.maxFence, variableIndex + 2], axisResolution, settings);

        // variable label on y-axis
        graph = drawing.addSolidLine(graph, {
            x1: -5,
            x2: 5,
            y1: min[1],
            y2: min[1],
            color: settings.axisColor,
            strokeWidth: settings.strokeWidth
        });

        graph = drawing.addText(graph, {
            align: 'middle',
            fontSize: settings.fontSize,
            x: -25,
            y: min[1],
            dy: '0.35em',
            rotation: '-90',
            rotationX: -25,
            rotationY: min[1],
            color: settings.axisColor,
            text: fiveNumberSummary.variableName
        });

        // line at min and line from min to box
        graph = drawing.addSolidLine(graph, {
            x1: min[0],
            y1: min[1] + 10,
            x2: min[0],
            y2: min[1] - 10,
            color: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        graph = drawing.addSolidLine(graph, {
            x1: min[0],
            y1: min[1],
            x2: q1[0],
            y2: q1[1],
            color: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        // box
        graph = drawing.addRectangleUsingCorners(graph, {
            classes: 'boxplotBox',
            id: 'box-' + q1[1],
            x1: q1[0],
            y1: q1[1] - 20,
            x2: q3[0],
            y2: q3[1] + 20,
            fillColor: settings.axisColor,
            strokeColor: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        // line at median
        graph = drawing.addSolidLine(graph, {
            x1: med[0],
            y1: med[1] + 20,
            x2: med[0],
            y2: med[1] - 20,
            color: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        // line at max and line from max to box
        graph = drawing.addSolidLine(graph, {
            x1: max[0],
            y1: max[1] + 10,
            x2: max[0],
            y2: max[1] - 10,
            color: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        graph = drawing.addSolidLine(graph, {
            x1: q3[0],
            y1: q3[1],
            x2: max[0],
            y2: max[1],
            color: settings.dataColor,
            strokeWidth: settings.strokeWidth
        });

        // outliers
        for (var i=0; i < fiveNumberSummary.outliers.length; i++) {
            var outlier = util.positionForCoordinates([fiveNumberSummary.outliers[i], variableIndex + 2], axisResolution, settings);

            graph = drawing.addCircle(graph, {
                cx: outlier[0],
                cy: outlier[1],
                r: settings.dotSize,
                fillColor: settings.dataColor
            });
        }

        return graph;
    },
    addBoxplotTooltip: function(graph, fiveNumberSummary, variableIndex, axisResolution, settings) {
        var med = util.positionForCoordinates([fiveNumberSummary.med, variableIndex + 2], axisResolution, settings);

        graph = drawing.addToolTip(graph, {
            type: 'box',
            x: med[0],
            y: med[1],
            width: 200,
            height: 350,
            backgroundColor: settings.backgroundColor,
            text: 'Minimum: ' + fiveNumberSummary.min +
                '<br />First quartile: ' + fiveNumberSummary.q1 +
                '<br />Median: ' + fiveNumberSummary.med +
                '<br />Third quartile: ' + fiveNumberSummary.q3 +
                '<br />Maximum: ' + fiveNumberSummary.max
        });

        return graph;
    }
}



