<template>
    <div class="panel-tab" id="scatterplot-container">
        <h1>Scatterplot</h1>
        Graph
        <select v-model="columns[0]" v-on:change="updateRegressionEquation">
            <option v-for="(column, index) in numericalData" v-bind:value="index">
                {{ column[0] }}
            </option>
        </select>
        vs.
        <select v-model="columns[1]" v-on:change="updateRegressionEquation">
            <option v-for="(column, index) in numericalData" v-bind:value="index">
                {{ column[0] }}
            </option>
        </select>
        <form class="right-aligned-form">
            <input type="checkbox" id="check-titles"
                v-model="includeTitles"
                @click="updateRegressionEquation" />
            <label for="check-titles">Show Axis Titles</label><br />

            <input type="checkbox" id="check-regline"
                v-model="includeRegressionLine"
                @click="updateRegressionEquation" />
            <label for="check-regline">Show Regression Line</label><br />

            <input type="checkbox" id="check-splitbycategory"
                v-model="splitBasedOnCategory"
                @click="updateRegressionEquation" />
            <label for="check-splitbycategory">Separate Based on Category<span v-if="splitBasedOnCategory">: <select v-model="categoricalColumnForSplitting" v-on:change="updateRegressionEquation">
                <option v-for="(column, index) in categoricalData" v-bind:value="column">
                    {{ column[0] }}
                </option>
            </select>
            </span>
            </label>
        </form>
        <Scatterplot :dataColumns="dataForScatterplot"
            :options="options">
        </Scatterplot>
        <div v-show="options.includeRegressionLine">
            Line of best fit: <span v-html="regressionEqDisplay"></span><br />
            Correlation coefficient: <span v-html="correlationDisplay"></span>
        </div>
    </div>
</template>

<script>
import data from '@/helpers/data.js';
import dotplots from '@/helpers/graphs/dotplots.js';
import $ from 'jquery';
import Scatterplot from './Scatterplot.vue';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.scatterplot;
            },
            set: function(newValue) {
                this.$store.dispatch('updateScatterplotOptions', newValue);
            }
        },
        columns: {
            get: function() {
                return this.options.columns;
            },
            set: function(newValue) {
                this.options = {
                    columns: newValue
                };
            }
        },
        includeRegressionLine: {
            get: function() {
                return this.options.includeRegressionLine;
            },
            set: function(newValue) {
                this.options = {
                    includeRegressionLine: newValue
                };
            }
        },
        includeTitles: {
            get: function() {
                return this.options.includeTitles;
            },
            set: function(newValue) {
                this.options = {
                    includeTitles: newValue
                };
            }
        },
        splitBasedOnCategory: {
            get: function() {
                return this.options.splitBasedOnCategory;
            },
            set: function(newValue) {
                this.options = {
                    splitBasedOnCategory: newValue
                };
            }
        },
        categoricalColumnForSplitting: {
            get: function() {
                return this.options.categoricalColumnForSplitting;
            },
            set: function(newValue) {
                this.options = {
                    categoricalColumnForSplitting: newValue
                };
            }
        },
        lineReg: {
            get: function() {
                return this.options.lineReg;
            },
            set: function(newValue) {
                this.options = {
                    lineReg: newValue
                };
            }
        },
        numericalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var numericalColumnIndices = this.$store.getters.numericalColumnIndices;
            var numericalColumns = [];

            for (var i=0; i < numericalColumnIndices.length; i++) {
                numericalColumns.push(allDataColumns[numericalColumnIndices[i]]);
            }

            return numericalColumns;
        },
        categoricalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var categoricalColumnIndices = this.$store.getters.categoricalColumnIndices;
            var categoricalColumns = [];

            for (var i=0; i < categoricalColumnIndices.length; i++) {
                categoricalColumns.push(allDataColumns[categoricalColumnIndices[i]]);
            }

            return categoricalColumns;
        },
        dataForScatterplot: function() {
            return [
                this.numericalData[this.columns[0]],
                this.numericalData[this.columns[1]]
            ]
        },
        correlation: function() {
            var correlationMatrix = this.$store.getters.correlationMatrix;
            var x = this.columns[0];
            var y = this.columns[1];

            if (correlationMatrix[x] !== undefined) {
                return correlationMatrix[x][y];
            }
        },
        regressionEqDisplay: function() {
            if (this.lineReg.intercept == 0) {
                return "\\(y = " + this.lineReg.slope + "x\\)";
            }
            if (this.lineReg.intercept > 0) {
                return "\\(y = " + this.lineReg.slope + "x + " + this.lineReg.intercept + "\\)";
            } else {
                return "\\(y = " + this.lineReg.slope + "x - " + (-this.lineReg.intercept) + "\\)";
            }
        },
        correlationDisplay: function() {
            return "\\(r = " + this.correlation + "\\)";
        }
    },
    methods: {
        updateRegressionEquation: function() {
            var xColumn = this.columns[0];
            var yColumn = this.columns[1];

            var x = this.numericalData[xColumn];
            var y = this.numericalData[yColumn];

            if (x !== undefined && y !== undefined) {
                x = x.slice(1);
                y = y.slice(1);

                var equation = dotplots.regressionEquation([x,y]);
                this.lineReg = {
                    slope: data.roundNumber(equation.slope, 3),
                    intercept: data.roundNumber(equation.intercept, 3)
                };
            }

            this.addPointTooltips();
        },
        addPointTooltips: function() {
            $(function() {
                $(".scatterPoint").hover(
                    function() {
                        var id = $(this).attr('id').split('-');
                        var x = id[1];
                        var y = id[2];
                        var tooltip = $("#tooltip-" + x + "-" + y);
                        tooltip.removeClass("hiddenTooltip");
                    },
                    function() {
                        var id = $(this).attr('id').split('-');
                        var x = id[1];
                        var y = id[2];
                        var tooltip = $("#tooltip-" + x + "-" + y);
                        tooltip.addClass("hiddenTooltip");
                    }
                );
            });
            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        },
    },
    mounted: function() {
        this.updateRegressionEquation();
        this.addPointTooltips();

        var parentWidth = document.getElementById('scatterplot-container').offsetWidth;
        var graphWidth = parentWidth * 0.8;
        var graphHeight = Math.floor(graphWidth * 2 / 3);

        if (graphWidth < 500) {
            graphWidth = 500;
            graphHeight = 333;
        }

        var dotRadius = 3;
        if (graphWidth < 150) {
            dotRadius = 1;
        }

        this.options = {
            width: graphWidth,
            height: graphHeight,
            dotSize: dotRadius,
            categoricalColumnForSplitting: this.categoricalData[0]
        }

        //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    },
    components: {
        Scatterplot
    }
}
</script>



