<template>
    <div class="popup-explanation popup-checklist">
        <h1>Select Variables to View</h1>
        <div class="data-btn-holder btn-group">
            <button class="btn" @click="showAllVariables">
                Show All
            </button>
        </div>
        <div class="variable-checklist-holder">
            <div v-for="(columnIndex, index) in numericalColumnIndices"
                class="variable-selector">
                <input type='checkbox' :id="'variable-' + index"
                        :value='columnIndex'
                        v-model='visibleVariableIndices'>
                <label :for="'variable-' + index">{{ variables[columnIndex] }}</label>
            </div>
            <button class="close-btn" @click="closeForm">Done</button>
        </div>
    </div>
</template>

<script>

export default {
    methods: {
        closeForm() {
            this.$emit('close');
        },
        showAllVariables() {
            this.hiddenVariableIndices = [];
        }
    },
    computed: {
        options: function() {
            return this.$store.getters.options.statisticsPage;
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        hiddenVariableIndices: {
            get: function() {
                return this.options.hiddenVariableIndices;
            },
            set: function(newArray) {
                this.$store.dispatch('updateStatisticsPageOptions', {
                    hiddenVariableIndices: newArray
                });
            }
        },
        visibleVariableIndices: {
            get: function() {
                var hidden = this.hiddenVariableIndices;
                var visible = [];
                for (var i=0; i < this.numericalColumnIndices.length; i++) {
                    var index = this.numericalColumnIndices[i];
                    if (hidden.indexOf(index) < 0) {
                        visible.push(index);
                    }
                }
                return visible;
            },
            set: function(visible) {
                var hidden = [];
                for (var i=0; i < this.numericalColumnIndices.length; i++) {
                    var index = this.numericalColumnIndices[i];
                    if (visible.indexOf(index) < 0) {
                        hidden.push(index);
                    }
                }
                this.hiddenVariableIndices = hidden;
            }
        }
    }
}

</script>



