import jStat from 'jstat';
import data from '@/helpers/data.js';

export default {
    // all basic statistics functions take a single data array, called dataArray
    observations: function(dataArray) {
        var observations = 0;
        for (var i=0; i < dataArray.length; i++) {
            if (dataArray[i] !== null) {
                observations += 1;
            }
        }
        return observations;
    },
    sum: function(dataArray, decimalPlaces) {
        var sum = 0;
        for (var i=0; i < dataArray.length; i++) {
            if (dataArray[i] !== null) {
                sum += dataArray[i];
            }
        }
        return sum;
    },
    mean: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }
        return data.roundNumber(jStat.mean(dataArray), decimalPlaces);
    },
    meanSE: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var standardDev = this.stdDev(dataArray, decimalPlaces).sample;
        var n = this.observations(dataArray, decimalPlaces);

        var se = standardDev / Math.sqrt(n);

        return data.roundNumber(se, decimalPlaces);
    },
    median: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }
        return data.roundNumber(jStat.median(dataArray), decimalPlaces);
    },
    mode: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }
        var modes = jStat.mode(dataArray);

        // if only one mode
        if (modes.length === undefined) {
            return modes.toString();
        }

        // if more than one mode, make a comma-separated string
        var modeDisplay = modes[0].toString();
        for (var i=1; i<modes.length; i++) {
            modeDisplay += ', ' + data.roundNumber(modes[i], decimalPlaces);
        }
        return modeDisplay;
    },
    sumOfSquares: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var ss = jStat.sumsqrd(dataArray);

        return data.roundNumber(ss, decimalPlaces);
    },
    range: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var max = jStat.max(dataArray);
        var min = jStat.min(dataArray);

        return data.roundNumber(max - min, decimalPlaces);
    },
    variance: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return {
                sample: null,
                population: null
            }
        }
        var sampleVariance = jStat.variance(dataArray, true);
        var popVariance = jStat.variance(dataArray);

        return {
            sample: data.roundNumber(sampleVariance, decimalPlaces) / 1,
            population: data.roundNumber(popVariance, decimalPlaces) / 1
        }
    },
    stdDev: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return {
                sample: null,
                population: null
            }
        }
        var sampleStdDev = jStat.stdev(dataArray, true);
        var popStdDev = jStat.stdev(dataArray);

        return {
            sample: data.roundNumber(sampleStdDev, decimalPlaces) / 1,
            population: data.roundNumber(popStdDev, decimalPlaces) / 1
        }
    },
    coefficientOfVariation: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var mean = this.mean(dataArray, decimalPlaces);
        var standardDev = this.stdDev(dataArray, decimalPlaces);

        var sampleCOV = (standardDev.sample / mean);
        var populationCOV = (standardDev.population / mean);

        return {
            sample: data.roundNumber(sampleCOV, decimalPlaces),
            population: data.roundNumber(populationCOV, decimalPlaces)
        };
    },
    skewness: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var skewness = jStat.skewness(dataArray);

        return data.roundNumber(skewness, decimalPlaces);
    },
    kurtosis: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var kurtosis = jStat.kurtosis(dataArray);

        return data.roundNumber(kurtosis, decimalPlaces);
    },
    fiveNumberSummary: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return {
                min: null,
                q1: null,
                med: null,
                q3: null,
                max: null
            }
        }

        return {
            min:  data.roundNumber(jStat.min(dataArray), decimalPlaces),
            q1: data.roundNumber(jStat.quartiles(dataArray)[0], decimalPlaces),
            med: data.roundNumber(jStat.median(dataArray), decimalPlaces),
            q3: data.roundNumber(jStat.quartiles(dataArray)[2], decimalPlaces),
            max: data.roundNumber(jStat.max(dataArray), decimalPlaces)
        }
    },
    interquartileRange: function(dataArray, decimalPlaces) {
        if (dataArray.length === 0) {
            return null;
        }

        var fiveNumberSummary = this.fiveNumberSummary(dataArray, decimalPlaces);
        var q1 = fiveNumberSummary.q1;
        var q3 = fiveNumberSummary.q3;

        return data.roundNumber(q3 - q1, decimalPlaces);
    },
    sampleCorrelation: function(x, y, decimalPlaces) {
        // x and y are both single lists of data
        // can't find correlation for different sample sizes
        if (x.length !== y.length) {
            return null;
        }

        // can't find correlation for empty sample
        if (x.length === 0) {
            return null;
        }

        // means
        var xbar = this.mean(x);
        var ybar = this.mean(y);
        // sample standard deviations
        var sx = this.stdDev(x).sample;
        var sy = this.stdDev(y).sample;

        // correlation is undefined if standard deviation is zero for either sample
        if (sx === 0 || sy === 0) {
            return null;
        }

        // using the formula for r
        var sum = 0;
        for (var i=0; i < x.length; i++) {
            sum += ((x[i] - xbar) / sx) * ((y[i] - ybar) / sy)
        }

        return data.roundNumber(sum / (x.length - 1), decimalPlaces);
    },
    covariance: function(x, y, decimalPlaces) {
        // x and y are both single lists of data
        // can't find covariance for different sample sizes
        if (x.length !== y.length) {
            return null;
        }

        // can't find covariance for empty sample
        if (x.length === 0) {
            return null;
        }

        // means
        var xbar = this.mean(x);
        var ybar = this.mean(y);

        // sample size
        var n = x.length;

        // using the formula for covariance
        var sum = 0;
        for (var i=0; i < n; i++) {
            sum += ((x[i] - xbar) * (y[i] - ybar)) / (n - 1);
        }

        return data.roundNumber(sum, decimalPlaces);
    },
    availableStatistics: function() {
        return [
            {
                id: "n",
                displayName: "Number of Observations"
            },
            {
                id: "mean",
                displayName: "Mean"
            },
            {
                id: "meanSE",
                displayName: "Standard Error of Mean"
            },
            {
                id: "median",
                displayName: "Median"
            },
            {
                id: "mode",
                displayName: "Mode"
            },
            {
                id: "sumOfSquares",
                displayName: "Sum of Squares"
            },
            {
                id: "range",
                displayName: "Range"
            },
            {
                id: "variance",
                displayName: "Variance"
            },
            {
                id: "stdDev",
                displayName: "Standard Deviation"
            },
            {
                id: "coefficientOfVariationSample",
                displayName: "Sample Coefficient of Variation"
            },
            {
                id: "coefficientOfVariationPopulation",
                displayName: "Population Coefficient of Variation"
            },
            {
                id: "skewness",
                displayName: "Skewness"
            },
            {
                id: "kurtosis",
                displayName: "Kurtosis"
            },
            {
                id: "fiveNumberSummary",
                displayName: "Five Number Summary"
            },
            {
                id: "interquartileRange",
                displayName: "Interquartile Range"
            }
        ]
    },
    singleColumnStats: function(nonNullData, decimalPlaces) {
        // expects data array to not have any null values
        return {
            n: this.observations(nonNullData),
            mean: this.mean(nonNullData, decimalPlaces),
            meanSE: this.meanSE(nonNullData, decimalPlaces),
            median: this.median(nonNullData, decimalPlaces),
            mode: this.mode(nonNullData, decimalPlaces),
            sumOfSquares: this.sumOfSquares(nonNullData, decimalPlaces),
            range: this.range(nonNullData, decimalPlaces),
            variance: this.variance(nonNullData, decimalPlaces),
            stdDev: this.stdDev(nonNullData, decimalPlaces),
            coefficientOfVariation: this.coefficientOfVariation(nonNullData, decimalPlaces),
            skewness: this.skewness(nonNullData, decimalPlaces),
            kurtosis: this.kurtosis(nonNullData, decimalPlaces),
            fiveNumberSummary: this.fiveNumberSummary(nonNullData, decimalPlaces),
            interquartileRange: this.interquartileRange(nonNullData, decimalPlaces),
        }
    },
    descriptiveStatistics: function(dataAsColumns, numericalColumnIndices, decimalPlaces) {
        var statsByColumn = {};
        /*form -- statsByColumn: {
            "variableName": {
                n: numberOfObs,
                mean: average,
                etc.
            }
        }*/
        for (var i=0; i < numericalColumnIndices.length; i++) {
            var columnIndex = numericalColumnIndices[i];
            var variableName = dataAsColumns[columnIndex][0];
            var columnContents = dataAsColumns[columnIndex].slice(1);
            var nonNullData = data.removeNullValues(columnContents);

            statsByColumn[variableName] = this.singleColumnStats(nonNullData, decimalPlaces);
        }

        return statsByColumn;
    },
    correlationMatrix: function(dataAsColumns, numericalColumnIndices, decimalPlaces) {
        // returns a 2-D array of correlations (null for categorical variables)
        var correlationMatrix = [];

        for (var i=0; i < numericalColumnIndices.length; i++) {
            /*// ignore empty columns
            if (data.isNullArray(dataAsColumns[i])) {
                continue;
            }*/

            correlationMatrix.push([]);

            var xIndex = numericalColumnIndices[i];
            var xColumn = dataAsColumns[xIndex].slice(1);
            var nonNullDataX = data.removeNullValues(xColumn);

            for (var j=0; j < numericalColumnIndices.length; j++) {
                var yIndex = numericalColumnIndices[j];
                var yColumn = dataAsColumns[yIndex].slice(1);
                var nonNullDataY = data.removeNullValues(yColumn);

                correlationMatrix[i].push(this.sampleCorrelation(nonNullDataX, nonNullDataY, decimalPlaces));
            }
        }

        return correlationMatrix;
    },
    covarianceMatrix: function(dataAsColumns, numericalColumnIndices, decimalPlaces) {
        // returns a 2-D array of covariances (null for categorical variables)
        var covarianceMatrix = [];

        for (var i=0; i < numericalColumnIndices.length; i++) {
            covarianceMatrix.push([]);

            var xIndex = numericalColumnIndices[i];
            var xColumn = dataAsColumns[xIndex].slice(1);
            var nonNullDataX = data.removeNullValues(xColumn);

            for (var j=0; j < numericalColumnIndices.length; j++) {
                var yIndex = numericalColumnIndices[j];
                var yColumn = dataAsColumns[yIndex].slice(1);
                var nonNullDataY = data.removeNullValues(yColumn);

                covarianceMatrix[i].push(this.covariance(nonNullDataX, nonNullDataY, decimalPlaces));
            }
        }

        return covarianceMatrix;
    },
    categoryStatsBy: function(brokenDownBy, dataAsRows, columnTypes, decimalPlaces) {
        var categoricalColumnIndices = data.findCategoricalColumnIndices(columnTypes);
        var numericalColumnIndices = data.findNumericalColumnIndices(columnTypes);

        var categorizedData;
        if (brokenDownBy === 'level') {
            categorizedData = data.categorizeDataByLevel(dataAsRows, categoricalColumnIndices);
        }
        if (brokenDownBy === 'variable') {
            categorizedData = data.categorizeDataByVariable(dataAsRows, categoricalColumnIndices);
        }
        var result = {};

        for (var categoricalVariable in categorizedData) {
            var statsBrokenDown = {};
            for (var key in categorizedData[categoricalVariable]) {
                var dataBrokenDown = categorizedData[categoricalVariable][key];
                var dataAsColumnsBrokenDown = data.transpose(dataBrokenDown);

                if (brokenDownBy === 'variable') {
                    // for this one, all the columns that are sent to descriptiveStatistics are numerical
                    numericalColumnIndices = [];
                    for (var i=0; i < dataAsColumnsBrokenDown.length; i++) {
                        numericalColumnIndices.push(i);
                    }
                }

                statsBrokenDown[key] = this.descriptiveStatistics(dataAsColumnsBrokenDown, numericalColumnIndices, decimalPlaces);
            }
            result[categoricalVariable] = statsBrokenDown;
        }

        return result;
    },
    categoryStatsByLevel: function(dataAsRows, columnTypes, decimalPlaces) {
        // finds stats for a given level of a categorical variable
        // returns object: topmost key is the column number for each categorical column
        // value: object
        //      key: category level
        //      value: object
        //          key: variable name
        //          value: stats results object
        return this.categoryStatsBy('level', dataAsRows, columnTypes, decimalPlaces);
    },
    categoryStatsByVariable: function(dataAsRows, columnTypes, decimalPlaces) {
        // finds stats for a given variable, separated by the level of a categorical variable
        // returns object: topmost key is the column number for each categorical column
        // value: object
        //      key: variable name
        //      value: object
        //          key: category level
        //          value: stats results
        return this.categoryStatsBy('variable', dataAsRows, columnTypes, decimalPlaces);
    },
    /*correlationMatrixByLevel: function(dataAsRows, categoricalColumns, decimalPlaces) {
        // for each level of each categorical variable, returns an array of correlations for the numerical columns
        // object: key: column number for categorical variable
        //      value: object
        //          key: level
        //          value: correlation matrix (null values for categorical variables)
    },
    covarianceMatrixByLevel: function(dataAsRows, categoricalColumns, decimalPlaces) {
        // for each level of each categorical variable, returns an array of covariances for the numerical columns
        // object: key: column number for categorical variable
        //      value: object
        //          key: level
        //          value: covariance matrix (null values for categorical variables)
    }*/
}



