import util from '../graphUtilities.js';
import drawing from '../drawing.js';

export default {
    ConfidenceIntervalGraph: function(interval, comparisonValue, comparisonType, settings) {
        settings = util.setDefaultSettings(settings);
        settings.showYTicks = false;
        settings.includeMinorXTicks = false;
        settings.includeMinorYTicks = false;

        var ci = [interval.min, interval.max];
        var center = interval.center;

        if (comparisonValue !== undefined && comparisonValue !== null) {
            var minValue = Math.min(ci[0], ci[1], comparisonValue);
            var maxValue = Math.max(ci[0], ci[1], comparisonValue);
        } else {
            var minValue = Math.min(ci[0], ci[1]);
            var maxValue = Math.max(ci[0], ci[1]);
        }

        var axisResolution = util.findAxisResolution([[minValue, maxValue], [1, 2]], false);
        var graph = util.startGraph(axisResolution, settings);

        var lowerCoord = util.positionForCoordinates([ci[0], 1], axisResolution, settings);
        var upperCoord = util.positionForCoordinates([ci[1], 1], axisResolution, settings);

        //var center = (ci[0] + ci[1]) / 2;
        var centerCoord = util.positionForCoordinates([center, 1], axisResolution, settings);

        if (isNaN(lowerCoord[0]) || isNaN(lowerCoord[1])) {
            return null;
        }
        if (isNaN(upperCoord[0]) || isNaN(upperCoord[1])) {
            return null;
        }
        if (isNaN(centerCoord[0]) || isNaN(centerCoord[1])) {
            return null;
        }

        if (comparisonValue !== undefined && comparisonValue !== null) {
            var comparisonCoord = util.positionForCoordinates([comparisonValue, 1], axisResolution, settings);

            graph = drawing.addDashedLine(graph, {
                x1: comparisonCoord[0],
                y1: comparisonCoord[1],
                x2: comparisonCoord[0],
                y2: 0,
                color: settings.lineColor,
                strokeWidth: settings.strokeWidth,
                dashLength: 8
            });
        }

        var middle = settings.height / 2;

        // lower dash
        graph = drawing.addSolidLine(graph, {
            x1: lowerCoord[0],
            y1: middle - 20,
            x2: lowerCoord[0],
            y2: middle + 20,
            color: settings.lineColor,
            strokeWidth: settings.strokeWidth
        });

        // upper dash
        graph = drawing.addSolidLine(graph, {
            x1: upperCoord[0],
            y1: middle - 20,
            x2: upperCoord[0],
            y2: middle + 20,
            color: settings.lineColor,
            strokeWidth: settings.strokeWidth
        });

        // line from lower to upper
        graph = drawing.addSolidLine(graph, {
            x1: lowerCoord[0],
            y1: middle,
            x2: upperCoord[0],
            y2: middle,
            color: settings.lineColor,
            strokeWidth: settings.strokeWidth
        });

        graph = drawing.addCircle(graph, {
            cx: centerCoord[0],
            cy: middle,
            r: 6,
            fillColor: settings.dataColor
        });

        graph = util.endGraph(graph);
        return graph;
    }
}



