<template>
    <div id="barchart-parent" class="main-panel">
        <h1>Bar Chart</h1>
        Select variable: <select v-model="options.column">
            <option v-for="(catColumn, index) in categoricalData" v-bind:value="index">
                {{ catColumn[0] }}
            </option>
        </select>

        <form class="right-aligned-form">
            <input type="checkbox" id="check-orderbyproportion"
                v-model="options.orderByProportion" />
            <label for="check-orderbyproportion">
                Show in Decreasing Order
            </label><br />
            <span v-if="options.orderByProportion" >
                <input type="checkbox" id="check-includecumulativeline"
                    v-model="options.includeCumulativeLine" />
                <label for="check-includecumulativeline">
                    Include Cumulative Total Line
                </label>
            </span>
        </form>

        <Barchart :categoricalColumn="dataForPlot"
            :options="options">
        </Barchart>
    </div>
</template>

<script>
import Barchart from './Barchart.vue';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.barchart;
            },
            set: function(newValue) {
                this.$store.dispatch('updateBarchartOptions', newValue)
            }
        },
        categoricalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var categoricalColumnIndices = this.$store.getters.categoricalColumnIndices;
            var categoricalColumns = [];

            for (var i=0; i < categoricalColumnIndices.length; i++) {
                categoricalColumns.push(allDataColumns[categoricalColumnIndices[i]]);
            }

            return categoricalColumns;
        },
        dataForPlot: function() {
            return this.categoricalData[this.options.column];
        }
    },
    mounted: function() {
        var parentWidth = document.getElementById('barchart-parent').offsetWidth;
        var graphWidth = parentWidth * 0.8;

        if (graphWidth < 500) {
            graphWidth = 500;
        }

        this.options = {
            width: graphWidth,
        }
    },
    components: {
        Barchart
    }
}

</script>



