import util from '../graphUtilities.js';
import drawing from '../drawing.js';
import data from '../data.js';

export default {
    histogram: function(dataList, settings) {
        // expects data in a single list
        // if some settings are not specified, set to defaults
        settings = util.setDefaultSettings(settings);
        if (settings.showAxisTitles) {
            settings.xtitle = dataList[0];
            settings.ytitle = 'Frequency';
        }

        dataList = data.removeNullValues(dataList.slice(1));

        var dataWithFreq = this.getFrequencies(dataList);

        var min = Math.min.apply(Math, dataList);
        var max = Math.max.apply(Math, dataList);
        // if no grouping, the number of bins equals number of unique data values
        if (!settings.barGrouping) {
            settings.numBins = (max - min) + 1;
        }
        settings.numBins = this.setMinBinWidth(min, max, settings.numBins);

        var bins = this.initializeBins(min, max, settings.numBins);
        bins = this.sortDataIntoBins(dataWithFreq, bins);

        // find max frequency to set axes
        var maxFreq = this.maxFrequencyForAllBins(bins);

        var axisResolution = util.findAxisResolution([[bins[0].left, bins[bins.length-1].right], [0, maxFreq]], true);
        var graph = util.startGraph(axisResolution, settings);

        for (var i=0; i < bins.length; i++) {
            var startCoordinates = util.positionForCoordinates([bins[i].left, bins[i].freq], axisResolution, settings);
            var endCoordinates = util.positionForCoordinates([bins[i].right, 0], axisResolution, settings);

            graph = drawing.addRectangleUsingCorners(graph, {
                id: 'histogramBar-' + startCoordinates[0] + '-' + startCoordinates[1],
                xData: dataWithFreq[0][i],
                freqData: dataWithFreq[1][i],
                classes: 'histBar',
                x1: startCoordinates[0],
                y1: startCoordinates[1],
                x2: endCoordinates[0],
                y2: endCoordinates[1],
                fillColor: settings.axisColor,
                strokeColor: settings.dataColor,
                strokeWidth: settings.strokeWidth
            });
        }

        for (var i=0; i < bins.length; i++) {
            var coordinates = util.positionForCoordinates([bins[i].left, bins[i].freq], axisResolution, settings);

            graph = drawing.addToolTip(graph, {
                type: 'histogram',
                x: coordinates[0],
                y: coordinates[1],
                width: 130,
                height: 50,
                text: bins[i].left + " &le; x &lt; " + bins[i].right + "<br />" +
                "n = " + bins[i].freq,
                backgroundColor: settings.backgroundColor
            })
        }

        graph = util.endGraph(graph);
        return graph;
    },
    getFrequencies: function(data) {
        var dataWithFreq = [[], []];

        for (var i=0; i<data.length; i++) {
            // if the value has already been seen
            var valIndex = dataWithFreq[0].indexOf(data[i]);
            if (valIndex > -1) {
                dataWithFreq[1][valIndex] += 1;
            } else {
                // if not, add it to the values array and add a frequency of 1
                dataWithFreq[0].push(data[i]);
                dataWithFreq[1].push(1);
            }
        }

        return dataWithFreq;
    },
    setMinBinWidth: function(min, max, numBins) {
        var partialAxes = util.findAxisResolution([[min, max], [0, 1]], true);
        // upper limit on the number of bins: binWidth of 1
        if (numBins > ((max - min) / (partialAxes.xstep / 5))) {
            return ((max - min) / (partialAxes.xstep / 5));
        }
        return numBins;
    },
    initializeBins: function(min, max, numBins) {
        var binWidth = ((max - min) / numBins).toFixed(2);
        var bins = [];
        for (var i=0; i < numBins; i++) {
            bins.push({
                left: data.roundNumber(min + i*binWidth, 4) / 1,
                right: data.roundNumber(min + (i+1)*binWidth, 4) / 1,
                freq: 0
            })
        }

        return bins;
    },
    sortDataIntoBins: function(dataWithFreq, bins) {
        for (var i=0; i < dataWithFreq[0].length; i++) {
            var lastBin = bins.length - 1;
            for (var j=0; j < lastBin; j++) {
                if (dataWithFreq[0][i] >= bins[j].left && dataWithFreq[0][i] < bins[j].right) {
                    bins[j].freq += dataWithFreq[1][i];
                    break;
                }
            }
            if (dataWithFreq[0][i] >= bins[lastBin].left && dataWithFreq[0][i] <= bins[lastBin].right) {
                bins[lastBin].freq += dataWithFreq[1][i];
            }
        }

        return bins;
    },
    maxFrequencyForAllBins: function(bins) {
        var maxFreq = 0;
        for (var i=0; i<bins.length; i++) {
            if (bins[i].freq > maxFreq) {
                maxFreq = bins[i].freq;
            }
        }

        return maxFreq;
    },
    stemAndLeaf: function(dataList, powerOfTen) {
        var stems = [];
        var leaves = [];

        for (var i=1; i < dataList.length; i++) {
            var sl = this.findStemAndLeaf(dataList[i], powerOfTen);
            var stem = sl.stem;
            var leaf = sl.leaf;

            var index = stems.indexOf(stem);
            if (index < 0) {
                if (stem !== '') {
                    stems.push(stem);
                    leaves.push([leaf]);
                }
            } else {
                leaves[index].push(leaf);
            }
        }

        for (var j=0; j < leaves.length; j++) {
            leaves[j].sort();
        }

        return {
            variableName: dataList[0],
            stems: stems,
            leaves: leaves
        };
    },
    findStemAndLeaf: function(number, powerOfTen) {
        var numberString = number.toString();
        var beforeDecimal = numberString.split('.')[0];
        var afterDecimal = numberString.split('.')[1];

        /*if (beforeDecimal === undefined) {
            beforeDecimal = '';
        }
        if (afterDecimal === undefined) {
            afterDecimal = '';
        }*/

        if (powerOfTen === -1) {
            if (beforeDecimal === undefined) {
                beforeDecimal = '0';
            }
            if (afterDecimal === undefined) {
                afterDecimal = '0';
            }

            return {
                stem: beforeDecimal,
                leaf: afterDecimal
            }
        }

        if (powerOfTen > -1) {
            var beforeDecimalArray = beforeDecimal.split('');
            var cuttingPoint = beforeDecimalArray.length - powerOfTen - 1;

            if (cuttingPoint < 0) {
                var stem = '';
                var leaf = '';
            } else {
                var stem = beforeDecimalArray.slice(0, cuttingPoint).join('');
                var leaf = beforeDecimalArray[cuttingPoint];
            }

            if (stem === '' && leaf !== '') {
                stem = '0';
            }

            return {
                stem: stem,
                leaf: leaf
            }
        }

        if (powerOfTen < -1) {
            if (afterDecimal === undefined) {
                afterDecimal = '0';
            }

            var afterDecimalArray = afterDecimal.split('');
            var cuttingPoint = -(powerOfTen + 1);

            var stem = afterDecimalArray.slice(0, cuttingPoint).join('');
            var leaf = afterDecimalArray.slice(cuttingPoint).join('');

            // pad stem with zeros
            while (stem.length < cuttingPoint) {
                stem += '0';
            }
            stem = '.' + stem;

            return {
                stem: stem,
                leaf: leaf
            }
        }
    }
}



