<template>
    <div class="panel-tab">
        <h3>Number of Samples</h3>
        <div>
            <button v-for="(sampleOption, index) in options.samples"
                  @click="sampleChoice(index)"
                  class="test-option"
                  :class="{ activeOption: samples == index, inactiveOption: samples !== index }">
                    {{ sampleOption }}
            </button>
        </div>

        <div v-if="(showTypeOptions.length > 0)">
        <h3>Type of Test</h3>
            <button v-for="(typeOption, index) in options.types"
                  @click="typeChoice(index)"
                  :disabled="(showTypeOptions.indexOf(index) === -1)"
                  class="test-option"
                  :class="{ activeOption: type == index, inactiveOption: type !== index }">
                    {{ typeOption.name }}
            </button>
        </div>

        <div v-if="(showComparingOptions.length > 0)">
        <h3>Comparing</h3>
            <button v-for="(comparingOption, index) in options.comparing"
                  @click="comparingChoice(index)"
                  :disabled="(showComparingOptions.indexOf(index) === -1)"
                  class="test-option"
                  :class="{ activeOption: comparing == index, inactiveOption: comparing !== index }">
                    {{ comparingOption.name }}
            </button>
        </div>

        <div class="start-test-holder">
            <button class="start-test"
            :disabled="startTestIsDisabled"
            @click="beginTest()">
                Begin Test
            </button>
        </div>

    </div>
</template>

<script>

export default {
    data: function() {
        return {
            options: {
                samples: [
                    "One Sample",
                    "Two Samples",
                    "More than Two Samples"
                ],
                types: [
                    {
                        name: "z-test",
                        showFor: [
                            "One Sample",
                            "Two Samples",
                            "More than Two Samples"
                        ],
                        readyToBegin: false
                    },
                    {
                        name: "t-test",
                        showFor: [
                            "One Sample",
                            //"Two Samples",
                            "More than Two Samples"
                        ],
                        readyToBegin: false
                    },
                    {
                        name: "Paired t-test",
                        showFor: [
                            "One Sample",
                            "Two Samples",
                            "More than Two Samples"
                        ],
                        readyToBegin: false
                    },
                    {
                        name: "Chi-square GOF",
                        showFor: [
                            "One Sample",
                            "Two Samples",
                            "More than Two Samples"
                        ],
                        readyToBegin: true
                    },
                    {
                        name: "ANOVA",
                        showFor: [
                            "One Sample",
                            "Two Samples",
                            "More than Two Samples"
                        ],
                        readyToBegin: true
                    }
                ],
                comparing: [
                    {
                        name: "Means",
                        showFor: [
                            "z-test",
                            "t-test"
                        ]
                    },
                    {
                        name: "Proportions",
                        showFor: [
                            "z-test",
                            "t-test"
                        ]
                    }
                ]
            },
            /*samples: -1,
            type: -1,
            comparing: -1*/
        }
    },
    computed: {
        samples: {
            get: function() {
                return this.$store.getters.options.test.samples;
            },
            set: function(newValue) {
                this.$store.dispatch('updateTestOptions', {
                    samples: newValue,
                    //type: this.type,
                    //comparing: this.comparing
                });
            }
        },
        type: {
            get: function() {
                return this.$store.getters.options.test.type;
            },
            set: function(newValue) {
                this.$store.dispatch('updateTestOptions', {
                    //samples: this.samples,
                    type: newValue,
                    //comparing: this.comparing
                });
            }
        },
        comparing: {
            get: function() {
                return this.$store.getters.options.test.comparing;
            },
            set: function(newValue) {
                this.$store.dispatch('updateTestOptions', {
                    //samples: this.samples,
                    //type: this.type,
                    comparing: newValue
                });
            }
        },
        readyToBegin: {
            get: function() {
                return this.$store.getters.options.test.readyToBegin;
            },
            set: function(newValue) {
                this.$store.dispatch('updateTestOptions', {
                    readyToBegin: newValue
                });
            }
        },
        startTestIsDisabled: function() {
            if (this.samples >= 0 && this.type >= 0 && this.comparing >= 0) {
                return false;
            }
            if (this.type >= 0 && this.options.types[this.type].readyToBegin) {
                return false;
            }
            return true;
        },
        showTypeOptions: function() {
            var available = [];
            if (this.samples < 0) {
                return available;
            }
            for (var i=0; i<this.options.types.length; i++) {
                if (this.options.types[i].showFor.indexOf(this.options.samples[this.samples]) > -1) {
                    available.push(i);
                }
            }
            return available;
        },
        showComparingOptions: function() {
            var available = [];
            if (this.type < 0) {
                return available;
            }
            for (var i=0; i<this.options.comparing.length; i++) {
                if (this.options.comparing[i].showFor.indexOf(this.options.types[this.type].name) > -1) {
                    available.push(i);
                }
            }
            return available;
        },
        testOptions: function() {
            return this.$store.getters.options.test;
        }
    },
    methods: {
        sampleChoice: function(choice) {
            this.samples = choice;
            this.type = -1;
            this.comparing = -1;
            this.readyToBegin = !this.startTestIsDisabled;
        },
        typeChoice: function(choice) {
            this.type = choice;
            this.comparing = -1;
            this.readyToBegin = !this.startTestIsDisabled;
        },
        comparingChoice: function(choice) {
            this.comparing = choice;
            this.readyToBegin = !this.startTestIsDisabled;
        },
        beginTest: function() {
            /*this.$store.dispatch('updateTestOptions', {
                samples: this.samples,
                type: this.type,
                comparing: this.comparing
            });*/

            this.$router.push({path: '/tests/results'});
        }
    }
}

</script>

<style lang="scss">

.test-option {

    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 2px;
    padding: 8px;
    margin: 5px;


    &:enabled {
        cursor: pointer;
    }
}

.inactiveOption {
    background-color: rgba(0,0,0,0.04);

    &:enabled {
        &:hover {
            background-color: rgba(0,0,0,0.08);
        }
    }
}

.activeOption:enabled {
    background-color: rgba(66,165,245,1);

    /*&:enabled {
        &:hover {
            background-color: rgba(21,101,192 ,1);
        }
    }*/
}

.start-test {

    &:enabled {
        background-color: rgba(66,165,245,1);
        color: white;
        cursor: pointer;

        &:hover {
            background-color: rgba(21,101,192 ,1);
        }
    }

    border: 1px solid rgba(21,101,192 ,1);
    border-radius: 2px;
    padding: 10px;
}

.start-test-holder {
    margin: 40px;
    float: right;
}

</style>



