export default {
    addSolidLine: function(graph, options) {
        // options: {x1, y1, x2, y2, color, strokeWidth}
        graph += "<line x1='" + options.x1 + "' y1='" + options.y1 + "' x2='" + options.x2 + "' y2='" + options.y2 + "' style='stroke:" + options.color + ";stroke-width:" + options.strokeWidth + "'/>\n";

        return graph;
    },
    addDashedLine: function(graph, options) {
        // options: {x1, y1, x2, y2, color, strokeWidth, dashLength}
        graph += "<line stroke-dasharray='" + options.dashLength + "," + options.dashLength + "' x1='" + options.x1 + "' y1='" + options.y1 + "' x2='" + options.x2 + "' y2='" + options.y2 + "' style='stroke:" + options.color + ";stroke-width:" + options.strokeWidth + "'/>\n";

        return graph;
    },
    addRectangle: function(graph, options) {
        // options: {x, y, width, height, fillColor, strokeColor, strokeWidth}
        var rectangle = "<rect x='" + options.x + "' y='" + options.y + "' ";
        rectangle += "width='" + options.width + "' height='" + options.height + "' ";
        rectangle += "style='fill:" + options.fillColor + ";stroke-width:" + options.strokeWidth + ";stroke:" + options.strokeColor + "' />\n";

        graph += rectangle;

        return graph;
    },
    addRectangleUsingCorners: function(graph, options) {
        // options: {x1, y1, x2, y2, fillColor, strokeColor, strokeWidth, id*, classes*, xData*, freqData*}
        // *optional
        var rectangle = "<polygon ";

        if (options.id !== undefined) {
            rectangle += "id='" + options.id + "' ";
        }
        if (options.classes !== undefined) {
            rectangle += "class='" + options.classes + "' ";
        }
        if (options.xData !== undefined) {
            rectangle += "x-data='" + options.xData + "' ";
        }
        if (options.freqData !== undefined) {
            rectangle += "freq-data='" + options.freqData + "' ";
        }

        rectangle += "points='" + options.x1 + "," + options.y1 + " " + options.x2 + "," + options.y1 + " " + options.x2 + "," + options.y2 + " " + options.x1 + "," + options.y2 + "' ";

        rectangle += "style='fill:" + options.fillColor + "; stroke:" + options.strokeColor + "; stroke-width:" + options.strokeWidth + ";' />\n";

        graph += rectangle;

        return graph;
    },
    addCircle: function(graph, options) {
        // options: {cx, cy, r, fillColor, classes*, id*, dataX*, dataY*, opacity*}
        // *optional
        var circle = "<circle cx='" + options.cx + "' cy='" + options.cy + "' r='" + options.r + "'";

        if (options.opacity !== undefined) {
            circle += "style='fill:" + options.fillColor + "; opacity:" + options.opacity + ";'";
        } else {
            circle += "style='fill:" + options.fillColor + ";'";
        }
        if (options.classes !== undefined) {
            circle += "class='" + options.classes + "'";
        }
        if (options.id !== undefined) {
            circle += "id='" + options.id + "'";
        }
        if (options.dataX !== undefined) {
            circle += "data-x='" + options.dataX + "'";
        }
        if (options.dataY !== undefined) {
            circle += "data-y='" + options.dataY + "'";
        }

        circle += " />\n";
        graph += circle;

        return graph;
    },
    addText: function(graph, options) {
        // options: {align, fontSize, fontFamily*, x, y, dx*, dy*, color, text, rotation*, rotationX*, rotationY*}
        // *optional
        var text = "<text font-size='" + options.fontSize + "' text-anchor='" + options.align + "' ";
        text += "x='" + options.x + "' y='" + options.y + "' ";

        if (options.fontFamily !== undefined) {
            text += "font-family='" + options.fontFamily + "' ";
        }
        if (options.dx !== undefined) {
            text += "dx='" + options.dx + "' ";
        }
        if (options.dy !== undefined) {
            text += "dy='" + options.dy + "' ";
        }
        if (options.rotation !== undefined) {
            if (options.rotationX === undefined || options.rotationY === undefined) {
                text += "transform='rotate(" + options.rotation + ")' ";
            } else {
                text += "transform='rotate(" + options.rotation + " " + options.rotationX + " " + options.rotationY + ")' ";
            }
        }

        text += "fill='" + options.color + "'";
        text += ">" + options.text + "</text>\n";

        graph += text;

        return graph;
    },
    addToolTip: function(graph, options) {
        // options: {x, y, *level, width, height, text, backgroundColor, type: scatterplot, histogram, box, circle}
        // *level only specified for circle
        var classes = 'pointTooltip hiddenTooltip';

        var tooltip = "<foreignobject ";

        if (options.type === 'scatterplot') {
            tooltip += "id='tooltip-" + options.x + "-" + options.y + "' ";
        }
        if (options.type === 'histogram') {
            tooltip += "id='histTooltip-" + options.x + "-" + options.y + "' ";
            options.y -= 20;
        }
        if (options.type === 'box') {
            tooltip += "id='boxTooltip-" + options.y + "' ";
            options.x += 150;
            options.y += 100;
        }
        if (options.type === 'circle') {
            tooltip += "id='circleTooltip-" + options.level + "' ";
            options.y += 35;
            classes = 'pointTooltip';
        }

        if (options.backgroundColor === undefined) {
            options.backgroundColor = '#fff';
        }

        tooltip += "class='" + classes + "' ";
        tooltip += "x='" + options.x + "' y='" + (options.y - 35) + "' ";
        tooltip += "width='" + options.width + "' height='" + options.height + "' transform='translate(-" + 0.5*options.width + " -" + 0.5*options.height + ")'>";
        tooltip += "<div class='pointTooltipDiv' style='background-color:" + options.backgroundColor + "'>" + options.text + "</div>" +
        "</foreignobject>\n";

        graph += tooltip;

        return graph;
    },
    addCircleWedge: function(graph, options) {
        // options: startPercentage, endPercentage, radius, fillColor, emptyCenter, centerColor* (optional)
        var largeArcFlag = (options.endPercentage - options.startPercentage > 0.5) ? 1 : 0;

        var start = this.getCircleCoordinatesForPercent(options.startPercentage, options.radius);
        var end = this.getCircleCoordinatesForPercent(options.endPercentage, options.radius);

        var wedge = "<path d='M " + start[0] + " " + start[1];
        wedge += " A " + options.radius + " " + options.radius + " 0 " + largeArcFlag + " 1 " + end[0] + " " + end[1];
        wedge += " L 0 0' ";

        if (options.id !== undefined) {
            wedge += "id='" + options.id + "' ";
        }
        if (options.classes !== undefined) {
            wedge += "class='" + options.classes + "' ";
        }

        wedge += "stroke='#fff' stroke-width='2' fill='" + options.fillColor + "'";
        wedge += "></path>\n";

        /*if (options.emptyCenter) {
            var radiusCenter = 0.7 * options.radius;
            var startCenter = this.getCircleCoordinatesForPercent(options.startPercentage, radiusCenter);
            var endCenter = this.getCircleCoordinatesForPercent(options.endPercentage, radiusCenter);

            wedge += "<path d='M " + startCenter[0] + " " + startCenter[1];
            wedge += " A " + radiusCenter + " " + radiusCenter + " 0 " + largeArcFlag + " 1 " + endCenter[0] + " " + endCenter[1];
            wedge += " L 0 0' ";

            if (options.centerColor !== undefined) {
                wedge += "stroke='" + options.centerColor + "'";
                wedge += "fill='" + options.centerColor + "'";
            }

            wedge += "></path>\n";
        }*/

        graph += wedge;

        return graph;
    },
    getCircleCoordinatesForPercent: function(percentage, radius) {
        var x = radius * Math.cos(2 * Math.PI * percentage).toFixed(3);
        var y = radius * Math.sin(2 * Math.PI * percentage).toFixed(3);

        return [x,y];
    },
    colors: function(index) {
        var colors = [
            '#3A445B',
            '#9D6E88',
            '#A2B47F',
            '#236A62',
            '#5F6A82',
            '#542B72',
            '#AA6939',
            '#C2AF88',
            '#AAA739',
            '#2D4471',
            '#862D63',
            '#7E9F35',
            '#AA8339',
            '#877550',
            '#6C7E4B',
            '#6E4159'
        ];

        if (index < colors.length) {
            return colors[index];
        }
        return colors[index % colors.length];
    }
}



