<template>
    <div class="main-panel hyp-test">
        <h1>Hypothesis Testing for Means</h1>

        <h3>Test Options</h3>

        <div class="organization-toggle-holder">
        <div class="organization-toggle">
            <table align="center">
                <tr>
                    <td>
                        <span class="organization-choice">One Sample</span>
                    </td>
                    <td>
                        <label class="switch" @click="updateFormatting">
                            <input type="checkbox" v-model="options.usingTwoSamples" />
                            <span class="slider"></span>
                        </label>
                    </td>
                    <td>
                        <span class="organization-choice">Two Samples</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span class="organization-choice">T Test</span>
                    </td>
                    <td>
                        <label class="switch" @click="updateFormatting">
                            <input type="checkbox" v-model="options.usingZ" />
                            <span class="slider"></span>
                        </label>
                    </td>
                    <td>
                        <span class="organization-choice">Z Test</span>
                    </td>
                </tr>
                <tr>
                    <td>
                        <span class="organization-choice">Use Stored Data</span>
                    </td>
                    <td>
                        <label class="switch" @click="calculateTestStats">
                            <input type="checkbox" v-model="options.usingStats" />
                            <span class="slider"></span>
                        </label>
                    </td>
                    <td>
                        <span class="organization-choice">Input Statistics</span>
                    </td>
                </tr>

            </table>
        </div>
        </div>

        <ColumnSelector v-if="!options.usingStats"
            v-on:columnChange="calculateTestStats">
        </ColumnSelector>
        <ColumnStatsForm v-if="options.usingStats"
            v-on:refreshMath="updateFormatting"></ColumnStatsForm>

        <PopSDForm v-if="options.usingZ"
            v-on:refreshMath="updateFormatting"></PopSDForm>

        <HypothesisForm v-on:refreshMath="updateFormatting"></HypothesisForm>

        <ConfidenceLevelForm :testType="'mean'"></ConfidenceLevelForm>

        <Results v-if="showResults"
            :testResults="testResults"
            :options="options">
        </Results>

        <CIResults v-if="showCIResults"
            :testType="'mean'"
            :testResults="testResults"
            :options="options">
        </CIResults>
    </div>
</template>

<script>
import statistics from '@/helpers/statistics.js';
import tests from '@/helpers/tests.js';

import ColumnSelector from './mean-tests/ColumnSelector.vue';
import ColumnStatsForm from './mean-tests/ColumnStatsForm.vue';
import PopSDForm from './mean-tests/PopSDForm.vue';
import HypothesisForm from './mean-tests/HypothesisForm.vue';
import ConfidenceLevelForm from './shared/ConfidenceLevelForm.vue';
import Results from './mean-tests/Results.vue';
import CIResults from './shared/CIResults.vue';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testMean;
            },
            set: function(newValue) {
                this.$store.dispatch('updateMeanTestOptions', newValue);
            }
        },
        testResults: function() {
            return tests.testMean(this.options);
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        descriptiveStatistics: function() {
            return this.$store.getters.descriptiveStatistics;
        },
        showResults: function() {
            if (isNaN(this.testResults.p) || isNaN(this.testResults.confidenceInterval.min)) {
                return false;
            }

            if (this.testResults.p === null) {
                return false;
            }

            if (!this.options.usingTwoSamples) {
                if (this.options.hypothesis.value === null || this.options.hypothesis.value === '')
                return false;
            }

            return true;
        },
        showCIResults: function() {
            if (isNaN(this.testResults.confidenceInterval.max) || isNaN(this.testResults.confidenceInterval.min)) {
                return false;
            }

            return true;
        }
    },
    methods: {
        updateFormatting: function() {
            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        },
        calculateTestStats: function() {
            var oneSampleVariable = this.variables[this.numericalColumnIndices[this.options.oneSample.column]];
            var oneSampleStats = this.descriptiveStatistics[oneSampleVariable];

            if (oneSampleStats !== undefined) {
                this.options.oneSample.xBar = oneSampleStats.mean / 1;
                this.options.oneSample.n = oneSampleStats.n / 1;
                this.options.oneSample.sampleStdDev = oneSampleStats.stdDev.sample / 1;
            }

            var twoSampleVariableA = this.variables[this.numericalColumnIndices[this.options.twoSample.columns[0]]];
            var twoSampleVariableB = this.variables[this.numericalColumnIndices[this.options.twoSample.columns[1]]];
            var statsA = this.descriptiveStatistics[twoSampleVariableA];
            var statsB = this.descriptiveStatistics[twoSampleVariableB];

            if (statsA !== undefined && statsB !== undefined) {
                this.options.twoSample.xBar = [statsA.mean / 1, statsB.mean / 1];
                this.options.twoSample.n = [statsA.n / 1, statsB.n / 1];
                this.options.twoSample.sampleStdDev = [statsA.stdDev.sample / 1, statsB.stdDev.sample / 1];
            }

            this.updateFormatting();
        }
    },
    mounted() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
        this.$nextTick(function() {
            this.calculateTestStats();
        });
    },
    components: {
        ColumnSelector,
        ColumnStatsForm,
        PopSDForm,
        HypothesisForm,
        ConfidenceLevelForm,
        Results,
        CIResults
    }
}

</script>



