import jStat from 'jstat';

export default {
    anyMissingParameters: function(params) {
        for (var i=0; i < params.length; i++) {
            if (params[i] === null || params[i] === '') {
                return true;
            }
        }
        return false;
    },
    normalPDF: function(x, mean, sd) {
        if (this.anyMissingParameters([x, mean, sd])) {
            return null;
        }
        return jStat.normal.pdf(x, mean, sd);
    },
    normalCDF: function(x, mean, sd) {
        if (this.anyMissingParameters([x, mean, sd])) {
            return null;
        }
        return jStat.normal.cdf(x, mean, sd);
    },
    normalInv: function(x, mean, sd) {
        if (this.anyMissingParameters([x, mean, sd])) {
            return null;
        }
        return jStat.normal.inv(x, mean, sd);
    },
    tPDF: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.studentt.pdf(x, df);
    },
    tCDF: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.studentt.cdf(x, df);
    },
    tInv: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.studentt.inv(x, df);
    },
    chiSquarePDF: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.chisquare.pdf(x, df);
    },
    chiSquareCDF: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.chisquare.cdf(x, df);
    },
    chiSquareInv: function(x, df) {
        if (this.anyMissingParameters([x, df])) {
            return null;
        }
        return jStat.chisquare.inv(x, df);
    },
    fPDF: function(x, df1, df2) {
        if (this.anyMissingParameters([x, df1, df2])) {
            return null;
        }
        return jStat.centralF.pdf(x, df1, df2);
    },
    fCDF: function(x, df1, df2) {
        if (this.anyMissingParameters([x, df1, df2])) {
            return null;
        }
        return jStat.centralF.cdf(x, df1, df2);
    },
    fInv: function(x, df1, df2) {
        if (this.anyMissingParameters([x, df1, df2])) {
            return null;
        }
        return jStat.centralF.inv(x, df1, df2);
    },
    binomialPDF: function(x, n, p) {
        if (this.anyMissingParameters([x, n, p])) {
            return null;
        }
        return jStat.binomial.pdf(x, n, p);
    },
    binomialCDF: function(x, n, p) {
        if (this.anyMissingParameters([x, n, p])) {
            return null;
        }
        return jStat.binomial.cdf(x, n, p);
    },
    poissonPDF: function(x, lambda) {
        if (this.anyMissingParameters([x, lambda])) {
            return null;
        }
        return jStat.poisson.pdf(x, lambda);
    },
    poissonCDF: function(x, lambda) {
        if (this.anyMissingParameters([x, lambda])) {
            return null;
        }
        return jStat.poisson.cdf(x, lambda);
    },
    geometricPDF: function(x, p) {
        if (this.anyMissingParameters([x, p])) {
            return null;
        }
        return p * (1 - p)**(x-1);
    },
    geometricCDF: function(x, p) {
        if (this.anyMissingParameters([x, p])) {
            return null;
        }
        return 1 - (1 - p)**x;
    }
}



