import util from '../graphUtilities.js';
import drawing from '../drawing.js';

export default {
    addShading: function(graph, shading, settings) {
        /* shading: {
            include: boolean,
            proportion: number or array (if 'between'),
            side: 'left', 'right', 'inside', 'outside', 'between'
        }*/

        if (shading.include) {
            graph += '<g clip-path="url(#dist-clip)">\n';

            if (shading.side === 'line') {
                var position = shading.proportion * settings.width;
                graph = drawing.addSolidLine(graph, {
                    x1: position,
                    y1: 0,
                    x2: position,
                    y2: settings.height,
                    color: settings.axisColor,
                    strokeWidth: 4
                });
            } else if (shading.side === 'left') {
                graph = drawing.addRectangle(graph, {
                    x: 0,
                    y: 0,
                    width: shading.proportion * settings.width,
                    height: settings.height,
                    fillColor: settings.axisColor,
                    strokeColor: settings.axisColor,
                    strokeWidth: 1
                });
            } else if (shading.side === 'right') {
                graph = drawing.addRectangle(graph, {
                    x: shading.proportion * settings.width,
                    y: 0,
                    width: (1 - shading.proportion) * settings.width,
                    height: settings.height,
                    fillColor: settings.axisColor,
                    strokeColor: settings.axisColor,
                    strokeWidth: 1
                });
            } else if (shading.side === 'between') {
                var left = shading.proportion[0];
                var right = shading.proportion[1];

                graph = drawing.addRectangle(graph, {
                    x: left * settings.width,
                    y: 0,
                    width: (right - left) * settings.width,
                    height: settings.height,
                    fillColor: settings.axisColor,
                    strokeColor: settings.axisColor,
                    strokeWidth: 1
                });
            } else {
                if (shading.proportion < 0.5) {
                    var left = shading.proportion;
                    var right = 1 - shading.proportion;
                } else {
                    var left = 1 - shading.proportion;
                    var right = shading.proportion;
                }

                if (shading.side === 'inside') {
                    graph = drawing.addRectangle(graph, {
                        x: left * settings.width,
                        y: 0,
                        width: (right - left) * settings.width,
                        height: settings.height,
                        fillColor: settings.axisColor,
                        strokeColor: settings.axisColor,
                        strokeWidth: 1
                    });
                }
                if (shading.side === 'outside') {
                    graph = drawing.addRectangle(graph, {
                        x: 0,
                        y: 0,
                        width: left * settings.width,
                        height: settings.height,
                        fillColor: settings.axisColor,
                        strokeColor: settings.axisColor,
                        strokeWidth: 1
                    });
                    graph = drawing.addRectangle(graph, {
                        x: right * settings.width,
                        y: 0,
                        width: left * settings.width,
                        height: settings.height,
                        fillColor: settings.axisColor,
                        strokeColor: settings.axisColor,
                        strokeWidth: 1
                    });
                }
            }
            graph += '</g>\n';
        }

        return graph;
    },
    defaultDistSettings: function(settings) {
        settings = util.setDefaultSettings(settings);
        settings.showXTicks = false;
        settings.showYTicks = false;
        settings.includeMinorXTicks = false;
        settings.includeMinorYTicks = false;
        settings.width = 624;
        settings.height = 370;

        return settings;
    },
    normal: function(shading, settings) {
        /* shading: {
            include: boolean,
            proportion: number,
            side: 'left', 'right', 'inside', 'outside', 'line'
        }*/
        settings = this.defaultDistSettings(settings);

        var axisResolution = util.findAxisResolution([[-4, 4], [0, 2]], true);
        var graph = util.startGraph(axisResolution, settings);

        graph += '<defs><clipPath id="dist-clip">\n';

        graph += '<path\
          fill="none"\
          stroke="' + settings.lineColor + '"\
          d="M624,352V344.5H600c-27.98,0-51.61-6.96-71.97-18.72c-29.641-17.101-52.36-44.37-71.48-75.12 c-28-45.01-48.31-97.48-71.39-136.52C365.13,80.26,343.02,56.5,312,56.5c-31.1,0-53.24,23.88-73.31,57.89       c-23.04,39.05-43.34,91.45-71.31,136.39c-19.28,30.979-42.21,58.41-72.2,75.45C75,337.72,51.62,344.5,24,344.5H0V352"\
          id="path11" />';

        graph += '</clipPath></defs>';

        graph += '<path\
          fill="none"\
          stroke="' + settings.lineColor + '"\
          d="M624,344.5H600c-27.98,0-51.61-6.96-71.97-18.72c-29.641-17.101-52.36-44.37-71.48-75.12 c-28-45.01-48.31-97.48-71.39-136.52C365.13,80.26,343.02,56.5,312,56.5c-31.1,0-53.24,23.88-73.31,57.89       c-23.04,39.05-43.34,91.45-71.31,136.39c-19.28,30.979-42.21,58.41-72.2,75.45C75,337.72,51.62,344.5,24,344.5H0"\
          id="path11" />';

        graph = this.addShading(graph, shading, settings);

        graph = util.endGraph(graph);
        return graph;
    },
    chiSquare: function(shading, settings) {
        /* shading: {
            include: boolean,
            proportion: number,
            side: 'left', 'right', 'inside', 'outside', 'line'
        }*/
        settings = this.defaultDistSettings(settings);

        var axisResolution = util.findAxisResolution([[.5, 4], [0, 2]], true);
        var graph = util.startGraph(axisResolution, settings);

        graph += '<defs><clipPath id="dist-clip">\n';

        graph += '<path\
          fill="none"\
          stroke="' + settings.lineColor + '"\
          d="M 32.444102,351.96498 C 100.58366,352.77555 78.450384,11.188137 147.1061,10.071641 216.39284,8.944885 196.39459,353.36185 609.10771,351.96498"\
          id="path2415" />';

        graph += '</clipPath></defs>';

        graph += '<path\
          fill="none"\
          stroke="' + settings.lineColor + '"\
          d="M 32.444102,351.96498 C 100.58366,352.77555 78.450384,11.188137 147.1061,10.071641 216.39284,8.944885 196.39459,353.36185 609.10771,351.96498"\
          id="path2416" />';

        graph = this.addShading(graph, shading, settings);

        graph = util.endGraph(graph);
        return graph;
    }
}



