import util from '../graphUtilities.js';
import drawing from '../drawing.js';
import data from '../data.js';
import statistics from '../statistics.js';

export default {
    dotPlot: function(points, isScatterplot, settings) {
        if (settings.showAxisTitles) {
            settings.xtitle = points[0][0];
            settings.ytitle = points[1][0];
        }

        if (settings.splitBasedOnCategory) {
            var categoryLevels = data.findCategoryLevels(settings.categoricalColumnForSplitting);
        }

        var x = points[0].slice(1);
        var y = points[1].slice(1);

        x = data.removeNullValues(x);
        y = data.removeNullValues(y);

        var xmax = Math.max.apply(Math, x);
        var xmin = Math.min.apply(Math, x);
        var ymax = Math.max.apply(Math, y);
        var ymin = Math.min.apply(Math, y);

        var axisResolution = util.findAxisResolution([[xmin, xmax], [ymin, ymax]], false);
        var graph = util.startGraph(axisResolution, settings);

        var dataLength = x.length;
        if (y.length > dataLength) {
            dataLength = y.length;
        }

        var nonNullData = [[], []];
        for (var i=0; i < dataLength; i++) {
            if (this.isValidPoint(x[i], y[i])) {
                nonNullData[0].push(x[i]);
                nonNullData[1].push(y[i]);
            }
        }

        if (isScatterplot) {
            graph = this.drawRegressionLine(graph, nonNullData, axisResolution, settings);
        }

        for (var i=0; i < dataLength; i++) {
            if (this.isValidPoint(x[i], y[i])) {
                var position = util.positionForCoordinates([x[i], y[i]], axisResolution, settings);
                var color = settings.dataColor;

                if (settings.splitBasedOnCategory) {
                    var level = settings.categoricalColumnForSplitting[i];

                    color = drawing.colors(categoryLevels.indexOf(level));
                    if (color === undefined) {
                        color = settings.dataColor;
                    }
                }

                // data points
                graph = drawing.addCircle(graph, {
                    classes: 'scatterPoint',
                    dataX: x[i],
                    dataY: y[i],
                    cx: position[0],
                    cy: position[1],
                    r: settings.dotSize,
                    fillColor: color
                });

                // larger circle appears when hovering
                graph = drawing.addCircle(graph, {
                    id: "hoverpt-" + position[0] + "-" + position[1],
                    classes: 'scatterPoint',
                    dataX: x[i],
                    dataY: y[i],
                    cx: position[0],
                    cy: position[1],
                    r: 10,
                    fillColor: settings.dataColor,
                    opacity: 0
                });
            }
        }

        if (settings.includeTooltips) {
            for (var i=0; i < dataLength; i++) {
                if (this.isValidPoint(x[i], y[i])) {
                    var position = util.positionForCoordinates([x[i], y[i]], axisResolution, settings);

                    // tooltips
                    graph = drawing.addToolTip(graph, {
                        x: position[0],
                        y: position[1],
                        width: 100,
                        height: 30,
                        text: "(" + x[i] + ", " + y[i] + ")",
                        type: 'scatterplot'
                    });
                }
            }
        }

        graph = util.endGraph(graph);
        return graph;
    },
    scatterplot: function(data, settings) {
        settings = util.setDefaultSettings(settings);

        /*var axisResolution = util.findAxisResolution(data, false);
        var graph = util.startGraph(axisResolution, settings);

        var x = data[0];
        var y = data[1];

        var dataLength = x.length;
        if (y.length > dataLength) {
            dataLength = y.length;
        }

        var nonNullData = [[], []];
        for (var i=0; i < dataLength; i++) {
            if (this.isValidPoint(x[i], y[i])) {
                nonNullData[0].push(x[i]);
                nonNullData[1].push(y[i]);
            }
        }

        for (var i=0; i < dataLength; i++) {
            if (this.isValidPoint(x[i], y[i])) {
                var position = util.positionForCoordinates([x[i], y[i]], axisResolution, settings);

                // tooltips
                graph = drawing.addToolTip(graph, {
                    x: position[0],
                    y: position[1],
                    width: 100,
                    height: 30,
                    text: "(" + x[i] + ", " + y[i] + ")",
                    type: 'scatterplot'
                });

                // data points
                graph = drawing.addCircle(graph, {
                    classes: 'scatterPoint',
                    dataX: x[i],
                    dataY: y[i],
                    cx: position[0],
                    cy: position[1],
                    r: 4,
                    fillColor: settings.dataColor
                });

                // larger circle appears when hovering
                graph = drawing.addCircle(graph, {
                    id: "hoverpt-" + position[0] + "-" + position[1],
                    classes: 'scatterPoint',
                    dataX: x[i],
                    dataY: y[i],
                    cx: position[0],
                    cy: position[1],
                    r: 10,
                    fillColor: settings.dataColor,
                    opacity: 0
                });
            }
        }

        graph = this.drawRegressionLine(graph, nonNullData, axisResolution, settings);

        graph = util.endGraph(graph);*/

        var graph = this.dotPlot(data, true, settings);
        return graph;
    },
    isValidPoint: function(x,y) {
        if (x === null || y === null) {
            return false;
        }
        if (isNaN(x) || isNaN(y)) {
            return false;
        }
        return true;
    },
    drawRegressionLine: function(graph, nonNullData, axisResolution, settings) {
        if (settings.includeRegressionLine) {
            var points = this.twoPointsOnRegressionLine(nonNullData, axisResolution, settings);

            if (settings.dashedLine) {
                graph = drawing.addDashedLine(graph, {
                    dashLength: 5,
                    x1: points[0][0],
                    y1: points[0][1],
                    x2: points[1][0],
                    y2: points[1][1],
                    color: settings.lineColor,
                    strokeWidth: settings.strokeWidth
                });
            } else {
                graph = drawing.addSolidLine(graph, {
                    x1: points[0][0],
                    y1: points[0][1],
                    x2: points[1][0],
                    y2: points[1][1],
                    color: settings.lineColor,
                    strokeWidth: settings.strokeWidth
                });
            }
        }

        return graph;
    },
    twoPointsOnRegressionLine: function(data, axes, settings) {
        var equation = this.regressionEquation(data);
        var slope = equation.slope;
        var intercept = equation.intercept;

        var x1 = axes.xmin;
        var x2 = axes.xmax;

        var y1 = slope*x1 + intercept;
        // make sure the line doesn't spill out of the picture
        if (y1 < axes.ymin) {
            y1 = axes.ymin;
            x1 = (y1 - intercept) / slope;
        }
        if (y1 > axes.ymax) {
            y1 = axes.ymax;
            x1 = (y1 - intercept) / slope;
        }

        var y2 = slope*x2 + intercept;
        if (y2 < axes.ymin) {
            y2 = axes.ymin;
            x2 = (y2 - intercept) / slope;
        }
        if (y2 > axes.ymax) {
            y2 = axes.ymax;
            x2 = (y2 - intercept) / slope;
        }

        return [util.positionForCoordinates([x1,y1], axes, settings), util.positionForCoordinates([x2,y2], axes, settings)];
    },
    regressionEquation: function(data) {
        var xbar = statistics.mean(data[0]);
        var ybar = statistics.mean(data[1]);

        var sx = statistics.stdDev(data[0]).sample;
        var sy = statistics.stdDev(data[1]).sample;

        var r = statistics.sampleCorrelation(data[0],data[1]);

        var slope = r*(sy / sx);
        var intercept = ybar - slope*xbar;

        return {
            slope: slope,
            intercept: intercept
        }
    }
}



