<template>
    <div>
        <span v-if="!isDonut" v-html="pieChart"></span>
        <span v-if="isDonut" v-html="donutChart"></span>
    </div>
</template>

<script>
import circleplots from '@/helpers/graphs/circleplots.js';

export default {
    props: {
        categoricalColumn: {
            type: Array,
            required: true
        },
        width: {
            type: Number,
            required: true
        },
        height: {
            type: Number,
            required: true
        },
        radius: {
            type: Number,
            required: true
        },
        isDonut: {
            type: Boolean,
            required: true
        }
    },
    computed: {
        pieChart: function() {
            return circleplots.pieChart(this.$props.categoricalColumn, {
                height: this.$props.height,
                width: this.$props.width,
                radius: this.$props.radius
            })
        },
        donutChart: function() {
            return circleplots.donutChart(this.$props.categoricalColumn, {
                height: this.$props.height,
                width: this.$props.width,
                radius: this.$props.radius
            })
        }
    }
}

</script>



