import data from './data.js';
import statistics from './statistics.js';
import drawing from './drawing.js';

export default {
    startGraph: function(axisResolution, settings) {
        var axesDisplay = this.drawAxes(axisResolution, settings);

        var width = settings.width;
        var height = settings.height;
        var viewBox = '0 0 ' + width + ' ' + height;

        if (settings.includePadding) {
            width += 50;
            height += 50;
            viewBox = '-50 -50 ' + (width + 100) + ' ' + (height + 100);
        }

        var graph = "<svg width='" + width + "' height='" + height + "' viewBox='" + viewBox + "'>";
        graph += "<rect x='0' y='0' width='" + (settings.width) + "' height='" + (settings.height) +
            "' style='fill:" + settings.backgroundColor + ";stroke-width:" + settings.strokeWidth + ";stroke:" + settings.axisColor + "' />\n";
        graph += axesDisplay[0];
        graph += axesDisplay[1];

        return graph;
    },
    endGraph: function(graph) {
        graph += "</svg>";

        return graph;
    },
    startBarChart: function(settings) {
        var width = settings.width;
        var height = settings.height;
        var viewBox = '0 0 ' + width + ' ' + height;

        if (settings.includePadding) {
            width += 50;
            height += 50;
            viewBox = '-50 -50 ' + (width + 100) + ' ' + (height + 100);
        }

        var graph = "<svg width='" + width + "' height='" + height + "' viewBox='" + viewBox + "'>";
        graph += "<rect x='0' y='0' width='" + (settings.width) + "' height='" + (settings.height) +
            "' style='fill:" + settings.backgroundColor + ";stroke-width:" + settings.strokeWidth + ";stroke:" + settings.axisColor + "' />\n";

        return graph;
    },
    startPieChart: function(settings) {
        var minX = -settings.width;
        var minY = -settings.height;
        var width = 2 * settings.width;
        var height = 2 * settings.height;

        return "<svg viewBox='" + minX + " " + minY + " " + width + " " + height + "'>"; // style='transform: rotate(-90deg)'>";
    },
    endPieChart: function(graph) {
        graph += "</svg>";
        return graph;
    },
    drawAxes: function(axisResolution, settings) {
        /* Returns an array of two strings; each string is the SVG code for one of the axes */
        //var axisResolution = this.findAxisResolution(dataXY, settings.startYAtZero);

        var axesDisplay = ["", ""];

        if (settings.showXTicks) {
            var xTicks = this.getTickMarkArrays(axisResolution.xmin, axisResolution.xmax, axisResolution.xstep, settings.width);
        }
        if (settings.showYTicks) {
            var yTicks = this.getTickMarkArrays(axisResolution.ymin, axisResolution.ymax, axisResolution.ystep, settings.height);

            // the y ticks were created from top to bottom
            yTicks.labels.reverse();
        }

        if (settings.showXTicks) {
            axesDisplay = this.drawXTickMarks(axesDisplay, xTicks, settings);
        }
        if (settings.showYTicks) {
            axesDisplay = this.drawYTickMarks(axesDisplay, yTicks, settings);
        }

        axesDisplay = this.addLinesForZero(axesDisplay, axisResolution, settings);

        axesDisplay = this.addAxisTitles(axesDisplay, axisResolution, settings);

        return axesDisplay;
    },
    getTickMarkArrays: function(min, max, step, widthOrHeight) {
        // widthOrHeight: width for x axis, height for y axis
        var tickCoordinates = [];
        var tickLabels = [];

        var stepsFromMinToMax = Math.round(Math.abs(max - min) / step);

        // the first tick
        tickCoordinates.push(Number((Math.floor(widthOrHeight / 20)).toFixed(5)))
        tickLabels.push(min);

        // the other ticks
        var distanceBetweenTicks = Math.floor(0.9*widthOrHeight / stepsFromMinToMax);
        for (var i=0; i<stepsFromMinToMax; i++) {
            tickCoordinates.push(tickCoordinates[i] + distanceBetweenTicks);
            tickLabels.push(Number((tickLabels[i] + step).toFixed(5)));
        }

        return {
            distance: distanceBetweenTicks,
            coordinates: tickCoordinates,
            labels: tickLabels
        }
    },
    drawXTickMarks: function(axesDisplay, ticks, settings) {
        for (var i=0; i < ticks.coordinates.length; i++) {
            axesDisplay[0] = drawing.addSolidLine(axesDisplay[0], {
                x1: ticks.coordinates[i],
                x2: ticks.coordinates[i],
                y1: settings.height + 5,
                y2: settings.height - 5,
                color: settings.axisColor,
                strokeWidth: settings.strokeWidth
            });
            axesDisplay[0] = drawing.addText(axesDisplay[0], {
                align: 'middle',
                fontSize: settings.fontSize,
                x: ticks.coordinates[i],
                y: settings.height + 25,
                color: settings.axisColor,
                text: ticks.labels[i]
            });

            // Add intermediate tick marks, but not after the last mark
            if (i < ticks.coordinates.length && settings.includeMinorXTicks) {
                var minorTick = ticks.coordinates[i] + ticks.distance / 5;

                for (var j=0; j < 4; j++) {
                    axesDisplay[0] = drawing.addSolidLine(axesDisplay[0], {
                        x1: minorTick,
                        x2: minorTick,
                        y1: settings.height + 3,
                        y2: settings.height - 3,
                        color: settings.axisColor,
                        strokeWidth: settings.secondaryStrokeWidth
                    });

                    minorTick += ticks.distance / 5;
                }
            }
        }

        return axesDisplay;
    },
    drawYTickMarks: function(axesDisplay, ticks, settings) {
        for (var i=0; i < ticks.coordinates.length; i++) {
            axesDisplay[1] = drawing.addSolidLine(axesDisplay[1], {
                x1: -5,
                x2: 5,
                y1: ticks.coordinates[i],
                y2: ticks.coordinates[i],
                color: settings.axisColor,
                strokeWidth: settings.strokeWidth
            });
            axesDisplay[1] = drawing.addText(axesDisplay[1], {
                align: 'middle',
                fontSize: settings.fontSize,
                x: -25,
                y: ticks.coordinates[i],
                dy: '0.35em',
                color: settings.axisColor,
                text: ticks.labels[i]
            });

            // Add intermediate tick marks, but not after the last mark
            if (i < ticks.coordinates.length && settings.includeMinorYTicks) {
                var minorTick = ticks.coordinates[i] + ticks.distance / 5;

                for (var j=0; j < 4; j++) {
                    axesDisplay[1] = drawing.addSolidLine(axesDisplay[1], {
                        y1: minorTick,
                        y2: minorTick,
                        x1: 3,
                        x2: -3,
                        color: settings.axisColor,
                        strokeWidth: settings.secondaryStrokeWidth
                    });

                    minorTick += ticks.distance / 5;
                }
            }
        }

        return axesDisplay;
    },
    addLinesForZero: function(axesDisplay, axisResolution, settings) {
        // If 0 is in the range, add a line for it
        var zeroPosition = this.positionForCoordinates([0,0], axisResolution, settings);
        if (axisResolution.xmin <= 0 && axisResolution.xmax >= 0) {
            axesDisplay[0] = drawing.addSolidLine(axesDisplay[0], {
                x1: zeroPosition[0],
                x2: zeroPosition[0],
                y1: 0,
                y2: settings.height,
                color: settings.axisColor,
                strokeWidth: settings.secondaryStrokeWidth
            });
        }
        if (axisResolution.ymin <= 0 && axisResolution.ymax >= 0) {
            axesDisplay[1] = drawing.addSolidLine(axesDisplay[1], {
                y1: zeroPosition[1],
                y2: zeroPosition[1],
                x1: 0,
                x2: settings.width,
                color: settings.axisColor,
                strokeWidth: settings.secondaryStrokeWidth
            });
        }

        return axesDisplay;
    },
    addAxisTitles: function(axesDisplay, axisResolution, settings) {
        var xcenter = (axisResolution.xmax + axisResolution.xmin) / 2;
        var ycenter = (axisResolution.ymax + axisResolution.ymin) / 2;
        var center = this.positionForCoordinates([xcenter, ycenter], axisResolution, settings);

        if (settings.xtitle !== null) {
            axesDisplay[0] = drawing.addText(axesDisplay[0], {
                // options: {align, fontSize, x1, y1, dx, dy, color, text}
                align: 'middle',
                fontSize: settings.fontSize,
                fontFamily: settings.titleFont,
                x: center[0],
                y: settings.height + 50,
                color: settings.axisColor,
                text: settings.xtitle
            });
        }
        if (settings.ytitle !== null) {
            axesDisplay[1] = drawing.addText(axesDisplay[1], {
                // options: {align, fontSize, x1, y1, dx, dy, color, text}
                align: 'middle',
                fontSize: settings.fontSize,
                fontFamily: settings.titleFont,
                x: -center[1],
                y: -50,
                color: settings.axisColor,
                text: settings.ytitle,
                rotation: -90
            });
        }

        return axesDisplay;
    },
    positionForCoordinates: function(point, axisResolution, settings) {
        /* Input: point in form [xdata, ydata]
        Output: point in form [xcoord, ycoord] in SVG form */
        var xPosition = 0;
        var yPosition = 0;

        var numXsteps = Math.round(Math.abs(axisResolution.xmax - axisResolution.xmin) / axisResolution.xstep);
        var xdistance = Math.floor(0.9*settings.width / numXsteps);

        var numYsteps = Math.round(Math.abs(axisResolution.ymax - axisResolution.ymin) / axisResolution.ystep);
        var ydistance = Math.floor(0.9*settings.height / numYsteps);

        xPosition = Math.floor(Math.floor(settings.width / 20) + (point[0] - axisResolution.xmin)*(xdistance / axisResolution.xstep));
        yPosition = Math.floor((settings.height - Math.floor(settings.height / 20)) - (point[1] - axisResolution.ymin)*(ydistance / axisResolution.ystep));

        return [xPosition, yPosition];
    },
    findAxisResolution: function(dataXY, startYAtZero) {
        var extremeDataValues = this.findExtremeDataValues(dataXY);
        var stepSizes = this.findStepSizes(extremeDataValues, startYAtZero);
        var xAxis = this.findMaxMinStep(extremeDataValues.xmax, extremeDataValues.xmin, stepSizes.x);
        var yAxis = this.findMaxMinStep(extremeDataValues.ymax, extremeDataValues.ymin, stepSizes.y);

        // force y axis to start at 0
        if (startYAtZero) {
            yAxis.min = 0;
        }

        var axisResolution = {
            xmin: xAxis.min,
            xmax: xAxis.max,
            xstep: xAxis.step,
            ymin: yAxis.min,
            ymax: yAxis.max,
            ystep: yAxis.step
        };

        return this.roundAxisValues(axisResolution);
    },
    findExtremeDataValues: function(dataXY) {
        var x = dataXY[0];
        var y = dataXY[1];

        var xmaxdata = Math.max.apply(Math, x);
        var xmindata = Math.min.apply(Math, x);
        var ymaxdata = Math.max.apply(Math, y);
        var ymindata = Math.min.apply(Math, y);

        return {
            xmax: xmaxdata,
            xmin: xmindata,
            ymax: ymaxdata,
            ymin: ymindata
        }
    },
    findStepSizes: function(extremeDataValues, startYAtZero) {
        /* FIND STEP SIZE: Divide the range into 8 blocks, then look for the closest value among
        1, 2, 5,   10, 20, 50,  ... [1,2,5] * 10^n.*/
        var xmaxdata = extremeDataValues.xmax;
        var xmindata = extremeDataValues.xmin;
        var ymaxdata = extremeDataValues.ymax;
        var ymindata = extremeDataValues.ymin;

        var numberOfBlocks = 8;

        var teststepx = (Math.abs(xmaxdata - xmindata)) / numberOfBlocks;
        var teststepy = (Math.abs(ymaxdata - ymindata)) / numberOfBlocks;
        // if y starts at 0, set ystep to about an eighth of the height
        if (startYAtZero) {
            teststepy = (Math.abs(ymaxdata)) / numberOfBlocks;
        }

        // Find order of magnitude of each step
        var nx = Math.floor(Math.log10(teststepx));
        var ny = Math.floor(Math.log10(teststepy));

        // Check possibilities in that order of magnitude (ex. if teststepx = 600, check 100, 200, 500 to see which is closest)
        var testarrayx = [10**nx, 2*10**nx, 5*10**nx];
        var xstep = testarrayx[0];

        var testarrayy = [10**ny, 2*10**ny, 5*10**ny];
        var ystep = testarrayy[0];

        var errorx = 10**(nx+1);
        var errory = 10**(ny+1);
        for (var i=0; i<3; i++) {
            if (Math.abs(testarrayx[i] - teststepx) < errorx) {
                errorx = Math.abs(testarrayx[i] - teststepx);
                xstep = testarrayx[i];
            }
            if (Math.abs(testarrayy[i] - teststepy) < errory) {
                errory = Math.abs(testarrayy[i] - teststepy);
                ystep = testarrayy[i];
            }
        }

        return {
            x: xstep,
            y: ystep
        }
    },
    findMaxMinStep: function(maxData, minData, stepSize) {
        var results = {}

        if (maxData === minData) {
            results.min = minData - 1;
            results.max = maxData + 1;
            results.step = 1;
        } else {
            results.min = Math.floor(minData / stepSize - 1) * stepSize;
            results.max = Math.ceil(maxData / stepSize + 1) * stepSize;
            results.step = stepSize;
        }

        return results;
    },
    roundAxisValues: function(axes) {
        axes.xmin = Number(axes.xmin.toFixed(5));
        axes.xmax = Number(axes.xmax.toFixed(5));
        axes.xstep = Number(axes.xstep.toFixed(5));
        axes.ymin = Number(axes.ymin.toFixed(5));
        axes.ymax = Number(axes.ymax.toFixed(5));
        axes.ystep = Number(axes.ystep.toFixed(5));

        return axes;
    },
    addNormalCurve: function(graph, mean, sd, axisResolution, settings) {

    },
    setDefaultSettings: function(settings) {
        /* settings: {
            width: default 600,
            height: default 400,
            includePadding: default true,
            radius: (for circleplots) default 200,
            backgroundColor: default light blue,
            axisColor: default blue,
            lineColor: default blue,
            showAxisTitles: default true,
            xtitle: default null,
            ytitle: default null,
            showXTicks: default true,
            showYTicks: default true,
            includeMinorXTicks: default true,
            includeMinorYTicks: default true,
            includeTooltips: default true,
            fontSize: default 12,
            titleFont: default Helvetica,
            titleFontSize: default 1.4em,
            dataColor: [array] default blue,
            dotFill: filled (default) or empty,
            fillColor: equal to dataColor (filled) or background (empty),
            dotSize: default 4,
            strokeWidth: default 2,
            secondaryStrokeWidth: default 1,
            includeRegressionLine: boolean (default false) -- for scatterplots
            dashedLine: boolean (default true) -- for regression lines
            barSeparation: boolean (default false) -- for bar graphs,
            barGrouping: boolean (default false) -- for bar graphs, whether to use a grouped freq table or not,
            numBins: default 1 -- for bar graphs,
            separateOutliers: default true -- for boxplots,
            splitBasedOnCategory: default false,
            categoricalColumnForSplitting: default [null]
        }*/

        // Set default settings
        if (settings.width === undefined) {
            settings.width = 600;
        }
        if (settings.height === undefined) {
            settings.height = 400;
        }
        if (settings.includePadding === undefined) {
            settings.includePadding = true;
        }
        if (settings.radius === undefined) {
            settings.radius = 200;
        }
        if (settings.backgroundColor === undefined) {
            //settings.backgroundColor = "rgba(219, 232, 255, 0.4)";
            settings.backgroundColor = '#fff';
        }
        if (settings.axisColor === undefined) {
            settings.axisColor = "rgb(74, 115, 181)";
        }
        if (settings.lineColor === undefined) {
            settings.lineColor = "rgb(74, 115, 181)";
        }
        if (settings.showAxisTitles === undefined) {
            settings.showAxisTitles = true;
        }
        if (settings.xtitle === undefined) {
            settings.xtitle = null;
        }
        if (settings.ytitle === undefined) {
            settings.ytitle = null;
        }
        if (settings.showXTicks === undefined) {
            settings.showXTicks = true;
        }
        if (settings.showYTicks === undefined) {
            settings.showYTicks = true;
        }
        if (settings.includeMinorXTicks === undefined) {
            settings.includeMinorXTicks = true;
        }
        if (settings.includeMinorYTicks === undefined) {
            settings.includeMinorYTicks = true;
        }
        if (settings.includeTooltips === undefined) {
            settings.includeTooltips = true;
        }
        if (settings.fontSize === undefined) {
            settings.fontSize = '1.4em';
        }
        if (settings.titleFont === undefined) {
            settings.titleFont = 'Bookman';
        }
        if (settings.titleFontSize === undefined) {
            settings.titleFontSize = '1.4em';
        }
        if (settings.dataColor === undefined) {
            settings.dataColor = "blue";
        }

        if (settings.dotFill === undefined) {
            settings.dotFill = "filled";
            settings.fillColor = settings.dataColor;
        } else if (settings.dotFill === "filled") {
            settings.fillColor = settings.dataColor;
        } else {
            settings.fillColor = settings.backgroundColor;
        }

        if (settings.dotSize == undefined) {
            settings.dotSize = 4;
        }
        if (settings.strokeWidth === undefined) {
            settings.strokeWidth = 2;
        }
        if (settings.secondaryStrokeWidth === undefined) {
            settings.secondaryStrokeWidth = 1;
        }

        if (settings.includeRegressionLine === undefined) {
            settings.includeRegressionLine = false;
        }
        if (settings.dashedLine === undefined) {
            settings.dashedLine = true;
        }
        if (settings.barSeparation === undefined) {
            settings.barSeparation = false;
        }
        if (settings.barGrouping === undefined) {
            settings.barGrouping = false;
        }
        if (settings.numBins === undefined) {
            settings.numBins = 1;
        }
        if (settings.separateOutliers === undefined) {
            settings.separateOutliers = true;
        }
        if (settings.splitBasedOnCategory === undefined) {
            settings.splitBasedOnCategory = false;
        }
        if (settings.categoricalColumnForSplitting === undefined) {
            settings.categoricalColumnForSplitting = [null];
        }

        return settings;
    }
}



