<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>Number of trials</td>
                <td>
                    <input id="binomial-n" type="number" min="0"
                        v-model.number="options.binomial.n" />
                </td>
            </tr>
            <tr>
                <td>Probability of success</td>
                <td>
                    <input id="binomial-p" type="number" step="0.0001" min="0" max="1"
                        v-model.number="options.binomial.p" />
                </td>
            </tr>
            <tr v-if="!options.cdf.calculateBetween">
                <td>Upper bound on number of successes</td>
                <td>
                    <input id="x-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.x" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Lower bound on number of successes</td>
                <td>
                    <input id="left-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.start" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Upper bound on number of successes</td>
                <td>
                    <input id="right-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.end" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result) && showResults">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div v-if="!options.cdf.calculateBetween">
                    The probability that there will be at most {{ options.cdf.x }} successes in {{ options.binomial.n }} trials, when the probability of success on a single trial is {{ options.binomial.p }}, or P(X &le; {{ options.cdf.x }}), is
                </div>
                <div v-if="options.cdf.calculateBetween">
                    The probability that there will be more than {{ options.cdf.start }} successes <strong>(not inclusive)</strong> and at most {{ options.cdf.end }} successes <strong>(inclusive)</strong> in {{ options.binomial.n }} trials, when the probability of success on a single trial is {{ options.binomial.p }}, or P({{ options.cdf.start }} &lt; X &le; {{ options.cdf.end }}), is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <table align="center" class="results-table">
                <tr>
                    <th>Successes</th>
                    <th>Probability</th>
                </tr>
                <tr v-for="(probability, index) in allResults">
                    <td>{{ index }}</td>
                    <td>{{ probability.toPrecision(5) }}</td>
                </tr>
                <tr style="border-top: 1px solid black">
                    <td>Total</td>
                    <td>{{ sumOfProbabilities.toPrecision(5) }}</td>
                </tr>
            </table>

            <!--<h3>Graph</h3>
            <div class="answer-holder">
                <span v-html="normalGraph"></span>
            </div>-->
        </div>

        <div v-if="invalidProbability" class="error-message">
            The probability of success must be between 0 and 1.
        </div>
        <div v-if="invalidTrials" class="error-message">
            The number of trials and number of successes must be integers.
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
//import distgraph from '@/helpers/graphs/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        invalidProbability: function() {
            if (this.options.binomial.p < 0 || this.options.binomial.p > 1) {
                return true;
            }
            return false;
        },
        invalidTrials: function() {
            if (this.options.cdf.calculateBetween) {
                if (!(Number.isInteger(this.options.cdf.start)) || !(Number.isInteger(this.options.cdf.end))) {
                    return true;
                }
            } else {
                if (!(Number.isInteger(this.options.binomial.n)) || !(Number.isInteger(this.options.cdf.x))) {
                    return true;
                }
            }

            return false;
        },
        showResults: function() {
            if (this.invalidProbability || this.invalidTrials) {
                return false;
            }
            return true;
        },
        result: function() {
            if (this.options.cdf.calculateBetween) {
                if (!distributions.anyMissingParameters([this.options.cdf.start, this.options.cdf.end])) {
                    return distributions.binomialCDF(this.options.cdf.end, this.options.binomial.n, this.options.binomial.p) - distributions.binomialCDF(this.options.cdf.start, this.options.binomial.n, this.options.binomial.p);
                }
            } else {
                return distributions.binomialCDF(this.options.cdf.x, this.options.binomial.n, this.options.binomial.p);
            }
        },
        allResults: function() {
            var probabilities = {};

            if (this.options.cdf.calculateBetween) {
                for (var i = this.options.cdf.start + 1; i <= this.options.cdf.end; i++) {
                    if (i >= 0 && i <= this.options.binomial.n) {
                        probabilities[i] = distributions.binomialPDF(i, this.options.binomial.n, this.options.binomial.p);
                    }
                }
            } else {
                for (var i=0; i <= this.options.cdf.x; i++) {
                    if (i >= 0 && i <= this.options.binomial.n) {
                        probabilities[i] = distributions.binomialPDF(i, this.options.binomial.n, this.options.binomial.p);
                    }
                }
            }

            return probabilities;
        },
        sumOfProbabilities: function() {
            var sum = 0;
            for (var index in this.allResults) {
                sum += this.allResults[index];
            }
            return sum;
        },
        /*normalGraph: function() {
            if (this.result !== null) {
                return distgraph.normal({
                    include: true,
                    proportion: this.result,
                    side: 'left'
                }, {});
            }
        }*/
    }
}

</script>



