<template>
    <div>
        <h3>Parameters</h3>

        <table align="center" class="parameters">
            <tr>
                <td>Probability of success</td>
                <td>
                    <input id="geometric-p" type="number" step="0.0001" min="0" max="1"
                        v-model.number="options.geometric.p" />
                </td>
            </tr>
            <tr>
                <td>Number of trials needed for success</td>
                <td>
                    <input id="x-pdf" type="number"
                        v-model.number="options.pdf.x" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result)">
            <h3>Answer</h3>
            <div class="answer-holder" v-if="showResults">
                <div>
                    If the probability of success for each trial is {{ options.geometric.p }}, then the probability that the first success will occur on the {{ options.pdf.x }}<span v-if="options.pdf.x === 1">st</span><span v-if="options.pdf.x === 2">nd</span><span v-if="options.pdf.x === 3">rd</span><span v-if="options.pdf.x > 3">th</span>
                    attempt is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <div v-if="invalidProbability" class="error-message">
                The probability of success must be between 0 and 1.
            </div>
            <div v-if="invalidTrials" class="error-message">
                The number of trials must be an integer greater than 0.
            </div>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        invalidProbability: function() {
            if (this.options.geometric.p < 0 || this.options.geometric.p > 1) {
                return true;
            }
            return false;
        },
        invalidTrials: function() {
            if (!(Number.isInteger(this.options.pdf.x))) {
                return true;
            }
            if (this.options.pdf.x <= 0) {
                return true;
            }
            return false;
        },
        showResults: function() {
            if (this.invalidProbability || this.invalidTrials) {
                return false;
            }
            return true;
        },
        result: function() {
            return distributions.geometricPDF(this.options.pdf.x, this.options.geometric.p);
        }
    }
}

</script>



