<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>First degree of freedom</td>
                <td>
                    <input id="f-df1" type="number" min="0"
                        v-model.number="options.f.df1" />
                </td>
            </tr>
            <tr>
                <td>Second degree of freedom</td>
                <td>
                    <input id="f-df2" type="number" min="0"
                        v-model.number="options.f.df2" />
                </td>
            </tr>
            <tr v-if="!options.cdf.calculateBetween">
                <td>Upper bound (x)</td>
                <td>
                    <input id="x-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.x" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Lower bound</td>
                <td>
                    <input id="left-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.start" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Upper bound</td>
                <td>
                    <input id="right-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.end" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result)">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div v-if="!options.cdf.calculateBetween">
                    The proportion of the F distribution with degrees of freedom {{ options.f.df1 }} and {{ options.f.df2}} that falls below {{ options.cdf.x }}, or P(X &le; {{ options.cdf.x }}), is
                </div>
                <div v-if="options.cdf.calculateBetween">
                    The proportion of the F distribution with degrees of freedom {{ options.f.df1 }} and {{ options.f.df2}} that falls between {{ options.cdf.start }} and {{ options.cdf.end }}, or P({{ options.cdf.start }} &le; X &le; {{ options.cdf.end }}), is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <h3>Graph</h3>
            <div class="answer-holder">
                <span v-html="fGraph"></span>
            </div>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
import distgraph from '@/helpers/graphs/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            if (this.options.cdf.calculateBetween) {
                if (!distributions.anyMissingParameters([this.options.cdf.start, this.options.cdf.end, this.options.f.df1, this.options.f.df2])) {
                    return distributions.fCDF(this.options.cdf.end, this.options.f.df1, this.options.f.df2) - distributions.fCDF(this.options.cdf.start, this.options.f.df1, this.options.f.df2);
                }
            } else {
                return distributions.fCDF(this.options.cdf.x, this.options.f.df1, this.options.f.df2);
            }
        },
        fGraph: function() {
            if (this.result !== null) {
                return distgraph.chiSquare({
                    include: true,
                    proportion: this.result,
                    side: 'left'
                }, {});
            }
        }
    }
}

</script>



