<template>
    <div id="boxplot-parent" class="main-panel">
        <h1>Boxplot</h1>
        <!--<select v-model="column" v-on:change="addPointTooltips">
            <option v-for="(column, index) in numericalData" v-bind:value="index">
                {{ column[0] }}
            </option>
        </select>-->
        <div class="data-btn-holder btn-group">
            <button class="btn" @click="openVariableForm">
                Show Variables
            </button>
        </div>

        <SelectVariablesForBoxplot v-if="variableFormVisible"
            @close='closeForm'>
        </SelectVariablesForBoxplot>
        <div class="grayed-out" v-if="variableFormVisible" @click="closeForm"></div>

        <!--<div v-for="(column, index) in numericalColumnIndices"
            class="variable-selector">
            <input type='checkbox'
                    :value='index'
                    v-model='columnsToShow'> {{ variables[column] }}
        </div>-->

        <Boxplot :dataColumns="dataForBoxplot"
            :options="options">
        </Boxplot>
    </div>
</template>

<script>
import Boxplot from './Boxplot.vue';
import SelectVariablesForBoxplot from './SelectVariablesForBoxplot';
import $ from 'jquery';
import data from '@/helpers/data.js';

export default {
    data: function() {
        return {
            variableFormVisible: false
        };
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.boxplot;
            },
            set: function(newValue) {
                this.$store.dispatch('updateBoxplotOptions', newValue);
            }
        },
        column: {
            get: function() {
                return this.options.column;
            },
            set: function(newValue) {
                this.options = {
                    column: newValue
                };
            }
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        columnsToShow: {
            get: function() {
                return this.options.columnsToShow;
            },
            set: function(newValue) {
                this.options = {
                    columnsToShow: newValue
                };
            }
        },
        numericalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var numericalColumnIndices = this.$store.getters.numericalColumnIndices;
            var numericalColumns = [];

            for (var i=0; i < numericalColumnIndices.length; i++) {
                numericalColumns.push(allDataColumns[numericalColumnIndices[i]]);
            }

            return numericalColumns;
        },
        dataForBoxplot: function() {
            var dataColumns = [];

            for (var i=0; i < this.numericalColumnIndices.length; i++) {
                var currentIndex = this.numericalColumnIndices[i];
                if (this.columnsToShow.indexOf(currentIndex) > -1) {
                    dataColumns.push(this.numericalData[currentIndex]);
                }
            }

            return dataColumns;
        }
    },
    methods: {
        openVariableForm() {
            this.variableFormVisible = true;
        },
        closeForm() {
            this.variableFormVisible = false;
            /*this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });*/
        },
        addPointTooltips: function() {
            $(function() {
                $(".boxplotBox").hover(
                    function() {
                        //console.log("here");
                        var id = $(this).attr('id').split('-');
                        var y = id[1];
                        var tooltip = $("#boxTooltip-" + y);
                        //console.log("tooltip", tooltip);
                        tooltip.removeClass("hiddenTooltip");
                    },
                    function() {
                        var id = $(this).attr('id').split('-');
                        var y = id[1];
                        var tooltip = $("#boxTooltip-" + y);
                        tooltip.addClass("hiddenTooltip");
                    }
                );
            });
            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        }
    },
    watch: {
        columnsToShow: function(val) {
            this.options = {
                height: val.length * 200
            }
            this.addPointTooltips();
        }
    },
    mounted: function() {
        this.addPointTooltips();

        var parentWidth = document.getElementById('boxplot-parent').offsetWidth;
        var graphWidth = parentWidth * 0.8;
        var graphHeight = this.columnsToShow.length * 175;

        if (graphWidth < 500) {
            graphWidth = 500;
        }

        this.options = {
            width: graphWidth,
            height: graphHeight
        }

    },
    components: {
        Boxplot,
        SelectVariablesForBoxplot
    }
}

</script>



