<template>
    <div>
        <h3>Hypotheses</h3>
        <div>
            <!--<div class="null-hypothesis">
                <span v-html="nullHypothesis"></span>:
                <span v-if="!options.usingTwoSamples" v-html="p"></span>
                <span v-if="options.usingTwoSamples" v-html="p1"></span>
                <span>
                    <span v-html="comparison.geq"
                        v-if="options.hypothesis.comparison === 'lt'"></span>
                    <span v-html="comparison.eq"
                        v-if="options.hypothesis.comparison === 'neq'"></span>
                    <span v-html="comparison.leq"
                        v-if="options.hypothesis.comparison === 'gt'"></span>
                </span>
                <span v-if="!options.usingTwoSamples">{{ options.hypothesis.value }}</span>
                <span v-if="options.usingTwoSamples" v-html="p2"></span><br />
            </div>-->

            <div class="alternate-hypothesis">
                <p>
                    Enter the alternate hypothesis:
                </p>
                <span v-html="alternateHypothesis"></span>:
                <span v-if="!options.usingTwoSamples" v-html="p"></span>
                <span v-if="options.usingTwoSamples" v-html="p1"></span>
                <span class="hypothesis-comparison">
                    <span v-html="comparison.gt"
                        v-bind:class="{ 'active-ht-choice' : (options.hypothesis.comparison === 'gt') }"
                        @click="changeHypothesisComparison('gt')"></span>
                    <span v-html="comparison.neq"
                        v-bind:class="{ 'active-ht-choice' : (options.hypothesis.comparison === 'neq') }"
                        @click="changeHypothesisComparison('neq')"></span>
                    <span v-html="comparison.lt"
                        v-bind:class="{ 'active-ht-choice' : (options.hypothesis.comparison === 'lt') }"
                        @click="changeHypothesisComparison('lt')"></span>
                </span>
                <input v-if="!options.usingTwoSamples"
                    v-on:keyup="$emit('refreshMath')" v-model.number="options.hypothesis.value"
                    type="number" step="0.01" />
                <span v-if="options.usingTwoSamples" v-html="p2"></span>
                <div class="error-message" v-if="invalidHypothesis">
                    A proportion must be between 0 and 1.
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data: function() {
        return {
            nullHypothesis: '\\(H_0\\)',
            alternateHypothesis: '\\(H_1\\)',
            p: '\\(p_0\\)',
            p1: '\\(p_1\\)',
            p2: '\\(p_2\\)',
            comparison: {
                eq: '\\(=\\)',
                neq: '\\(\\neq\\)',
                geq: '\\(\\geq\\)',
                gt: '\\(>\\)',
                leq: '\\(\\leq\\)',
                lt: '\\(<\\)'
            }
        }
    },
    methods: {
        updateFormatting: function() {
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        },
        changeHypothesisComparison: function(newComparison) {
            this.options.hypothesis.comparison = newComparison;
            this.updateFormatting();
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testProportion;
            },
            set: function(newValue) {
                this.$store.dispatch('updateProportionTestOptions', newValue);
            }
        },
        invalidHypothesis: function() {
            if (!this.options.usingTwoSamples) {
                if (this.options.hypothesis.value < 0 || this.options.hypothesis.value > 1) {
                    return true;
                }
            }

            return false;
        },
    }
}

</script>



