<template>
    <div>
        <h2>Confidence Interval</h2>
        <div class="ci-results">
            <h3>{{ confidenceLevel }}% confidence interval for
                <span v-if="!options.usingTwoSamples && testType === 'mean'" v-html="mu">
                </span>
                <span v-if="options.usingTwoSamples && testType === 'mean'" v-html="muDifference">
                </span>
                <span v-if="!options.usingTwoSamples && testType === 'proportion'" v-html="p">
                </span>
                <span v-if="options.usingTwoSamples && testType === 'proportion'" v-html="pDifference">
                </span>
                 ({{ testResults.confidenceInterval.min.toFixed(4) }}, {{ testResults.confidenceInterval.max.toFixed(4) }})</h3>

            <span v-html="CIgraph"></span>
        </div>
    </div>
</template>

<script>
import cigraph from '@/helpers/graphs/confidence.js';

export default {
    props: {
        testType: {
            type: String,
            required: true
        },
        testResults: {
            type: Object,
            required: true
        },
        options: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            mu: '\\(\\mu\\):',
            muDifference: '\\(\\mu_1 - \\mu_2\\):',
            p: '\\(p_0\\):',
            pDifference: '\\(p_1 - p_2\\):'
        }
    },
    computed: {
        showCIGraph: function() {
            var ci = this.$props.testResults.confidenceInterval;

            if (this.$props.testType === 'mean') {
                if (this.$props.testResults.ts === null || this.$props.testResults.ts === Infinity || isNaN(ci.min)) {
                    return false;
                }
            }
            if (this.$props.testType === 'proportion') {
                if (isNaN(ci.min)) {
                    return false;
                }
            }
            return true;
        },
        CIgraph: function() {
            var ci = this.$props.testResults.confidenceInterval;

            var comparisonValue = this.$props.options.hypothesis.value;
            if (this.$props.options.usingTwoSamples) {
                comparisonValue = 0;
            }
            if (comparisonValue === '') {
                comparisonValue = null;
            }

            var comparisonType = 'mean';

            if (this.showCIGraph) {
                return cigraph.ConfidenceIntervalGraph(ci, comparisonValue, comparisonType, {
                    strokeWidth: 3,
                    width: 600,
                    height: 150,
                    fontSize: '0.8em',
                    secondaryStrokeWidth: 0
                });
            }
        },
        confidenceLevel: function() {
            return 100 * (1 - this.$props.options.alpha);
        }
    }
}

</script>



