<template>
    <div class="main-panel gof hyp-test">
        <h1>Goodness-of-fit Test</h1>

        <h3>Test Options</h3>

        <div class="organization-toggle-holder">
        <div class="organization-toggle">
            <table align="center">
                <tr>
                    <td>
                        <span class="organization-choice">Use Expected Percentages</span>
                    </td>
                    <td>
                        <label class="switch" @click="updateFormatting">
                            <input type="checkbox" v-model="options.usingExpectedFrequencies" />
                            <span class="slider"></span>
                        </label>
                    </td>
                    <td>
                        <span class="organization-choice">Use Expected Frequencies</span>
                    </td>
                </tr>
            </table>
        </div>
        </div>

        <!--<input v-model="options.experimentName" /><br /><br />-->
        <div class="gof-test-groups">
            Use <input v-model.number="options.numberOfGroups"
                    type="number"
                    v-on:keyup="updateNumberOfGroups"
                    v-on:change="updateNumberOfGroups"/> groups
        </div>

        <table align="center">
            <tr class="btn-table-row">
                <td></td>
                <td style="text-align:center;"><a class="btn" @click="openObsPopup">Import</a></td>
                <td style="text-align:center;"><a class="btn" @click="openExpPopup">Import</a></td>
            </tr>
            <tr>
                <th>Group</th>
                <th>Observed<br /> Frequency</th>
                <th v-if="!options.usingExpectedFrequencies">Expected<br /> Percentage</th>
                <th v-if="options.usingExpectedFrequencies">Expected<br /> Frequency</th>
            </tr>
            <tr v-for="(group, index) in options.groupNames">
                <td>
                    <input type="text" v-model="options.groupNames[index]" />
                </td>
                <td>
                    <input v-model.number="options.observedFrequencies[index]"
                        type="number"
                        :id="'input-' + index + '-0'"
                        v-on:keyup="updateTotalObservedFrequency"
                        v-on:keyup.enter="jumpToNextRow(index, 0)"
                        v-on:change="updateTotalObservedFrequency"/>
                </td>
                <td v-if="!options.usingExpectedFrequencies">
                    <input v-model.number="options.expectedPercentages[index]"
                        type="number"
                        step="0.01"
                        :id="'input-' + index + '-1'"
                        v-on:keyup="updateFormatting"
                        v-on:keyup.enter="jumpToNextRow(index, 1)"
                        v-on:change="updateFormatting" />
                </td>
                <td v-if="!options.usingExpectedFrequencies">
                    <span class="percent">%</span>
                </td>
                <td v-if="options.usingExpectedFrequencies">
                    <input v-model.number="options.expectedFrequencies[index]"
                        type="number"
                        :id="'input-' + index + '-2'"
                        v-on:keyup="updateFormatting"
                        v-on:keyup.enter="jumpToNextRow(index, 2)"
                        v-on:change="updateFormatting" />
                </td>
            </tr>

        </table>

        <div v-if="!frequenciesAreIntegers" class="error-message">
            Note: all freqencies must be entered as integers
        </div>
        <div v-if="tooFewObserved" class="error-message">
            Note: for the test to be valid, all frequencies should be larger than 5
        </div>

        <Results v-if="showResults && frequenciesAreIntegers"
            :testResults="testResults"
            :options="options">
        </Results>

        <div class="grayed-out" v-if="ObsPopupOpen || ExpPopupOpen" @click="closePopup"></div>
        <div class="popup-explanation" v-if="ObsPopupOpen">
            <h1>Pick a column</h1>
            <div class="btn-group-vertical">
            <div class="btn"
                v-for="(column, index) in numericalColumnIndices"
                @click="selectObsColumn(column)">
                {{ variables[column] }}
            </div>
            </div>
            <!--<table>
                <tr v-for="(column, index) in numericalColumnIndices">
                    <td @click="selectObsColumn(column)">
                        {{ variables[column] }}
                    </td>
                </tr>
            </table>-->
        </div>
        <div class="popup-explanation" v-if="ExpPopupOpen">
            <h1>Pick a column</h1>
            <div class="btn-group-vertical">
            <div class="btn"
                v-for="(column, index) in numericalColumnIndices"
                @click="selectExpColumn(column)">
                {{ variables[column] }}
            </div>
            </div>
            <!--<table>
                <tr v-for="(column, index) in numericalColumnIndices">
                    <td @click="selectExpColumn(column)">
                        {{ variables[column] }}
                    </td>
                </tr>
            </table>-->
        </div>

    </div>
</template>

<script>
import tests from '@/helpers/tests.js';
import data from '@/helpers/data.js';

import Results from './gof-tests/Results.vue';

export default {
    data: function() {
        return {
            ObsPopupOpen: false,
            ExpPopupOpen: false
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testGOF;
            },
            set: function(newValue) {
                this.$store.dispatch('updateGOFTestOptions', newValue);
            }
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        dataAsColumns: function() {
            return this.$store.getters.dataAsColumns;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        anyEmptyFrequencies: function() {
            for (var i=0; i < this.options.observedFrequencies.length; i++) {
                if (this.options.observedFrequencies[i] === null || this.options.observedFrequencies[i] === '') {
                    return true;
                }
            }
            return false;
        },
        tooFewObserved: function() {
            for (var i=0; i < this.options.observedFrequencies.length; i++) {
                if (this.options.observedFrequencies[i] !== null && this.options.observedFrequencies[i] < 5) {
                    return true;
                }
            }
            return false;
        },
        frequenciesAreIntegers: function() {
            if (!data.isIntegerArray(this.options.observedFrequencies)) {
                return false;
            }
            if (this.options.usingExpectedFrequencies && !data.isIntegerArray(this.options.expectedFrequencies)) {
                return false;
            }
            return true;
        },
        showResults: function() {
            if (this.anyEmptyFrequencies) {
                return false;
            }
            if (this.testResults.chiSquare === Infinity) {
                return false;
            }
            if (isNaN(this.testResults.p)) {
                return false;
            }
            return true;
        },
        testResults: function() {
            return tests.testGOF(this.options);
        }
    },
    methods: {
        updateNumberOfGroups: function() {
            var length = this.options.numberOfGroups;

            if (this.options.groupNames.length > length) {
                this.options.groupNames = this.options.groupNames.slice(0, length);
            }
            if (this.options.observedFrequencies.length > length) {
                this.options.observedFrequencies = this.options.observedFrequencies.slice(0, length);
            }
            if (this.options.expectedFrequencies.length > length) {
                this.options.expectedFrequencies = this.options.expectedFrequencies.slice(0, length);
            }
            if (this.options.expectedPercentages.length > length) {
                this.options.expectedPercentages = this.options.expectedPercentages.slice(0, length);
            }

            while (this.options.groupNames.length < length) {
                this.options.groupNames.push('Group ' + (this.options.groupNames.length + 1));
            }
            while (this.options.observedFrequencies.length < length) {
                this.options.observedFrequencies.push(null);
            }
            while (this.options.expectedFrequencies.length < length) {
                this.options.expectedFrequencies.push(null);
            }
            while (this.options.expectedPercentages.length < length) {
                this.options.expectedPercentages.push(null);
            }
        },
        jumpToNextRow: function(index, column) {
            if (index === this.options.numberOfGroups - 1) {
                this.options.numberOfGroups += 1;
                this.options.groupNames.push('Group ' + this.options.numberOfGroups);
            }

            this.$nextTick(function() {
                var inputToFocus = document.getElementById('input-' + (index + 1) + '-' + column);
                inputToFocus.focus();
            });
        },
        updateTotalObservedFrequency: function() {
            var totalFreq = 0;
            for (var i=0; i < this.options.observedFrequencies.length; i++) {
                totalFreq += this.options.observedFrequencies[i];
            }
            this.options.totalFrequency = totalFreq;

            this.updateFormatting();
        },
        updateFormatting: function() {
            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        },
        openObsPopup: function() {
            this.ObsPopupOpen = true;
        },
        openExpPopup: function() {
            this.ExpPopupOpen = true;
        },
        closePopup: function() {
            this.ObsPopupOpen = false;
            this.ExpPopupOpen = false;
        },
        selectObsColumn: function(index) {
            this.options.observedFrequencies = this.dataAsColumns[index].slice(1, this.options.numberOfGroups + 1);

            this.closePopup();
            this.updateTotalObservedFrequency();
        },
        selectExpColumn: function(index) {
            if (this.options.usingExpectedFrequencies) {
                this.options.expectedFrequencies = this.dataAsColumns[index].slice(1, this.options.numberOfGroups + 1);
            } else {
                this.options.expectedPercentages = this.dataAsColumns[index].slice(1, this.options.numberOfGroups + 1);
            }

            this.closePopup();
        }
    },
    mounted() {
        this.updateFormatting();
    },
    components: {
        Results
    }
}

</script>



