<template>
    <div class="main-panel">
        <div class="nav-tabs">
            <ul>
                <li :class="{active: options.activeTab === 'pdf'}"
                    @click="tabNav('pdf')">
                    PDF
                </li>
                <li :class="{active: options.activeTab === 'cdf'}"
                    @click="tabNav('cdf')">
                    CDF
                </li>
                <li :class="{active: options.activeTab === 'inverse'}"
                    @click="tabNav('inverse')">
                    Inverse
                </li>
            </ul>
        </div>

        <ProbDF v-if="options.activeTab === 'pdf'"></ProbDF>

        <CumDF v-if="options.activeTab === 'cdf'"></CumDF>

        <Inverse v-if="options.activeTab === 'inverse'"></Inverse>
    </div>
</template>

<script>
import ProbDF from './distributions/PDF.vue';
import CumDF from './distributions/CDF.vue';
import Inverse from './distributions/Inverse.vue';

export default {
    methods: {
        tabNav: function(id) {
            this.options.activeTab = id;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        }
    },
    components: {
        ProbDF,
        CumDF,
        Inverse
    }
}

</script>



