<template>
    <div class="results">
        <h2>Results</h2>
        <h3>Hypotheses</h3>
        <div>
            <span v-html="nullHypothesis"></span>: The observed frequencies match the expected frequencies<br />

            <span v-html="alternateHypothesis"></span>: The observed frequencies do not match the expected frequencies
        </div>

        <h3>Test Statistic</h3>
        <div>
            <span v-html="chisquare"></span>
            {{ testResults.chiSquare.toPrecision(4) }}
        </div>

        <h3>p-Value</h3>
        <div>
            <span v-html="pVal"></span>
            {{ testResults.p.toPrecision(4) }}
        </div>
    </div>
</template>

<script>

export default {
    props: {
        testResults: {
            type: Object,
            required: true
        },
        options: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            nullHypothesis: '\\(H_0\\)',
            alternateHypothesis: '\\(H_1\\)',
            chisquare: '\\(\\chi^2 = \\)',
            comparison: {
                eq: '\\(=\\)',
                neq: '\\(\\neq\\)',
                geq: '\\(\\geq\\)',
                gt: '\\(>\\)',
                leq: '\\(\\leq\\)',
                lt: '\\(<\\)'
            },
            pVal: '\\(p = \\)'
        }
    }
}

</script>



