<template>
    <div class="popup-explanation" v-if="show">
        <h1>Correlation Coefficient</h1>
    </div>
</template>

<script>
export default {
    props: [
        'activePopup'
    ],
    computed: {
        show: function() {
            if (this.$props.activePopup === 'correlationCoefficient') {
                return true;
            }
            return false;
        }
    }
}
</script>



