<template>
    <div>
        <h3>Parameters</h3>

        <table align="center" class="parameters">
            <tr>
                <td>
                    Expected number of occurrences in one period,
                    <span v-html="lambda"></span>
                </td>
                <td>
                    <input id="poisson-lambda" type="number" step="0.0001"
                        v-model.number="options.poisson.lambda" />
                </td>
            </tr>
            <tr>
                <td>Number of occurrences (x)</td>
                <td>
                    <input id="x-pdf" type="number"
                        v-model.number="options.pdf.x" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result) && showResults">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div>
                    If {{ options.poisson.lambda }}
                    <span v-if="options.poisson.lambda === 1">
                        occurrence is expected
                    </span>
                    <span v-if="options.poisson.lambda !== 1">
                        occurrences are expected
                    </span>
                    in each period, the probability of {{ options.pdf.x }} occurrence<span v-if="options.pdf.x !== 1">s</span> in one period is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>
        </div>
        <div v-if="invalidX" class="error-message">
            The number of occurrences must be an integer greater than or equal to 0.
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

export default {
    data: function() {
        return {
            lambda: '\\(\\lambda\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            return distributions.poissonPDF(this.options.pdf.x, this.options.poisson.lambda);
        },
        invalidX: function() {
            if (!Number.isInteger(this.options.pdf.x)) {
                return true;
            }
            if (this.options.pdf.x < 0) {
                return true;
            }
            return false;
        },
        showResults: function() {
            if (this.invalidX) {
                return false;
            }
            return true;
        }
    },
    mounted: function() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    }
}

</script>



