<template>
    <div class="panel-tab">
        <div class="panel-block">
            <!--<span class="panel-block-title">Summary Statistics Broken Down by Category</span>-->

            <div class="organization-toggle-holder">
            <div class="organization-toggle">
                <span class="organization-choice">One Variable at a Time</span>
                <label class="switch" @click="updateFormatting">
                    <input type="checkbox" v-model="organizeByLevel" />
                    <span class="slider"></span>
                </label>
                <span class="organization-choice">One Categorical Level at a Time</span>
            </div>
            </div>

            <div class="data-btn-holder btn-group">
                <button class="btn" @click="openVariableForm"
                    v-if="organizeByLevel">
                    Show Variables
                </button><button class="btn" @click="openStatsForm">
                    Show Statistics
                </button>
            </div>

            <StatsTableSelectVariables v-if="variableFormVisible && organizeByLevel"
                @close='closeForm'>
            </StatsTableSelectVariables>
            <StatsTableSelectStatistics v-if="statsFormVisible"
                @close='closeForm'>
            </StatsTableSelectStatistics>
            <div class="grayed-out" v-if="variableFormVisible || statsFormVisible" @click="closeForm"></div>

            <!--{{ categoricalStats }}-->

            <h1>Separated by <select class="custom-select" v-model="separatedBy" v-on:change="updateSeparatedBy">
                <option class="custom-select-option" v-for="(variable, index) in categoricalVariables" v-bind:value="index">
                    {{ categoricalVariables[index] }}
                </option>
            </select></h1>

            <!--<div v-for="(categoricalVariable, categoricalColumnIndex) in categoricalStats"
                    v-if="!organizeByLevel && categoricalVariables[separatedBy] === variables[categoricalColumnIndex]">-->
                <!--<h1>Separated by {{ variables[categoricalColumnIndex] }}</h1>-->
            <div>
            <!--<div v-for="(categoricalVariable, categoricalColumnIndex) in categoricalStats">-->
            <!--<div>-->
                <div v-if="!organizeByLevel">
                    View summary statistics for variable: <select v-model="currentlyViewingVariable" v-on:change="updateFormatting">
                        <option v-for="(numericalVariable, numericalColumnIndex) in numericalVariables" v-bind:value="numericalColumnIndex">
                            {{ numericalVariable }}
                        </option>
                    </select>
                </div>
                <div v-if="organizeByLevel">
                    View summary statistics for level: <select v-model="currentlyViewingLevel" v-on:change="updateFormatting">
                        <option v-for="(level, levelIndex) in levels[currentlyViewingCategory]" v-bind:value="levelIndex">
                            {{ level }}
                        </option>
                    </select>
                </div>

                <h2 v-if="!organizeByLevel">{{ numericalVariables[currentlyViewingVariable] }}</h2>
                <h2 v-if="organizeByLevel">{{ levels[currentlyViewingCategory][currentlyViewingLevel] }}</h2>

                <DescriptiveStatisticsTable v-if="!organizeByLevel"
                    :statistics="categoricalStats[currentlyViewingCategory][numericalVariables[currentlyViewingVariable]]">
                </DescriptiveStatisticsTable>

                <!--<DescriptiveStatisticsTable v-if="organizeByLevel"
                    :statistics="categoricalStats[currentlyViewingCategory][levels[currentlyViewingCategory][currentlyViewingLevel]]">
                </DescriptiveStatisticsTable>-->
                <DescriptiveStatisticsTable v-if="organizeByLevel"
                    :statistics="statisticsToShowByCategory">
                </DescriptiveStatisticsTable>
            </div>



        </div>
    </div>
</template>

<script>

import DescriptiveStatisticsTable from './DescriptiveStatisticsTable.vue';
import StatsTableSelectStatistics from './StatsTableSelectStatistics.vue';
import StatsTableSelectVariables from './StatsTableSelectVariables.vue';

export default {
    mounted() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    },
    data: function() {
        return {
            statsFormVisible: false,
            variableFormVisible: false
        };
    },
    computed: {
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalVariables: function() {
            return this.$store.getters.numericalColumnNames;
        },
        categoricalVariables: function() {
            return this.$store.getters.categoricalColumnNames;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        categoricalColumnIndices: function() {
            return this.$store.getters.categoricalColumnIndices;
        },
        categoricalStatsByLevel: function() {
            return this.$store.getters.categoryStatsByLevel;
        },
        categoricalStatsByVariable: function() {
            return this.$store.getters.categoryStatsByVariable;
        },
        organizeByLevel: {
            get: function() {
                return this.$store.getters.options.categoricalStatistics.organizeByLevel;
            },
            set: function(newValue) {
                this.$store.dispatch('updateCategoricalStatsOptions', {
                    organizeByLevel: newValue
                });
            }
        },
        categoricalStats: function() {
            if (this.organizeByLevel) {
                return this.categoricalStatsByLevel;
            } else {
                return this.categoricalStatsByVariable;
            }
        },
        levels: function() {
            var levels = {};
            for (var categoricalVariable in this.categoricalStatsByLevel) {
                levels[categoricalVariable] = [];
                for (var level in this.categoricalStatsByLevel[categoricalVariable]) {
                    levels[categoricalVariable].push(level);
                }
            }
            return levels;
        },
        currentlyViewingVariable: {
            get: function() {
                return this.$store.getters.options.categoricalStatistics.currentlyViewingVariable;
            },
            set: function(newValue) {
                this.$store.dispatch('updateCategoricalStatsOptions', {
                    currentlyViewingVariable: newValue
                });
            }
        },
        separatedBy: {
            get: function() {
                return this.$store.getters.options.categoricalStatistics.separatedBy;
            },
            set: function(newValue) {
                this.$store.dispatch('updateCategoricalStatsOptions', {
                    separatedBy: newValue
                });
            }
        },
        currentlyViewingCategory: function() {
            return this.categoricalColumnIndices[this.separatedBy];
        },
        currentlyViewingLevel: {
            get: function() {
                return this.$store.getters.options.categoricalStatistics.currentlyViewingLevel;
            },
            set: function(newValue) {
                this.$store.dispatch('updateCategoricalStatsOptions', {
                    currentlyViewingLevel: newValue
                });
            }
        },
        visibleVariableIndices: function() {
            var hidden = this.$store.getters.options.statisticsPage.hiddenVariableIndices;
            var visible = [];
            for (var i=0; i < this.numericalColumnIndices.length; i++) {
                var index = this.numericalColumnIndices[i];
                if (hidden.indexOf(index) < 0) {
                    visible.push(index);
                }
            }
            return visible;
        },
        statisticsToShowByCategory: function() {
            var statisticsToShow = {};
            for (var i=0; i < this.visibleVariableIndices.length; i++) {
                var variableName = this.variables[this.visibleVariableIndices[i]];
                statisticsToShow[variableName] = this.categoricalStats[this.currentlyViewingCategory][this.levels[this.currentlyViewingCategory][this.currentlyViewingLevel]][variableName];
            }

            return statisticsToShow;
        },
    },
    methods: {
        updateFormatting: function() {
            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        },
        updateSeparatedBy: function() {
            this.currentlyViewingLevel = 0;
            this.updateFormatting();
        },
        openStatsForm() {
            this.statsFormVisible = true;
        },
        openVariableForm() {
            this.variableFormVisible = true;
        },
        closeForm() {
            this.statsFormVisible = false;
            this.variableFormVisible = false;
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        }
    },
    components: {
        DescriptiveStatisticsTable,
        StatsTableSelectStatistics,
        StatsTableSelectVariables
    }
}

</script>



