<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>Mean</td>
                <td>
                    <input id="normal-mean" type="number" step="0.0001"
                        v-model.number="options.normal.mean" />
                </td>
            </tr>
            <tr>
                <td>Standard deviation</td>
                <td>
                    <input id="normal-sd" type="number" step="0.0001" min="0"
                        v-model.number="options.normal.sd" />
                </td>
            </tr>
            <tr>
                <td>Value (x)</td>
                <td>
                    <input id="x-pdf" type="number" step="0.0001"
                        v-model.number="options.pdf.x" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result)">
            <h3>Answer</h3>
            <div class="answer-holder">
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            return distributions.normalPDF(this.options.pdf.x, this.options.normal.mean, this.options.normal.sd);
        }
    }
}

</script>



