<template>
    <div class="main-panel">
        <div class="data-btn-holder btn-group">
            <button class="btn" @click="loadSampleData">
                Load Sample Data
            </button><button class="btn" @click="clearData">
                Clear Data
            </button>
        </div>

        <div class="data-table-holder">
            <HotTable :root="root" ref="DataTable" :settings="hotSettings"></HotTable>
        </div>
    </div>
</template>

<script>
  import HotTable from 'vue-handsontable-official';
  import Handsontable from 'handsontable';
  import Vue from 'vue';
  import $ from 'jquery';
  import _ from 'lodash';
  import data from '@/helpers/data.js';

  function firstRowRenderer(instance, td, row, col, prop, value, cellProperties) {
      Handsontable.renderers.TextRenderer.apply(this, arguments);
      td.style.padding = '10px';
      td.style.background = 'rgba(0, 0, 0, 0.05)';
  }

  function firstColumnRenderer(instance, td, row, col, prop, value, cellProperties) {
      Handsontable.renderers.TextRenderer.apply(this, arguments);
      td.style.padding = '20px';
      td.style.background = 'rgba(0, 0, 0, 0.05)';
  }

  function datacellRenderer(instance, td, row, col, prop, value, cellProperties) {
      Handsontable.renderers.TextRenderer.apply(this, arguments);

      //td.style.padding = '5px';
      //td.style.fontFamily = 'Garamond';
      //td.style.fontWeight = '600';
      //td.style.fontSize = '1.4em';
      //td.style.padding = '4px';
      td.style.textAlign = 'center';
  }

export default {
    data: function() {
        var vuem = this;
        return {
            root: 'test-hot',
            hotSettings: {
              data: [],
              className: 'data-table',
              colHeaders: false,
              /*columns: [
                  {type: 'numeric', format: "0.00"},
                  {type: 'numeric', format: "0.00"},
                  {type: 'numeric', format: "0.00"},
                  {type: 'numeric', format: "0.00"},
                  //{}
              ],*/
              rowHeaders: function(index) {
                  if (index !== 0) {
                      return index;
                  }
              },
              minSpareCols: 20,
              minSpareRows: 20,
              fixedRowsTop: 1,
              autoWrapRow: true,
              observeChanges: true,
              afterChange: this.saveData,
              undo: true,
              contextMenu: {
                  callback: function(key, options) {
                      var vm = this;

                      if (key === 'cat_yes') {
                          setTimeout(function() {
                              vuem.makeColumnCategorical(vm.getSelected()[1]);
                          }, 100);
                      }

                      if (key === 'cat_no') {
                          setTimeout(function() {
                              vuem.unmakeColumnCategorical(vm.getSelected()[1]);
                          }, 100);
                      }

                      /*if (key === 'categorical') {
                          setTimeout(function() {
                              //vm.catt();
                              console.log("this:", this.name);
                              console.log("HELP", example);
                              console.log("vm:", vm);
                              console.log("selected: " + vm.getSelected());
                          }, 100);
                      }*/
                  },
                  items: {
                      "row_above": {
                          disabled: function() {
                              //if first row, disable this option
                              return this.getSelected()[0] === 0;
                          }
                      },
                      "row_below": {name: "Insert row below"},
                      "hsep1": "---------",
                      "col_left": {
                          name: "Insert column to the left"
                      },
                      "col_right": {name: "Insert column to the right"},
                      "hsep2": "---------",
                      "remove_row": {
                          name: "Remove row",
                          disabled: function() {
                              //if first row, disable this option
                              return this.getSelected()[0] === 0;
                          }
                      },
                      "remove_col": {name: "Remove column"},
                      "hsep3": "---------",
                      "cat_yes": {
                          name: "Make column categorical",
                          disabled: function() {
                              return vuem.columnFixedCategorical(this.getSelected()[1]);
                          }
                      },
                      "cat_no": {
                          name: "Automatic column type",
                          disabled: function() {
                              return !vuem.columnFixedCategorical(this.getSelected()[1]);
                          }
                      }
                  }
              },
              columnSorting: true,
              cells: function(row, column, prop) {
                  var cellMeta = {};

                  if (row === 0) {
                      cellMeta.renderer = firstRowRenderer;
                  } else {
                      cellMeta.renderer = datacellRenderer;
                  }

                  return cellMeta;
              }
            },
        };
    },
    name: 'SampleApp',
    computed: {
        currentStoreData() {
            return this.$store.getters.dataAsRows;
        },
        currentColumnData() {
            return this.$store.getters.dataAsColumns;
        },
        currentTableData() {
            return this.hotSettings.data;
        },
        options() {
            return this.$store.getters.options;
        }
    },
    methods: {
        columnFixedCategorical: function(selectedColumn) {
            var fixedCat = this.options.fixedCategoricalColumns;
            if (fixedCat.indexOf(selectedColumn) > -1) {
                return true;
            }
            return false;
        },
        makeColumnCategorical: function(selectedColumn) {
            this.$store.dispatch('makeColumnCategorical', selectedColumn);
            this.saveData();
            this.$store.dispatch('resetStatisticsOptions');
        },
        unmakeColumnCategorical: function(selectedColumn) {
            this.$store.dispatch('makeColumnAutomaticType', selectedColumn);
            this.saveData();
            this.$store.dispatch('resetStatisticsOptions');
        },
        saveData: _.debounce(
            function() {
                console.log("saving data...");

                this.$store.dispatch('updateData', this.currentTableData);
                this.$store.dispatch('updateStats');
            },
            1000
        ),
        addColumn: function() {
            this.$store.dispatch('addDataColumn');
        },
        loadSampleData: function() {
            //this.$store.dispatch('loadInitialData');
            this.$store.dispatch('updateData', data.getIrisData());
            this.loadStoreData();
        },
        clearData: function() {
            this.$store.dispatch('clearData');
            this.loadStoreData();
        },
        loadStoreData: function() {
            this.hotSettings.data = JSON.parse(JSON.stringify(this.currentStoreData));
        }
    },
    mounted: function() {
        var current = this;
        setTimeout(function() {
                current.loadStoreData();
            }, 100
        );

        // CTRL + S to save data
        window.addEventListener('keydown', function(e) {
            if (e.ctrlKey === true && e.keyCode === 83) {
                e.preventDefault();
                current.saveData();
            }
        });
    },
    components: {
      HotTable
    }
  }
</script>



