<template>
    <div class="main-panel">
        <div class="nav-tabs">
            <ul>
                <li :class="{active: 0 === activeTab}"
                    @click="tabNav(0)">
                    Test Options
                </li>
                <li v-if="!testOptions.readyToBegin"
                    class="disabled-tab">
                    Test Results
                </li>
                <li v-if="testOptions.readyToBegin"
                    :class="{active: 1 === activeTab}"
                    @click="tabNav(1)">
                    Test Results
                </li>

            </ul>
        </div>
        <TestOptions v-if="activeTab === 0"></TestOptions>
        <TestResults v-if="activeTab === 1"></TestResults>
    </div>
</template>

<script>

import TestOptions from './tests/TestOptions.vue';
import TestResults from './tests/TestResults.vue';

export default {
    data: function() {
        return {
            activeTab: 0
        }
    },
    computed: {
        testOptions: function() {
            return this.$store.getters.options.test;
        }
    },
    methods: {
        tabNav: function(tabId) {
            this.activeTab = tabId;
        }
    },
    components: {
        TestOptions,
        TestResults
    }
}

</script>

<style lang="scss">

.test-option {

    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 2px;
    padding: 8px;
    margin: 5px;


    &:enabled {
        cursor: pointer;
    }
}

.inactiveOption {
    background-color: rgba(0,0,0,0.04);

    &:enabled {
        &:hover {
            background-color: rgba(0,0,0,0.08);
        }
    }
}

.activeOption:enabled {
    background-color: rgba(66,165,245,1);

    /*&:enabled {
        &:hover {
            background-color: rgba(21,101,192 ,1);
        }
    }*/
}

.start-test {

    &:enabled {
        background-color: rgba(66,165,245,1);
        color: white;
        cursor: pointer;

        &:hover {
            background-color: rgba(21,101,192 ,1);
        }
    }

    border: 1px solid rgba(21,101,192 ,1);
    border-radius: 2px;
    padding: 10px;
}

.start-test-holder {
    margin: 40px;
    float: right;
}

</style>



