<template>
    <div>
        <h3>Sample Statistics</h3>

        <table align="center" class="parameters" v-if="!options.usingTwoSamples">
            <tr>
                <td>Sample mean, <span v-html="xBarDisplay"></span></td>
                <td>
                    <input id="mean-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.oneSample.xBar"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td>Sample size, <span v-html="nDisplay"></span></td>
                <td>
                    <input id="n-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.oneSample.n"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td>Sample standard deviation, <span v-html="sDisplay"></span></td>
                <td>
                    <input id="s-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.oneSample.sampleStdDev"
                        type="number" step="0.0001" />
                </td>
            </tr>
        </table>

        <table align="center" class="parameters" v-if="options.usingTwoSamples">
            <tr>
                <td colspan="2" style="text-align:center">Sample means</td>
            </tr>
            <tr>
                <td><span v-html="xBar1Display"></span></td>
                <td>
                    <input id="mean-input-1"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.xBar[0]"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td><span v-html="xBar2Display"></span></td>
                <td>
                    <input id="mean-input-2"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.xBar[1]"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td colspan="2" style="text-align:center">Sample sizes</td>
            </tr>
            <tr>
                <td><span v-html="n1Display"></span></td>
                <td>
                    <input id="n-input-1"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.n[0]"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td><span v-html="n2Display"></span></td>
                <td>
                    <input id="n-input-2"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.n[1]"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td colspan="2" style="text-align:center">Sample standard deviations</td>
            </tr>
            <tr>
                <td><span v-html="s1Display"></span></td>
                <td>
                    <input id="s-input-1"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.sampleStdDev[0]"
                        type="number" step="0.0001" />
                </td>
            </tr>
            <tr>
                <td><span v-html="s2Display"></span></td>
                <td>
                    <input id="s-input-2"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.sampleStdDev[1]"
                        type="number" step="0.0001" />
                </td>
            </tr>
        </table>
    </div>
</template>

<script>

export default {
    data: function() {
        return {
            xBarDisplay: '\\(\\overline{x}\\)',
            xBar1Display: '\\(\\overline{x}_1\\)',
            xBar2Display: '\\(\\overline{x}_2\\)',
            nDisplay: '\\(n\\)',
            n1Display: '\\(n_1\\)',
            n2Display: '\\(n_2\\)',
            sDisplay: '\\(s\\)',
            s1Display: '\\(s_1\\)',
            s2Display: '\\(s_2\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testMean;
            },
            set: function(newValue) {
                this.$store.dispatch('updateMeanTestOptions', newValue);
            }
        }
    },
}

</script>



