<template>
    <div class="distribution">
        <h2>Inverse CDF</h2>

        <h3>
            <select class="pick-distribution" v-model="options.inverse.distribution" v-on:change="clearX">
                <option v-for="distribution in availableDistributions"
                    v-bind:value="distribution">
                    {{ distribution }}
                </option>
            </select>
            Distribution
        </h3>

        <div v-if="options.inverse.distribution !== ''">
            <NormalInv v-if="options.inverse.distribution === 'Normal'">
            </NormalInv>

            <tInv v-if="options.inverse.distribution === 'Student\'s t'">
            </tInv>

            <ChiSquareInv v-if="options.inverse.distribution === 'Chi-square'">
            </ChiSquareInv>

            <fInv v-if="options.inverse.distribution === 'F'">
            </fInv>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

import NormalInv from './inverses/NormalInv.vue';
import tInv from './inverses/tInv.vue';
import ChiSquareInv from './inverses/ChiSquareInv.vue';
import fInv from './inverses/fInv.vue';

export default {
    data: function() {
        return {
            availableDistributions: [
                'Normal',
                'Student\'s t',
                'Chi-square',
                'F'
            ]
        }
    },
    methods: {
        clearX: function() {
            this.options.inverse.x = null;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        }
    },
    components: {
        NormalInv,
        tInv,
        ChiSquareInv,
        fInv
    }
}

</script>



