<template>
    <div class="distribution pdf">
        <h2>Probability Density Function</h2>

        <h3>
            <select class="pick-distribution" v-model="options.pdf.distribution" v-on:change="clearX">
                <option v-for="distribution in availableDistributions"
                    v-bind:value="distribution">
                    {{ distribution }}
                </option>
            </select>
            Distribution
        </h3>

        <div v-if="options.pdf.distribution !== ''">
            <NormalPDF v-if="options.pdf.distribution === 'Normal'">
            </NormalPDF>

            <tPDF v-if="options.pdf.distribution === 'Student\'s t'">
            </tPDF>

            <ChiSquarePDF v-if="options.pdf.distribution === 'Chi-square'">
            </ChiSquarePDF>

            <fPDF v-if="options.pdf.distribution === 'F'">
            </fPDF>

            <BinomialPDF v-if="options.pdf.distribution === 'Binomial'">
            </BinomialPDF>

            <PoissonPDF v-if="options.pdf.distribution === 'Poisson'">
            </PoissonPDF>

            <GeometricPDF v-if="options.pdf.distribution === 'Geometric'">
            </GeometricPDF>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';

import NormalPDF from './pdfs/NormalPDF.vue';
import tPDF from './pdfs/tPDF.vue';
import ChiSquarePDF from './pdfs/ChiSquarePDF.vue';
import fPDF from './pdfs/fPDF.vue';
import BinomialPDF from './pdfs/BinomialPDF.vue';
import PoissonPDF from './pdfs/PoissonPDF.vue';
import GeometricPDF from './pdfs/GeometricPDF.vue';

export default {
    data: function() {
        return {
            availableDistributions: [
                'Normal',
                'Student\'s t',
                'Chi-square',
                'F',
                'Binomial',
                'Poisson',
                'Geometric'
            ]
        }
    },
    methods: {
        clearX: function() {
            this.options.pdf.x = null;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        }
    },
    components: {
        NormalPDF,
        tPDF,
        ChiSquarePDF,
        fPDF,
        BinomialPDF,
        PoissonPDF,
        GeometricPDF
    }
}

</script>



