import data from '@/helpers/data.js';
import statistics from '@/helpers/statistics.js';

export const addDataColumn = function(state) {
    state.dataAsRows[0].push("Title");
    for (let i=1; i<state.dataAsRows.length; i++) {
        state.dataAsRows[i].push(null);
    }
    state.dataAsColumns = data.transpose(state.dataAsRows);
}

export const updateData = function(state, newData) {
    var dataAsRows = data.convertNumericalDataToNumbers(newData);
    var dataAsColumns = data.transpose(dataAsRows);

    // save the data itself
    state.dataAsRows = dataAsRows;
    state.dataAsColumns = dataAsColumns;

    // process the data: save column types
    state.variables = data.findVariableNames(dataAsRows);
    var columnTypes = data.findColumnTypes(dataAsColumns, state.options.fixedCategoricalColumns);
    var numericalColumns = data.findNumericalColumnIndices(columnTypes);
    var categoricalColumns = data.findCategoricalColumnIndices(columnTypes);
    state.numericalColumnIndices = numericalColumns;
    state.categoricalColumnIndices = categoricalColumns;

    // set up initial column visibility for stats page
    var numericalColumnVisibility = {};
    for (var i=0; i < numericalColumns.length; i++) {
        var index = numericalColumns[i];
        numericalColumnVisibility[index] = true;
    }
    state.options.statisticsPage.numericalColumnVisibility = numericalColumnVisibility;
    // show all stats in descriptive table by default
    state.options.statisticsPage.availableStatistics = statistics.availableStatistics();

    var variableNames = data.findVariableNames(dataAsRows);
    var numericalColumnNames = [];
    var categoricalColumnNames = [];

    for (var i=0; i < numericalColumns.length; i++) {
        numericalColumnNames.push(variableNames[numericalColumns[i]]);
    }
    for (var j=0; j < categoricalColumns.length; j++) {
        categoricalColumnNames.push(variableNames[categoricalColumns[j]]);
    }

    state.numericalColumnNames = numericalColumnNames;
    state.categoricalColumnNames = categoricalColumnNames;

    // process the data: update statistics
    state.descriptiveStatistics = statistics.descriptiveStatistics(dataAsColumns, numericalColumns, state.options.decimalPlaces);
    state.correlationMatrix = statistics.correlationMatrix(dataAsColumns, numericalColumns, state.options.decimalPlaces);
    state.covarianceMatrix = statistics.covarianceMatrix(dataAsColumns, numericalColumns, state.options.decimalPlaces);

    state.categoryStatsByLevel = statistics.categoryStatsByLevel(dataAsRows, columnTypes, state.options.decimalPlaces);
    state.categoryStatsByVariable = statistics.categoryStatsByVariable(dataAsRows, columnTypes, state.options.decimalPlaces);
    //state.correlationMatrixByLevel = statistics.correlationMatrixByLevel(dataAsRows, categoricalColumns, state.options.decimalPlaces);
    //state.covarianceMatrixByLevel = statistics.covarianceMatrixByLevel(dataAsRows, categoricalColumns, state.options.decimalPlaces);
}

export const clearData = function(state) {
    state.dataAsRows = [[null], [null]];
    state.dataAsColumns = [[null], [null]];
    state.options.fixedCategoricalColumns = [];
}

export const makeColumnCategorical = function(state, columnIndex) {
    if (state.options.fixedCategoricalColumns.indexOf(columnIndex) < 0) {
        state.options.fixedCategoricalColumns.push(columnIndex);
    }
}

export const makeColumnAutomaticType = function(state, columnIndex) {
    var position = state.options.fixedCategoricalColumns.indexOf(columnIndex)
    if (position > -1) {
        state.options.fixedCategoricalColumns.splice(position, 1);
    }
}

export const updateStats = function(state) {
    var dataAsRows = state.dataAsRows;
    var dataAsColumns = state.dataAsColumns;

    // process the data: save column types
    var columnTypes = data.findColumnTypes(dataAsColumns, state.options.fixedCategoricalColumns);
    var numericalColumns = data.findNumericalColumnIndices(columnTypes);
    var categoricalColumns = data.findCategoricalColumnIndices(columnTypes);
    state.numericalColumnIndices = numericalColumns;
    state.categoricalColumnIndices = categoricalColumns;

    // set up initial column visibility for stats page
    var numericalColumnVisibility = {};
    for (var i=0; i < numericalColumns.length; i++) {
        var index = numericalColumns[i];
        numericalColumnVisibility[index] = true;
    }
    state.options.statisticsPage.numericalColumnVisibility = numericalColumnVisibility;
    // show all stats in descriptive table by default
    state.options.statisticsPage.availableStatistics = statistics.availableStatistics();

    var variableNames = data.findVariableNames(dataAsRows);
    var numericalColumnNames = [];
    var categoricalColumnNames = [];

    for (var i=0; i < numericalColumns.length; i++) {
        numericalColumnNames.push(variableNames[numericalColumns[i]]);
    }
    for (var j=0; j < categoricalColumns.length; j++) {
        categoricalColumnNames.push(variableNames[categoricalColumns[j]]);
    }

    state.numericalColumnNames = numericalColumnNames;
    state.categoricalColumnNames = categoricalColumnNames;

    // process the data: update statistics
    state.descriptiveStatistics = statistics.descriptiveStatistics(dataAsColumns, numericalColumns, state.options.decimalPlaces);
    state.correlationMatrix = statistics.correlationMatrix(dataAsColumns, numericalColumns, state.options.decimalPlaces);
    state.covarianceMatrix = statistics.covarianceMatrix(dataAsColumns, numericalColumns, state.options.decimalPlaces);

    state.categoryStatsByLevel = statistics.categoryStatsByLevel(dataAsRows, columnTypes, state.options.decimalPlaces);
    state.categoryStatsByVariable = statistics.categoryStatsByVariable(dataAsRows, columnTypes, state.options.decimalPlaces);
    //state.correlationMatrixByLevel = statistics.correlationMatrixByLevel(dataAsRows, categoricalColumns, state.options.decimalPlaces);
    //state.covarianceMatrixByLevel = statistics.covarianceMatrixByLevel(dataAsRows, categoricalColumns, state.options.decimalPlaces);
}

export const updateStatisticsPageOptions = function(state, options) {
    for (var key in state.options.statisticsPage) {
        if (options[key] !== undefined) {
            state.options.statisticsPage[key] = options[key];
        }
    }
}

