<template>
    <div class="boxplot-holder">
        <span v-html="boxplot"></span>
    </div>
</template>

<script>
import box from '@/helpers/graphs/box.js';

export default {
    props: {
        dataColumns: {
            type: Array,
            required: false
        },
        options: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            graphWidth: 600,
            graphHeight: 400
        }
    },
    computed: {
        boxplot: function() {
            if (this.$props.dataColumns[0] !== undefined) {
                return box.boxplot(this.$props.dataColumns, {
                    width: this.$props.options.width,
                    height: this.$props.options.height,
                    includePadding: this.$props.options.includePadding,
                    //includeTooltips: this.$props.options.includeTooltips
                });
            }
        }
    }
}

</script>



