<template>
    <div id="scatterplot-parent" class="main-panel">
        <div class="nav-tabs">
            <ul>
                <li :class="{active: activeTab === 0}" @click="tabNav(0)">Scatterplot</li>
                <li :class="{active: activeTab === 1}" @click="tabNav(1)">Scatterplot grid</li>
            </ul>
        </div>
        <ScatterplotHolder v-if="activeTab === 0">
        </ScatterplotHolder>
        <ScatterplotGrid v-if="activeTab === 1">
        </ScatterplotGrid>
    </div>
</template>

<script>
import ScatterplotHolder from './ScatterplotHolder.vue';
import ScatterplotGrid from './ScatterplotGrid.vue';

export default {
    methods: {
        tabNav: function(tabId) {
            this.activeTab = tabId;
        }
    },
    computed: {
        activeTab: {
            get: function() {
                return this.$store.getters.options.scatterplot.activeTab;
            },
            set: function(newValue) {
                this.$store.dispatch('updateScatterplotOptions', {
                    activeTab: newValue
                });
            }
        }
    },
    components: {
        ScatterplotHolder,
        ScatterplotGrid
    }
}
</script>



