<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>Mean</td>
                <td>
                    <input id="normal-mean" type="number" step="0.0001"
                        v-model.number="options.normal.mean" />
                </td>
            </tr>
            <tr>
                <td>Standard deviation</td>
                <td>
                    <input id="normal-sd" type="number" step="0.0001" min="0"
                        v-model.number="options.normal.sd" />
                </td>
            </tr>
            <tr v-if="!options.cdf.calculateBetween">
                <td>Upper bound (x)</td>
                <td>
                    <input id="x-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.x" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Lower bound</td>
                <td>
                    <input id="left-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.start" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Upper bound</td>
                <td>
                    <input id="right-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.end" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result)">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div v-if="!options.cdf.calculateBetween">
                    The proportion of the given normal distribution that falls below {{ options.cdf.x }}, or P(X &le; {{ options.cdf.x }}), is
                </div>
                <div v-if="options.cdf.calculateBetween">
                    The proportion of the given normal distribution that falls between {{ options.cdf.start }} and {{ options.cdf.end }}, or P({{ options.cdf.start }} &le; X &le; {{ options.cdf.end }}), is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <h3>Graph</h3>
            <div class="answer-holder">
                <span v-html="normalGraph"></span>
            </div>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
import distgraph from '@/helpers/graphs/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            if (this.options.cdf.calculateBetween) {
                if (!distributions.anyMissingParameters([this.options.cdf.start, this.options.cdf.end])) {
                    return distributions.normalCDF(this.options.cdf.end, this.options.normal.mean, this.options.normal.sd) - distributions.normalCDF(this.options.cdf.start, this.options.normal.mean, this.options.normal.sd);
                }
            } else {
                return distributions.normalCDF(this.options.cdf.x, this.options.normal.mean, this.options.normal.sd);
            }
        },
        normalGraph: function() {
            if (this.result !== null) {
                if (this.options.cdf.calculateBetween) {
                    var right = distributions.normalCDF(this.options.cdf.end, this.options.normal.mean, this.options.normal.sd);
                    var left = distributions.normalCDF(this.options.cdf.start, this.options.normal.mean, this.options.normal.sd);
                    return distgraph.normal({
                        include: true,
                        proportion: [left, right],
                        side: 'between'
                    }, {});
                } else {
                    return distgraph.normal({
                        include: true,
                        proportion: this.result,
                        side: 'left'
                    }, {});
                }
            }
        }
    }
}

</script>



