<template>
    <div class="popup-explanation popup-checklist">
        <h1>Select Variables to View</h1>
        <div class="data-btn-holder btn-group">
            <button class="btn" @click="showAllVariables">
                Show All
            </button>
        </div>
        <div class="variable-checklist-holder">
            <div v-for="(column, index) in numericalColumnIndices"
                class="variable-selector">
                <input type='checkbox' :id="'variable-' + index"
                        :value='index'
                        v-model='columnsToShow'>
                <label :for="'variable-' + index">{{ variables[column] }}</label>
            </div>
            <button class="close-btn" @click="closeForm">Done</button>
        </div>
    </div>
</template>

<script>

export default {
    methods: {
        closeForm() {
            this.$emit('close');
        },
        showAllVariables() {
            var show = [];
            for (var i=0; i < this.numericalColumnIndices.length; i++) {
                show.push(i);
            }
            this.columnsToShow = show;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.boxplot;
            },
            set: function(newValue) {
                this.$store.dispatch('updateBoxplotOptions', newValue);
            }
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalColumnIndices: function() {
            return this.$store.getters.numericalColumnIndices;
        },
        columnsToShow: {
            get: function() {
                return this.options.columnsToShow;
            },
            set: function(newValue) {
                this.options = {
                    columnsToShow: newValue
                };
            }
        }
    }
}

</script>



