<template>
    <div style="width: 100%; overflow: auto">
        <table class="stats-table" align="center">
            <tr>
                <th></th>
                <th></th>
                <th v-for="name in variableNames">{{ name }}</th>
            </tr>
            <tr v-for="row in rowsInTable"
                @click="popupExplanation(row)"
                v-if="statisticsVisibility[row.id]" >
                <td>{{ row.name }}</td>
                <td class="symbol">{{ row.symbol }}</td>
                <td v-for="(col, index) in row.value" class="value">{{ col }}</td>
            </tr>
        </table>

        <div class="grayed-out" v-if="activePopupName !== null" @click="closePopup"></div>
        <Mean :activePopup="activePopupName"></Mean>
        <MeanStandardError :activePopup="activePopupName"></MeanStandardError>
        <Median :activePopup="activePopupName"></Median>
        <Mode :activePopup="activePopupName"></Mode>
        <SumOfSquares :activePopup="activePopupName"></SumOfSquares>
        <RangeExp :activePopup="activePopupName"></RangeExp>
        <Variance :activePopup="activePopupName"></Variance>
        <StandardDeviation :activePopup="activePopupName"></StandardDeviation>
        <CoefficientOfVariation :activePopup="activePopupName"></CoefficientOfVariation>
        <Skewness :activePopup="activePopupName"></Skewness>
        <Kurtosis :activePopup="activePopupName"></Kurtosis>
        <FiveNumberSummary :activePopup="activePopupName"></FiveNumberSummary>
    </div>
</template>

<script>

import Mean from '../explanations/Mean.vue';
import MeanStandardError from '../explanations/MeanStandardError.vue';
import Median from '../explanations/Median.vue';
import Mode from '../explanations/Mode.vue';
import SumOfSquares from '../explanations/SumOfSquares.vue';
import RangeExp from '../explanations/RangeExp.vue';
import Variance from '../explanations/Variance.vue';
import StandardDeviation from '../explanations/StandardDeviation.vue';
import CoefficientOfVariation from '../explanations/CoefficientOfVariation.vue';
import Skewness from '../explanations/Skewness.vue';
import Kurtosis from '../explanations/Kurtosis.vue';
import FiveNumberSummary from '../explanations/FiveNumberSummary.vue';

export default {
    props: {
        statistics: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            activePopupName: null
        };
    },
    methods: {
        popupExplanation(row) {
            this.activePopupName = row.popupID;
        },
        closePopup() {
            this.activePopupName = null;
        }
    },
    computed: {
        options: function() {
            return this.$store.getters.options.statisticsPage;
        },
        variableNames: function() {
            var names = [];
            for (var key in this.$props.statistics) {
                if (key !== 'null' && key !== '') {
                    names.push(key);
                } else {
                    names.push('Unclassified');
                }

            }

            return names;
        },
        availableStatistics: function() {
            return this.options.availableStatistics;
        },
        hiddenStatistics: function() {
            return this.options.hiddenStatistics;
        },
        statisticsVisibility: function() {
            var visibility = {};
            for (var i=0; i < this.availableStatistics.length; i++) {
                var stat = this.availableStatistics[i].id;
                if (this.hiddenStatistics.indexOf(stat) < 0) {
                    visibility[stat] = true;
                } else {
                    visibility[stat] = false;
                }
            }
            return visibility;
        },
        rows: function() {
            var n = [];
            var mean = [];
            var meanSE = [];
            var median = [];
            var mode = [];
            var sumOfSquares = [];
            var range = [];
            var varianceSample = [];
            var variancePopulation = [];
            var stdDevSample = [];
            var stdDevPopulation = [];
            var coefficientOfVariationSample = [];
            var coefficientOfVariationPopulation = [];
            var skewness = [];
            var kurtosis = [];
            var min = [];
            var q1 = [];
            var q3 = [];
            var max = [];
            var interquartileRange = [];

            var stats = this.$props.statistics;
            for (var variable in stats) {
                n.push(stats[variable].n);
                mean.push(stats[variable].mean);
                meanSE.push(stats[variable].meanSE);
                median.push(stats[variable].median);
                mode.push(stats[variable].mode);
                sumOfSquares.push(stats[variable].sumOfSquares);
                range.push(stats[variable].range);
                varianceSample.push(stats[variable].variance.sample);
                variancePopulation.push(stats[variable].variance.population);
                stdDevSample.push(stats[variable].stdDev.sample);
                stdDevPopulation.push(stats[variable].stdDev.population);
                coefficientOfVariationSample.push(stats[variable].coefficientOfVariation.sample);
                coefficientOfVariationPopulation.push(stats[variable].coefficientOfVariation.population);
                skewness.push(stats[variable].skewness);
                kurtosis.push(stats[variable].kurtosis);
                min.push(stats[variable].fiveNumberSummary.min);
                q1.push(stats[variable].fiveNumberSummary.q1);
                q3.push(stats[variable].fiveNumberSummary.q3);
                max.push(stats[variable].fiveNumberSummary.max);
                interquartileRange.push(stats[variable].interquartileRange);
            }

            return {
                n: n,
                mean: mean,
                meanSE: meanSE,
                median: median,
                mode: mode,
                sumOfSquares: sumOfSquares,
                range: range,
                varianceSample: varianceSample,
                variancePopulation: variancePopulation,
                stdDevSample: stdDevSample,
                stdDevPopulation: stdDevPopulation,
                coefficientOfVariationSample: coefficientOfVariationSample,
                coefficientOfVariationPopulation: coefficientOfVariationPopulation,
                skewness: skewness,
                kurtosis: kurtosis,
                min: min,
                q1: q1,
                q3: q3,
                max: max,
                interquartileRange: interquartileRange
            }
        },
        rowsInTable: function() {
            return [
                {
                    id: 'n',
                    symbol: '\\(n\\)',
                    name: 'Number of observations',
                    value: this.rows.n,
                    popupID: null
                },
                {
                    id: 'mean',
                    symbol: '\\(\\overline{x}\\) or \\(\\mu\\)',
                    name: 'Mean',
                    value: this.rows.mean,
                    popupID: 'mean'
                },
                {
                    id: 'meanSE',
                    symbol: '\\(s_{\\overline{x}}\\)',
                    name: 'Standard Error of Mean',
                    value: this.rows.meanSE,
                    popupID: 'meanSE'
                },
                {
                    id: 'median',
                    symbol: '\\(Q_2\\)',
                    name: 'Median',
                    value: this.rows.median,
                    popupID: 'median'
                },
                {
                    id: 'mode',
                    symbol: '',
                    name: 'Mode(s)',
                    value: this.rows.mode,
                    popupID: 'mode'
                },
                {
                    id: 'sumOfSquares',
                    symbol: 'SS',
                    name: 'Sum of Squares',
                    value: this.rows.sumOfSquares,
                    popupID: 'sumOfSquares'
                },
                {
                    id: 'range',
                    symbol: '',
                    name: 'Range',
                    value: this.rows.range,
                    popupID: 'range'
                },
                {
                    id: 'variance',
                    symbol: '\\(s^2\\)',
                    name: 'Sample Variance',
                    value: this.rows.varianceSample,
                    popupID: 'sample-variance'
                },
                {
                    id: 'variance',
                    symbol: '\\(\\sigma^2\\)',
                    name: 'Population Variance',
                    value: this.rows.variancePopulation,
                    popupID: 'population-variance'
                },
                {
                    id: 'stdDev',
                    symbol: '\\(s\\)',
                    name: 'Sample Standard Deviation',
                    value: this.rows.stdDevSample,
                    popupID: 'sample-standard-deviation'
                },
                {
                    id: 'stdDev',
                    symbol: '\\(\\sigma\\)',
                    name: 'Population Standard Deviation',
                    value: this.rows.stdDevPopulation,
                    popupID: 'population-standard-deviation'
                },
                {
                    id: 'coefficientOfVariationSample',
                    symbol: 'CV',
                    name: 'Sample Coefficient of Variation',
                    value: this.rows.coefficientOfVariationSample,
                    popupID: 'coefficientOfVariationSample'
                },
                {
                    id: 'coefficientOfVariationPopulation',
                    symbol: 'CV',
                    name: 'Population Coefficient of Variation',
                    value: this.rows.coefficientOfVariationPopulation,
                    popupID: 'coefficientOfVariationPopulation'
                },
                {
                    id: 'skewness',
                    symbol: '\\(\\gamma_1\\)',
                    name: 'Skewness',
                    value: this.rows.skewness,
                    popupID: 'skewness'
                },
                {
                    id: 'kurtosis',
                    symbol: '\\(\\beta_2\\)',
                    name: 'Kurtosis',
                    value: this.rows.kurtosis,
                    popupID: 'kurtosis'
                },
                {
                    id: 'fiveNumberSummary',
                    symbol: '',
                    name: 'Minimum',
                    value: this.rows.min,
                    popupID: 'min'
                },
                {
                    id: 'fiveNumberSummary',
                    symbol: '\\(Q_1\\)',
                    name: 'First Quartile',
                    value: this.rows.q1,
                    popupID: 'q1'
                },
                {
                    id: 'fiveNumberSummary',
                    symbol: '\\(Q_2\\)',
                    name: 'Median',
                    value: this.rows.median,
                    popupID: 'median-five'
                },
                {
                    id: 'fiveNumberSummary',
                    symbol: '\\(Q_3\\)',
                    name: 'Third Quartile',
                    value: this.rows.q3,
                    popupID: 'q3'
                },
                {
                    id: 'fiveNumberSummary',
                    symbol: '',
                    name: 'Maximum',
                    value: this.rows.max,
                    popupID: 'max'
                },
                {
                    id: 'interquartileRange',
                    symbol: 'IQR',
                    name: 'Interquartile Range',
                    value: this.rows.interquartileRange,
                    popupID: 'interquartileRange'
                }
            ]
        }
    },
    components: {
        Mean,
        MeanStandardError,
        Median,
        Mode,
        SumOfSquares,
        RangeExp,
        Variance,
        StandardDeviation,
        CoefficientOfVariation,
        Skewness,
        Kurtosis,
        FiveNumberSummary
    }
}

</script>



