<template>
    <div class="panel-tab" style="width: 100%; overflow: auto">
        <div class="panel-block">

            <span class="panel-block-title">Correlation Matrix</span>

            <table class="correlation-table" @click="popupExplanation('correlationCoefficient')">
                <tr>
                    <th class="column-name-top column-name-left">\(r\)</th>
                    <th v-for="name in numericalVariables" class="column-name-top">
                        {{ name }}
                    </th>
                </tr>
                <tr v-for="(name, i) in numericalVariables">
                    <th class="column-name-left">{{ name }}</th>
                    <td v-for="(name, j) in numericalVariables" class="correlation-value">
                        {{ correlationMatrix[i][j] }}
                    </td>
                </tr>
            </table>
            <div v-if="anyNullCorrelations">
                Why are some of the table entries empty?
            </div>

            <span class="panel-block-title">Covariance Matrix</span>

            <table class="correlation-table" @click="popupExplanation('covariance')">
                <tr>
                    <th class="column-name-top column-name-left">\(r\)</th>
                    <th v-for="name in numericalVariables" class="column-name-top">
                        {{ name }}
                    </th>
                </tr>
                <tr v-for="(name, i) in numericalVariables">
                    <th class="column-name-left">{{ name }}</th>
                    <td v-for="(name, j) in numericalVariables" class="correlation-value">
                        {{ covarianceMatrix[i][j] }}
                    </td>
                </tr>
            </table>

            <div class="grayed-out" v-if="activePopupName !== null" @click="closePopup"></div>
            <CorrelationCoefficient :activePopup="activePopupName">
            </CorrelationCoefficient>
            <Covariance :activePopup="activePopupName">
            </Covariance>
        </div>
    </div>
</template>

<script>
import CorrelationCoefficient from '../explanations/CorrelationCoefficient.vue';
import Covariance from '../explanations/Covariance.vue';

export default {
    mounted() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    },
    data: function() {
        return {
            activePopupName: null
        };
    },
    computed: {
        correlation: function() {
            return this.$store.getters.correlationMatrix;
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalVariables: function() {
            return this.$store.getters.numericalColumnNames;
        },
        correlationMatrix: function() {
            return this.$store.getters.correlationMatrix;
        },
        covarianceMatrix: function() {
            return this.$store.getters.covarianceMatrix;
        },
        anyNullCorrelations: function() {
            for (var i=0; i<this.correlationMatrix.length; i++) {
                for (var k=0; k<this.correlationMatrix[i].length; k++) {
                    if (this.correlationMatrix[i][k] === null) {
                        return true;
                    }
                }
            }
            return false;
        }
    },
    methods: {
        popupExplanation(explanationToOpen) {
            this.activePopupName = explanationToOpen;
        },
        closePopup() {
            this.activePopupName = null;
        }
    },
    components: {
        CorrelationCoefficient,
        Covariance
    }
}

</script>



