<template>
    <div class="barchart-holder">
        <span v-html="barChart"></span>
    </div>
</template>

<script>
import categoricalGraphs from '@/helpers/graphs/categorical.js';

export default {
    props: {
        categoricalColumn: {
            type: Array,
            required: false
        },
        options: {
            type: Object,
            required: true
        }
    },
    computed: {
        barChart: function() {
            if (this.$props.categoricalColumn !== undefined) {
                return categoricalGraphs.barChart(this.$props.categoricalColumn, {
                    orderByProportion: this.$props.options.orderByProportion,
                    includeCumulativeLine: this.$props.options.includeCumulativeLine
                });
            }

        }
    }
}

</script>



