//import axios from 'axios'

export const addDataColumn = function(context) {
    context.commit('addDataColumn');
}

export const updateData = function(context, newData) {
    context.commit('updateData', newData);
}

export const clearData = function(context) {
    context.commit('clearData');
    context.commit('updateStats');
}

export const makeColumnCategorical = function(context, columnIndex) {
    context.commit('makeColumnCategorical', columnIndex);
}

export const makeColumnAutomaticType = function(context, columnIndex) {
    context.commit('makeColumnAutomaticType', columnIndex);
}

export const updateStats = function(context) {
    context.commit('updateStats');
}

export const updateStatisticsPageOptions = function(context, options) {
    context.commit('updateStatisticsPageOptions', options);
}

export const resetStatisticsOptions = function(context) {
    context.commit('updateStatisticsOptions', {activeTab: 0});
}

export const updateCategoricalStatsOptions = function(context, options) {
    context.commit('updateCategoricalStatsOptions', options);
}

export const updateScatterplotOptions = function(context, options) {
    context.commit('updateScatterplotOptions', options);
}

export const updateHistogramOptions = function(context, options) {
    context.commit('updateHistogramOptions', options);
}

export const updateStemAndLeafOptions = function(context, options) {
    context.commit('updateStemAndLeafOptions', options);
}

export const updateBoxplotOptions = function(context, options) {
    context.commit('updateBoxplotOptions', options);
}

export const updateBarchartOptions = function(context, options) {
    context.commit('updateBarchartOptions', options);
}

export const updatePiechartOptions = function(context, options) {
    context.commit('updatePiechartOptions', options);
}

export const updateMeanTestOptions = function(context, options) {
    context.commit('updateMeanTestOptions', options);
}

export const updateProportionTestOptions = function(context, options) {
    context.commit('updateProportionTestOptions', options);
}

export const updateGOTTestOptions = function(context, options) {
    context.commit('updateGOTTestOptions', options);
}

export const updateDistributionOptions = function(context, options) {
    context.commit('updateDistributionOptions', options);
}

export const updateTestOptions = function(context, options) {
    context.commit('updateTestOptions', options);
}

export const updateTestResults = function(context, results) {
    context.commit('updateTestResults', results);
}



