<template>
    <div class="distribution cdf">
        <h2>Cumulative Density Function</h2>

        <h3>
            <select class="pick-distribution" v-model="options.cdf.distribution" v-on:change="clearX">
                <option v-for="distribution in availableDistributions"
                    v-bind:value="distribution">
                    {{ distribution }}
                </option>
            </select>
            Distribution
        </h3>

        <div class="organization-toggle-holder">
        <div class="organization-toggle">
            <table align="center">
                <tr>
                    <td>
                        <span class="organization-choice">
                            Upper bound only
                        </span>
                    </td>
                    <td>
                        <label class="switch">
                            <input type="checkbox" v-model="options.cdf.calculateBetween" />
                            <span class="slider"></span>
                        </label>
                    </td>
                    <td>
                        <span class="organization-choice">
                            Lower and upper bounds
                        </span>
                    </td>
                </tr>
            </table>
        </div>
        </div>

        <!--Select a distribution:
        <select v-model="options.cdf.distribution" v-on:change="clearX">
            <option v-for="distribution in availableDistributions"
                v-bind:value="distribution">
                {{ distribution }}
            </option>
        </select><br /><br />-->

        <div v-if="options.cdf.distribution !== ''">
            <NormalCDF v-if="options.cdf.distribution === 'Normal'">
            </NormalCDF>

            <tCDF v-if="options.cdf.distribution === 'Student\'s t'">
            </tCDF>

            <ChiSquareCDF v-if="options.cdf.distribution === 'Chi-square'">
            </ChiSquareCDF>

            <fCDF v-if="options.cdf.distribution === 'F'">
            </fCDF>

            <BinomialCDF v-if="options.cdf.distribution === 'Binomial'">
            </BinomialCDF>

            <PoissonCDF v-if="options.cdf.distribution === 'Poisson'">
            </PoissonCDF>

            <GeometricCDF v-if="options.cdf.distribution === 'Geometric'">
            </GeometricCDF>
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
import distgraph from '@/helpers/graphs/distributions.js';

import NormalCDF from './cdfs/NormalCDF.vue';
import tCDF from './cdfs/tCDF.vue';
import ChiSquareCDF from './cdfs/ChiSquareCDF.vue';
import fCDF from './cdfs/fCDF.vue';
import BinomialCDF from './cdfs/BinomialCDF.vue';
import PoissonCDF from './cdfs/PoissonCDF.vue';
import GeometricCDF from './cdfs/GeometricCDF.vue';

export default {
    data: function() {
        return {
            availableDistributions: [
                'Normal',
                'Student\'s t',
                'Chi-square',
                'F',
                'Binomial',
                'Poisson',
                'Geometric'
            ]
        }
    },
    methods: {
        clearX: function() {
            this.options.cdf.x = null;
            this.options.cdf.start = null;
            this.options.cdf.end = null;
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
    },
    components: {
        NormalCDF,
        tCDF,
        ChiSquareCDF,
        fCDF,
        BinomialCDF,
        PoissonCDF,
        GeometricCDF
    }
}

</script>



