<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>
                    Expected number of occurrences in one period,
                    <span v-html="lambda"></span>
                </td>
                <td>
                    <input id="poisson-lambda" type="number" step="0.0001"
                        v-model.number="options.poisson.lambda" />
                </td>
            </tr>
            <tr v-if="!options.cdf.calculateBetween">
                <td>Upper bound on number of occurrences</td>
                <td>
                    <input id="x-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.x" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Lower bound on number of occurrences</td>
                <td>
                    <input id="left-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.start" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Upper bound on number of occurrences</td>
                <td>
                    <input id="right-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.end" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result) && showResults">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div v-if="!options.cdf.calculateBetween">
                    If {{ options.poisson.lambda }}
                    <span v-if="options.poisson.lambda === 1">
                        occurrence is expected
                    </span>
                    <span v-if="options.poisson.lambda !== 1">
                        occurrences are expected
                    </span>
                    in each period, the probability of at most {{ options.cdf.x }} occurrence<span v-if="options.cdf.x !== 1">s</span> in one period is
                </div>
                <div v-if="options.cdf.calculateBetween">
                    If {{ options.poisson.lambda }}
                    <span v-if="options.poisson.lambda === 1">
                        occurrence is expected
                    </span>
                    <span v-if="options.poisson.lambda !== 1">
                        occurrences are expected
                    </span>
                    in each period, the probability of more than {{ options.cdf.start }} occurrence<span v-if="options.cdf.start !== 1">s</span> <strong>(not inclusive)</strong> and at most {{ options.cdf.end }} occurrence<span v-if="options.cdf.end !== 1">s</span> <strong>(inclusive)</strong> in one period is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <table align="center" class="results-table">
                <tr>
                    <th>Occurrences</th>
                    <th>Probability</th>
                </tr>
                <tr v-for="(probability, index) in allResults">
                    <td>{{ index }}</td>
                    <td>{{ probability.toPrecision(5) }}</td>
                </tr>
                <tr style="border-top: 1px solid black">
                    <td>Total</td>
                    <td>{{ sumOfProbabilities.toPrecision(5) }}</td>
                </tr>
            </table>

            <!--<h3>Graph</h3>
            <div class="answer-holder">
                <span v-html="normalGraph"></span>
            </div>-->
        </div>

        <div v-if="invalidX" class="error-message">
            The number of occurrences must be an integer greater than or equal to 0.
        </div>

    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
//import distgraph from '@/helpers/graphs/distributions.js';

export default {
    data: function() {
        return {
            lambda: '\\(\\lambda\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            if (this.options.cdf.calculateBetween) {
                if (!distributions.anyMissingParameters([this.options.cdf.start, this.options.cdf.end])) {
                    return distributions.poissonCDF(this.options.cdf.end, this.options.poisson.lambda) - distributions.poissonCDF(this.options.cdf.start, this.options.poisson.lambda);
                }
            } else {
                return distributions.poissonCDF(this.options.cdf.x, this.options.poisson.lambda);
            }
        },
        allResults: function() {
            var probabilities = {};

            if (this.options.cdf.calculateBetween) {
                for (var i = this.options.cdf.start + 1; i <= this.options.cdf.end; i++) {
                    probabilities[i] = distributions.poissonPDF(i, this.options.poisson.lambda);
                }
            } else {
                for (var i=0; i <= this.options.cdf.x; i++) {
                    probabilities[i] = distributions.poissonPDF(i, this.options.poisson.lambda);
                }
            }

            return probabilities;
        },
        sumOfProbabilities: function() {
            var sum = 0;
            for (var index in this.allResults) {
                sum += this.allResults[index];
            }
            return sum;
        },
        invalidX: function() {
            if (this.options.cdf.calculateBetween) {
                if (!Number.isInteger(this.options.cdf.start) || !Number.isInteger(this.options.cdf.end)) {
                    return true;
                }
                if (this.options.cdf.start < 0 || this.options.cdf.end < 0) {
                    return true;
                }
            } else {
                if (!Number.isInteger(this.options.cdf.x)) {
                    return true;
                }
                if (this.options.cdf.x < 0) {
                    return true;
                }
            }

            return false;
        },
        showResults: function() {
            if (this.invalidX) {
                return false;
            }
            return true;
        }
        /*normalGraph: function() {
            if (this.result !== null) {
                return distgraph.normal({
                    include: true,
                    proportion: this.result,
                    side: 'left'
                }, {});
            }
        }*/
    },
    mounted: function() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    }
}

</script>



