import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
//import Table from '@/components/Table'
import Statistics from '@/components/Statistics'
import Data from '@/components/Data'
import Graphs from '@/components/Graphs'
import ScatterplotPage from '@/components/graphs/scatterplot/ScatterplotPage'
import HistogramPage from '@/components/graphs/histogram/HistogramPage'
import StemAndLeafPage from '@/components/graphs/stemAndLeaf/StemAndLeafPage'
import BarchartPage from '@/components/graphs/barchart/BarchartPage'
import PiechartPage from '@/components/graphs/piechart/PiechartPage'
import BoxplotPage from '@/components/graphs/boxplot/BoxplotPage'
import Tests from '@/components/Tests'
import TestResults from '@/components/tests/TestResults'
import Analysis from '@/components/Analysis'
import HypothesisTestMean from '@/components/analysis/HypothesisTestMean'
import HypothesisTestProportion from '@/components/analysis/HypothesisTestProportion'
import GOFTest from '@/components/analysis/GOFTest'
import Distributions from '@/components/Distributions'
import Calculations from '@/components/Calculations'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/data'
  },
  {
    path: '/data',
    name: 'Data',
    component: Data,
    meta: {
        displayName: 'Data',
        breadcrumbs: 'Data'
    }
  },
  {
    path: '/statistics',
    name: 'Statistics',
    component: Statistics,
    meta: {
        displayName: 'Statistics',
        breadcrumbs: 'Statistics'
    }
  },
  {
    path: '/graphs',
    name: 'Graphs',
    component: Graphs,
    meta: {
        displayName: 'Graphs',
        breadcrumbs: 'Graphs'
    }
  },
  {
    path: '/graphs/scatterplot',
    name: 'Scatterplot',
    component: ScatterplotPage,
    meta: {
        displayName: 'Graphs: Scatterplot',
        breadcrumbs: 'Graphs / Scatterplot',
        parentName: 'Graphs'
    }
  },
  {
    path: '/graphs/histogram',
    name: 'Histogram',
    component: HistogramPage,
    meta: {
        displayName: 'Graphs: Histogram',
        breadcrumbs: 'Graphs / Histogram',
        parentName: 'Graphs'
    }
  },
  {
    path: '/graphs/stem-and-leaf',
    name: 'StemAndLeaf',
    component: StemAndLeafPage,
    meta: {
        displayName: 'Graphs: Stem-and-Leaf Plot',
        breadcrumbs: 'Graphs / Stem-and-Leaf Plot',
        parentName: 'Graphs'
    }
  },
  {
    path: '/graphs/boxplot',
    name: 'Boxplot',
    component: BoxplotPage,
    meta: {
        displayName: 'Graphs: Boxplot',
        breadcrumbs: 'Graphs / Boxplot',
        parentName: 'Graphs'
    }
  },
  {
    path: '/graphs/barchart',
    name: 'Barchart',
    component: BarchartPage,
    meta: {
        displayName: 'Graphs: Bar Chart',
        breadcrumbs: 'Graphs / Bar Chart',
        parentName: 'Graphs'
    }
  },
  {
    path: '/graphs/piechart',
    name: 'Piechart',
    component: PiechartPage,
    meta: {
        displayName: 'Graphs: Pie Chart',
        breadcrumbs: 'Graphs / Pie Chart',
        parentName: 'Graphs'
    }
  },
  {
    path: '/analysis',
    name: 'Analysis',
    component: Analysis,
    meta: {
        displayName: 'Analysis',
        breadcrumbs: 'Analysis'
    }
  },
  {
    path: '/analysis/hypothesis-testing-mean',
    name: 'HypothesisTestMean',
    component: HypothesisTestMean,
    meta: {
        displayName: 'Hypothesis Testing for Means',
        breadcrumbs: 'Analysis / Hypothesis Testing',
        parentName: 'Analysis'
    }
  },
  {
    path: '/analysis/hypothesis-testing-proportion',
    name: 'HypothesisTestProportion',
    component: HypothesisTestProportion,
    meta: {
        displayName: 'Hypothesis Testing for Proportions',
        breadcrumbs: 'Analysis / Hypothesis Testing',
        parentName: 'Analysis'
    }
  },
  {
    path: '/analysis/goodness-of-fit',
    name: 'GoodnessOfFitTest',
    component: GOFTest,
    meta: {
        displayName: 'Goodness-of-fit Test',
        breadcrumbs: 'Analysis / GOF Testing',
        parentName: 'Analysis'
    }
  },
  {
    path: '/tests',
    name: 'Tests',
    component: Tests,
    meta: {
        displayName: 'Tests',
        breadcrumbs: 'Tests'
    }
  },
  {
    path: '/tests/results',
    name: 'TestResults',
    component: TestResults,
    meta: {
        displayName: 'Test Results',
        breadcrumbs: 'Tests / Results',
        parentName: 'Tests'
    }
  },
  {
    path: '/distributions',
    name: 'Distributions',
    component: Distributions,
    meta: {
        displayName: 'Distributions',
        breadcrumbs: 'Distributions'
    }
},
  {
    path: '/calculations',
    name: 'Calculations',
    component: Calculations,
    meta: {
        displayName: 'Calculations',
        breadcrumbs: 'Calculations'
    }
  }
  ]
})



