<template>
    <div id="sl-parent" class="main-panel">
        <h1>Stem-and-Leaf Plot</h1>

        <select v-model="column">
            <option v-for="(column, index) in numericalData" v-bind:value="index">
                {{ column[0] }}
            </option>
        </select>

        Units for leaves: 10^ <input v-model.number="options.powerOfTen" type="number" />

        <StemAndLeaf :dataColumn="dataForPlot"
            :options="options">
        </StemAndLeaf>
    </div>
</template>

<script>
import StemAndLeaf from './StemAndLeaf.vue';
import $ from 'jquery';
import data from '@/helpers/data.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.stemAndLeaf;
            },
            set: function(newValue) {
                this.$store.dispatch('updateStemAndLeafOptions', newValue);
            }
        },
        column: {
            get: function() {
                return this.options.column;
            },
            set: function(newValue) {
                this.options = {
                    column: newValue
                }
            }
        },
        /*splitBasedOnCategory: {
            get: function() {
                return this.options.splitBasedOnCategory;
            },
            set: function(newValue) {
                this.options = {
                    splitBasedOnCategory: newValue
                }
            }
        },
        categoricalIndexForSplitting: {
            get: function() {
                return this.options.categoricalIndexForSplitting;
            },
            set: function(newValue) {
                this.options = {
                    categoricalIndexForSplitting: newValue
                }
            }
        },
        dataAsRows: function() {
            return this.$store.getters.dataAsRows;
        },
        variables: function() {
            return this.$store.getters.variables;
        },*/
        numericalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var numericalColumnIndices = this.$store.getters.numericalColumnIndices;
            var numericalColumns = [];

            for (var i=0; i < numericalColumnIndices.length; i++) {
                numericalColumns.push(allDataColumns[numericalColumnIndices[i]]);
            }

            return numericalColumns;
        },
        /*categoricalColumnIndices: function() {
            return this.$store.getters.categoricalColumnIndices;
        },*/
        dataForPlot: function() {
            return this.numericalData[this.column];
        },
        /*dataSplitByCategoryLevel: function() {
            return data.categorizeDataByLevel(this.dataAsRows, this.categoricalColumnIndices);
        },
        dataForChosenCategory: function() {
            var resultAsRows = this.dataSplitByCategoryLevel[this.categoricalColumnIndices[this.categoricalIndexForSplitting]];

            var resultAsColumns = {};
            for (var level in resultAsRows) {
                resultAsColumns[level] = data.transpose(resultAsRows[level]);
            }

            return resultAsColumns;
        }*/
    },
    components: {
        StemAndLeaf
    }
}

</script>



