<template>
    <div class="histogram-holder">
        <span v-html="histogram"></span>
    </div>
</template>

<script>
import barplots from '@/helpers/graphs/barplots.js';

export default {
    props: {
        dataColumn: {
            type: Array,
            required: false
        },
        options: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            graphWidth: 600,
            graphHeight: 400
        }
    },
    computed: {
        histogram: function() {
            if (this.$props.dataColumn !== undefined) {
                return barplots.histogram(this.$props.dataColumn, {
                    numBins: this.$props.options.numBins || 1,
                    barGrouping: true,
                    width: this.$props.options.width,
                    height: this.$props.options.height,
                    includePadding: this.$props.options.includePadding,
                    includeTitles: this.$props.options.includeTitles,
                    showXTicks: this.$props.options.showXTicks,
                    showYTicks: this.$props.options.showYTicks,
                    includeMinorXTicks: this.$props.options.includeMinorXTicks,
                    includeMinorYTicks: this.$props.options.includeMinorYTicks,
                    includeTooltips: this.$props.options.includeTooltips
                });
            }
        }
    },
    /*mounted: function() {
        var parentWidth = document.getElementById('histogram-parent').offsetWidth;
        this.graphWidth = parentWidth * 0.8;
        this.graphHeight = Math.floor(this.graphWidth * 2 / 3);
    }*/
}

</script>



