<template>
    <div class="main-panel">
        <h1>Circle Chart</h1>
        Select variable: <select v-model="options.column">
            <option v-for="(catColumn, index) in categoricalData" v-bind:value="index">
                {{ catColumn[0] }}
            </option>
        </select><!--<select class="custom-select" v-model="activeCategoricalIndex">
            <option class="custom-select-option" v-for="(variable, index) in categoricalVariables" v-bind:value="index">
                {{ categoricalVariables[index] }}
            </option>
        </select>--><br /><br />

        <div class="organization-toggle-holder">
        <div class="organization-toggle">
            <span class="organization-choice">Pie Chart</span>
            <label class="switch">
                <input type="checkbox" v-model="isDonut" />
                <span class="slider"></span>
            </label>
            <span class="organization-choice">Donut Chart</span>
        </div>
        </div>

        <div class="piechart-holder" v-if="categoricalColumnIndices.length > 0">
            <!--<Piechart :categoricalColumn="dataAsColumns[activeCategoricalIndex]"
                :width="dimensions.width" :height="dimensions.height" :radius="dimensions.radius"
                :isDonut="isDonut">
            </Piechart>-->
            <Piechart :categoricalColumn="dataForPlot"
                :width="dimensions.width" :height="dimensions.height" :radius="dimensions.radius"
                :isDonut="isDonut">
            </Piechart>
        </div>
    </div>
</template>

<script>
import Piechart from './Piechart.vue';

export default {
    computed: {
        options: function() {
            return this.$store.getters.options.piechart;
        },
        isDonut: {
            get: function() {
                return this.options.isDonut;
            },
            set: function(newValue) {
                this.$store.dispatch('updatePiechartOptions', {
                    isDonut: newValue
                });
            }
        },
        dimensions: function() {
            return {
                width: this.options.width,
                height: this.options.height,
                radius: this.options.radius
            }
        },
        categoricalColumnIndices: function() {
            return this.$store.getters.categoricalColumnIndices;
        },
        categoricalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var categoricalColumnIndices = this.categoricalColumnIndices;
            var categoricalColumns = [];

            for (var i=0; i < categoricalColumnIndices.length; i++) {
                categoricalColumns.push(allDataColumns[categoricalColumnIndices[i]]);
            }

            return categoricalColumns;
        },
        dataForPlot: function() {
            return this.categoricalData[this.options.column];
        }
        /*dataAsColumns: function() {
            return this.$store.getters.dataAsColumns;
        },
        categoricalVariables: function() {
            return this.$store.getters.categoricalColumnNames;
        },
        activeCategoricalIndex: {
            get: function() {
                return this.categoricalColumnIndices[this.options.column];
            },
            set: function(newValue) {
                this.$store.dispatch('updatePiechartOptions', {
                    column: newValue
                });
            }
        }*/
    },
    components: {
        Piechart
    }
}

</script>



