<template>
    <div class="panel-tab">
        <!--{{ testOptions }}-->
        <h3>Step 1: Hypothesis</h3>
        <div>
            <span v-if="testOptions.comparing === 0">Mean 1</span>
            <span v-if="testOptions"
        </div>

        <h3>Step 2: Test Statistic</h3>
        <div>

        </div>

        <h3>Step 3: p-Value</h3>
        <div>

        </div>

        <h3>Step 4: Conclusion</h3>
        <div>

        </div>

        <span v-html="CIgraph"></span>
    </div>
</template>

<script>

import tests from '@/helpers/tests.js';
import graphs from '@/helpers/graphs.js';

export default {
    computed: {
        testOptions: function() {
            return this.$store.getters.options.test;
        },
        testDisplay: function() {
            return {

            }
        },
        testResults: {
            get: function() {
                return this.$store.getters.testResults;
            },
            set: function(newResults) {
                this.$store.dispatch('updateTestResults', {
                    testStatistic: {
                        type: newResults.testStatistic.type,
                        value: newResults.testStatistic.value
                    },
                    p: newResults.p,
                    se: newResults.se,
                    ci: newResults.ci,
                    comparisonValue: newResults.comparisonValue
                });
            }
        },
        CIgraph: function() {
            //return "";
            return graphs.CIgraph(this.testResults.ci, this.testResults.comparisonValue, "Mean", {
                strokeWidth: 3,
                width: 500,
                height: 150
            });
        }
    },
    mounted: function() {
        var results = tests.testResults(null, {
            value: 9.4,
            mean: 8,
            sd: 5,
            sides: 1,
            n: 25,
            confidenceLevel: 0.95
        }, null);

        this.testResults = results;

        //console.log("Test Results:");
        //console.log(this.testResults.z);
    }
}

</script>



