<template>
    <div class="scatterplot-holder">
        <span v-html="scatterplot"></span>
    </div>
</template>

<script>
import dotplots from '@/helpers/graphs/dotplots.js';

export default {
    props: {
        dataColumns: {
            type: Array,
            required: true
        },
        options: {
            type: Object,
            required: true
        }
    },
    computed: {
        scatterplot: function() {
            if (this.$props.dataColumns.length > 1 && this.$props.dataColumns[0]) {
                return dotplots.scatterplot(this.$props.dataColumns, {
                    showAxisTitles: this.$props.options.includeTitles,
                    height: this.$props.options.height,
                    width: this.$props.options.width,
                    includePadding: this.$props.options.includePadding,
                    includeRegressionLine: this.$props.options.includeRegressionLine,
                    showXTicks: this.$props.options.showXTicks,
                    showYTicks: this.$props.options.showYTicks,
                    includeMinorXTicks: this.$props.options.includeMinorXTicks,
                    includeMinorYTicks: this.$props.options.includeMinorYTicks,
                    dotSize: this.$props.options.dotSize,
                    includeTooltips: this.$props.options.includeTooltips,
                    splitBasedOnCategory: this.$props.options.splitBasedOnCategory,
                    categoricalColumnForSplitting: this.$props.options.categoricalColumnForSplitting
                });
            }


        }
    }
}
</script>



