<template>
    <div id="histogram-parent" class="main-panel">
        <h1>Histogram</h1>
        View histogram for
        <select v-model="column" v-on:change="addPointTooltips">
            <option v-for="(column, index) in numericalData" v-bind:value="index">
                {{ column[0] }}
            </option>
        </select>
        using
        <input v-model.number="options.numBins" type="number" min="1" v-on:change="addPointTooltips" />
        bins

        <form class="right-aligned-form">
            <input type="checkbox" id="check-splitbycategory"
                v-model="splitBasedOnCategory"
                @click="addPointTooltips" />
            <label for="check-splitbycategory">Separate Based on Category<span v-if="splitBasedOnCategory">: <select v-model="categoricalIndexForSplitting"
                v-on:change="addPointTooltips">
                <option v-for="(column, index) in categoricalColumnIndices" v-bind:value="index">
                    {{ variables[column] }}
                </option>
            </select>
            </span>
            </label>
        </form>

        <Histogram v-if="!splitBasedOnCategory" :dataColumn="dataForHistogram"
            :options="options">
        </Histogram>

        <!--<div v-if="splitBasedOnCategory"
            v-for="(subset, level) in dataForChosenCategory"
            :key="level">
            {{ subset }}
        </div>-->

        <div v-if="splitBasedOnCategory"
            v-for="(subset, level) in dataForChosenCategory"
            :key="level">
            <h2>{{ level }}</h2>
            <Histogram
                :dataColumn="subset[column]"
                :options="options">
            </Histogram>
        </div>
    </div>
</template>

<script>
import Histogram from './Histogram.vue';
import $ from 'jquery';
import data from '@/helpers/data.js';

export default {
    computed: {
        column: {
            get: function() {
                return this.options.column;
            },
            set: function(newValue) {
                this.$store.dispatch('updateHistogramOptions', {
                    column: newValue
                });
            }
        },
        options: {
            get: function() {
                return this.$store.getters.options.histogram;
            },
            set: function(newValue) {
                this.$store.dispatch('updateHistogramOptions', newValue);
            }
        },
        splitBasedOnCategory: {
            get: function() {
                return this.options.splitBasedOnCategory;
            },
            set: function(newValue) {
                this.options = {
                    splitBasedOnCategory: newValue
                }
            }
        },
        categoricalIndexForSplitting: {
            get: function() {
                return this.options.categoricalIndexForSplitting;
            },
            set: function(newValue) {
                this.options = {
                    categoricalIndexForSplitting: newValue
                }
            }
        },
        dataAsRows: function() {
            return this.$store.getters.dataAsRows;
        },
        variables: function() {
            return this.$store.getters.variables;
        },
        numericalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var numericalColumnIndices = this.$store.getters.numericalColumnIndices;
            var numericalColumns = [];

            for (var i=0; i < numericalColumnIndices.length; i++) {
                numericalColumns.push(allDataColumns[numericalColumnIndices[i]]);
            }

            return numericalColumns;
        },
        categoricalColumnIndices: function() {
            return this.$store.getters.categoricalColumnIndices;
        },
        dataForHistogram: function() {
            return this.numericalData[this.column];
        },
        dataSplitByCategoryLevel: function() {
            return data.categorizeDataByLevel(this.dataAsRows, this.categoricalColumnIndices);
        },
        dataForChosenCategory: function() {
            var resultAsRows = this.dataSplitByCategoryLevel[this.categoricalColumnIndices[this.categoricalIndexForSplitting]];

            var resultAsColumns = {};
            for (var level in resultAsRows) {
                resultAsColumns[level] = data.transpose(resultAsRows[level]);
            }

            return resultAsColumns;
        }
    },
    methods: {
        addPointTooltips: function() {

            //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            this.$nextTick(function() {
                $(function() {
                    $(".histBar").hover(
                        function() {
                            var id = $(this).attr('id').split('-');
                            var x = id[1];
                            var y = id[2];
                            var tooltip = $("#histTooltip-" + x + "-" + y);
                            tooltip.removeClass("hiddenTooltip");
                        },
                        function() {
                            var id = $(this).attr('id').split('-');
                            var x = id[1];
                            var y = id[2];
                            var tooltip = $("#histTooltip-" + x + "-" + y);
                            tooltip.addClass("hiddenTooltip");
                        }
                    );
                });
                //MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        }
    },
    mounted: function() {
        this.addPointTooltips();

        var parentWidth = document.getElementById('histogram-parent').offsetWidth;
        var graphWidth = parentWidth * 0.8;
        var graphHeight = Math.floor(graphWidth * 2 / 3);

        if (graphWidth < 500) {
            graphWidth = 500;
            graphHeight = 333;
        }

        this.options = {
            width: graphWidth,
            height: graphHeight
        }

    },
    components: {
        Histogram
    }
}

</script>



