<template>
    <div>
        <h3>Confidence Level</h3>
        <table align="center" class="parameters">
            <tr>
                <td>Probability of Type I Error, <span v-html='alpha'></span></td>
                <td>
                    <input id="alpha-input" v-model.number="options.alpha"
                        type="number" step="0.001" />
                </td>
            </tr>
            <tr>
                <td>Confidence level</td>
                <td>
                    <input id="cl-input" v-model.number="confidenceLevel"
                        type="number" step="0.1" /> %
                </td>
            </tr>
        </table>

        <!--<form>
            <label for="alpha-input">
                <span v-html='alpha'></span>
            </label>
            <input id="alpha-input" v-model.number="options.alpha"
                type="number" step="0.001" /><br />

            <label for="cl-input">
                <span>Confidence Level:</span>
            </label>
            <input id="cl-input" v-model.number="confidenceLevel"
                type="number" step="0.1" /> %
        </form>-->
    </div>
</template>

<script>

export default {
    props: {
        testType: {
            type: String,
            required: true
        }
    },
    data: function() {
        return {
            alpha: '\\(\\alpha\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                if (this.$props.testType === 'mean') {
                    return this.$store.getters.options.testMean;
                }
                if (this.$props.testType === 'proportion') {
                    return this.$store.getters.options.testProportion;
                }
            },
            set: function(newValue) {
                if (this.$props.testType === 'mean') {
                    this.$store.dispatch('updateMeanTestOptions', newValue);
                }
                if (this.$props.testType === 'proportion') {
                    this.$store.dispatch('updateProportionTestOptions', newValue);
                }
            }
        },
        confidenceLevel: {
            get: function() {
                var alpha = this.options.alpha;
                return 100 * (1 - alpha);
            },
            set: function(newValue) {
                this.options.alpha = (1 - newValue / 100);
            }
        }
    },
}

</script>



