<template>
    <div>
        <h3>Parameters</h3>
        <table align="center" class="parameters">
            <tr>
                <td>Probability of success</td>
                <td>
                    <input id="geometric-p" type="number" step="0.0001" min="0" max="1"
                        v-model.number="options.geometric.p" />
                </td>
            </tr>
            <tr v-if="!options.cdf.calculateBetween">
                <td>Upper bound on number of trials</td>
                <td>
                    <input id="x-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.x" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Lower bound on number of trials</td>
                <td>
                    <input id="left-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.start" />
                </td>
            </tr>
            <tr v-if="options.cdf.calculateBetween">
                <td>Upper bound on number of trials</td>
                <td>
                    <input id="right-cdf" type="number" step="0.0001"
                        v-model.number="options.cdf.end" />
                </td>
            </tr>
        </table>

        <div v-if="result !== null && !isNaN(result) && showResults">
            <h3>Answer</h3>
            <div class="answer-holder">
                <div v-if="!options.cdf.calculateBetween">
                    If the probability of success for each trial is {{ options.geometric.p }}, then the probability that the first success will occur within the first {{ options.cdf.x }} attempt<span v-if="options.cdf.x !== 1">s</span> is
                </div>
                <div v-if="options.cdf.calculateBetween">
                    If the probability of success for each trial is {{ options.geometric.p }}, then the probability that the first success will occur after the {{ options.cdf.start }}<span v-if="options.cdf.start === 1">st</span><span v-if="options.cdf.start === 2">nd</span><span v-if="options.cdf.start === 3">rd</span><span v-if="options.cdf.start > 3">th</span> attempt and by the {{ options.cdf.end }}<span v-if="options.cdf.end === 1">st</span><span v-if="options.cdf.end === 2">nd</span><span v-if="options.cdf.end === 3">rd</span><span v-if="options.cdf.end > 3">th</span> attempt is
                </div>
                <span class="answer">{{ result.toPrecision(5) }}</span>
            </div>

            <table align="center" class="results-table">
                <tr>
                    <th>Trials</th>
                    <th>Probability</th>
                </tr>
                <tr v-for="(probability, index) in allResults">
                    <td>{{ index }}</td>
                    <td>{{ probability.toPrecision(5) }}</td>
                </tr>
                <tr style="border-top: 1px solid black">
                    <td>Total</td>
                    <td>{{ sumOfProbabilities.toPrecision(5) }}</td>
                </tr>
            </table>

            <!--<h3>Graph</h3>
            <div class="answer-holder">
                <span v-html="normalGraph"></span>
            </div>-->
        </div>

        <div v-if="invalidProbability" class="error-message">
            The probability of success must be between 0 and 1.
        </div>
        <div v-if="invalidX" class="error-message">
            The number of occurrences must be an integer greater than or equal to 0.
        </div>
    </div>
</template>

<script>
import distributions from '@/helpers/distributions.js';
//import distgraph from '@/helpers/graphs/distributions.js';

export default {
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.distributions;
            },
            set: function(newValue) {
                this.$store.dispatch('updateDistributionOptions', newValue);
            }
        },
        result: function() {
            if (this.options.cdf.calculateBetween) {
                if (!distributions.anyMissingParameters([this.options.cdf.start, this.options.cdf.end])) {
                    return distributions.geometricCDF(this.options.cdf.end, this.options.geometric.p) - distributions.geometricCDF(this.options.cdf.start, this.options.geometric.p);
                }
            } else {
                return distributions.geometricCDF(this.options.cdf.x, this.options.geometric.p);
            }
        },
        allResults: function() {
            var probabilities = {};

            if (this.options.cdf.calculateBetween) {
                for (var i = this.options.cdf.start + 1; i <= this.options.cdf.end; i++) {
                    probabilities[i] = distributions.geometricPDF(i, this.options.geometric.p);
                }
            } else {
                for (var i=1; i <= this.options.cdf.x; i++) {
                    probabilities[i] = distributions.geometricPDF(i, this.options.geometric.p);
                }
            }

            return probabilities;
        },
        sumOfProbabilities: function() {
            var sum = 0;
            for (var index in this.allResults) {
                sum += this.allResults[index];
            }
            return sum;
        },
        invalidX: function() {
            if (this.options.cdf.calculateBetween) {
                if (!Number.isInteger(this.options.cdf.start) || !Number.isInteger(this.options.cdf.end)) {
                    return true;
                }
                if (this.options.cdf.start <= 0 || this.options.cdf.end <= 0) {
                    return true;
                }
            } else {
                if (!Number.isInteger(this.options.cdf.x)) {
                    return true;
                }
                if (this.options.cdf.x <= 0) {
                    return true;
                }
            }

            return false;
        },
        invalidProbability: function() {
            if (this.options.geometric.p < 0 || this.options.geometric.p > 1) {
                return true;
            }
            return false;
        },
        showResults: function() {
            if (this.invalidX || this.invalidProbability) {
                return false;
            }
            return true;
        },
        /*normalGraph: function() {
            if (this.result !== null) {
                return distgraph.normal({
                    include: true,
                    proportion: this.result,
                    side: 'left'
                }, {});
            }
        }*/
    }
}

</script>



