<template>
    <div class="panel-tab" id='scatterplot-grid-container'>
        <h1>Scatterplot Grid</h1>

        <div class="scatterplot-grid-table">
            <table>
                <tr>
                    <th></th>
                    <th v-for="(column, index) in numericalData">
                        {{ column[0] }}
                    </th>
                </tr>
                <tr v-for="(columnX, indexX) in numericalData">
                    <th>
                        {{ columnX[0] }}
                    </th>
                    <td v-for="(columnY, indexY) in numericalData">
                        <Scatterplot v-if="indexY < indexX"
                            :dataColumns="[columnX, columnY]"
                            :options="scatterplotOptions">
                        </Scatterplot>
                        <Histogram v-if="indexY === indexX"
                            :dataColumn="columnX"
                            :options="histogramOptions">
                        </Histogram>
                        <span v-if="indexY > indexX"
                            class="scatterplot-grid-correlation">
                            r = {{ correlationMatrix[indexX][indexY] }}
                        </span>
                    </td>
                </tr>
            </table>

        </div>

        <!--<div class="scatterplot-grid-container">

        <div v-for="(columnX, indexX) in numericalData">
            <div v-for="(columnY, indexY) in numericalData">
                <Scatterplot :dataColumns="[columnX, columnY]"
                    :options="options">
                </Scatterplot>
            </div>
        </div>

    </div>-->


    </div>
</template>

<script>
import Scatterplot from './Scatterplot.vue';
import Histogram from '../histogram/Histogram.vue';

export default {
    data: function() {
        return {
            dimensions: {
                width: 300,
                height: 200,
                dotSize: 2
            }
        }
    },
    computed: {
        scatterplotOptions: function() {
            return {
                width: this.dimensions.width,
                height: this.dimensions.height,
                includePadding: false,
                includeTitles: false,
                includeRegressionLine: false,
                showXTicks: false,
                showYTicks: false,
                includeMinorXTicks: false,
                includeMinorYTicks: false,
                dotSize: this.dimensions.dotSize,
                includeTooltips: false
            }
        },
        histogramOptions: function() {
            return {
                width: this.dimensions.width,
                height: this.dimensions.height,
                numBins: 12,
                includePadding: false,
                includeTitles: false,
                showXTicks: false,
                showYTicks: false,
                includeMinorXTicks: false,
                includeMinorYTicks: false,
                includeTooltips: false
            }
        },
        numericalData: function() {
            var allDataColumns = this.$store.getters.dataAsColumns;
            var numericalColumnIndices = this.$store.getters.numericalColumnIndices;
            var numericalColumns = [];

            for (var i=0; i < numericalColumnIndices.length; i++) {
                numericalColumns.push(allDataColumns[numericalColumnIndices[i]]);
            }

            return numericalColumns;
        },
        correlationMatrix: function() {
            return this.$store.getters.correlationMatrix;
        }
    },
    mounted: function() {
        var parentWidth = document.getElementById('scatterplot-grid-container').offsetWidth;
        var graphWidth = parentWidth / (this.numericalData.length + 1);
        var graphHeight = Math.floor(graphWidth * 2 / 3);

        if (graphWidth < 100) {
            graphWidth = 100;
            graphHeight = 66;
        }

        var dotRadius = 2;
        if (graphWidth < 150) {
            dotRadius = 1;
        }

        this.dimensions = {
            width: graphWidth,
            height: graphHeight,
            dotSize: dotRadius
        }
    },
    components: {
        Scatterplot,
        Histogram
    }
}

</script>



