import jStat from 'jstat';
import data from './data.js';

export default {
    testMean: function(options) {
        if (!options.usingTwoSamples) {
            var ts = this.oneMeanTestStatistic(options);
        } else {
            var ts = this.twoMeanTestStatistic(options);
        }

        return {
            ts: ts,
            p: this.pValue(ts, options),
            confidenceInterval: this.confidenceInterval(options)
        }
    },
    testProportion: function(options) {
        options.usingZ = true;

        if (!options.usingTwoSamples) {
            var ts = this.oneProportionTestStatistic(options);
        } else {
            var ts = this.twoProportionTestStatistic(options);
        }

        return {
            ts: ts,
            p: this.pValue(ts, options),
            confidenceInterval: this.propConfidenceInterval(options)
        }
    },
    oneMeanTestStatistic: function(options) {
        var num = options.oneSample.xBar - options.hypothesis.value;

        if (options.usingZ) {
            var den = options.oneSample.popStdDev / Math.sqrt(options.oneSample.n);
        } else {
            var den = options.oneSample.sampleStdDev / Math.sqrt(options.oneSample.n);
        }

        var ts = num / den;

        return ts;
    },
    oneProportionTestStatistic: function(options) {
        var phat = options.oneSample.x / options.oneSample.n;
        var p = options.hypothesis.value;
        var num = phat - p;

        var den = p * (1 - p) / options.oneSample.n;
        den = Math.sqrt(den);

        var ts = num / den;

        return ts;
    },
    twoMeanTestStatistic: function(options) {
        var num = options.twoSample.xBar[0] - options.twoSample.xBar[1];

        if (options.usingZ) {
            var sd = options.twoSample.popStdDev;
        } else {
            var sd = options.twoSample.sampleStdDev;
        }

        var denSq = sd[0]*sd[0] / options.twoSample.n[0];
        denSq += sd[1]*sd[1] / options.twoSample.n[1];

        var den = Math.sqrt(denSq);
        var ts = num / den;

        return ts;
    },
    twoProportionTestStatistic: function(options) {
        var x1 = options.twoSample.x[0];
        var x2 = options.twoSample.x[1];
        var n1 = options.twoSample.n[0];
        var n2 = options.twoSample.n[1]

        var phat1 = x1 / n1;
        var phat2 = x2 / n2;
        var ppooled = (x1 + x2) / (n1 + n2);

        var num = (phat1 - phat2);
        var den = ppooled * (1 - ppooled) * (1/n1 + 1/n2);
        den = Math.sqrt(den);

        var ts = num / den;

        return ts;
    },
    pValue: function(ts, options) {
        if (ts === null || ts === Infinity) {
            return null;
        }

        if (options.usingZ) {
            if (options.hypothesis.comparison === 'lt') {
                return jStat.normal.cdf(ts, 0, 1);
            } else if (options.hypothesis.comparison === 'gt') {
                return 1 - jStat.normal.cdf(ts, 0, 1);
            } else if (options.hypothesis.comparison === 'neq') {
                if (ts < 0) {
                    return 2 * jStat.normal.cdf(ts, 0, 1);
                } else {
                    return 2 * (1 - jStat.normal.cdf(ts, 0, 1));
                }
            }
        } else {
            var df = this.df(options);
            if (options.hypothesis.comparison === 'lt') {
                return jStat.studentt.cdf(ts, df);
            } else if (options.hypothesis.comparison === 'gt') {
                return 1 - jStat.studentt.cdf(ts, df);
            } else if (options.hypothesis.comparison === 'neq') {
                if (ts < 0) {
                    return 2 * jStat.studentt.cdf(ts, df);
                } else {
                    return 2 * (1 - jStat.studentt.cdf(ts, df));
                }
            }
        }
    },
    df: function(options) {
        if (options.usingTwoSamples) {
            var num = options.twoSample.sampleStdDev[0]*options.twoSample.sampleStdDev[0] / options.twoSample.n[0];
            num += options.twoSample.sampleStdDev[1]*options.twoSample.sampleStdDev[1] / options.twoSample.n[1];
            num = num*num;

            var den1 = options.twoSample.sampleStdDev[0]*options.twoSample.sampleStdDev[0] / options.twoSample.n[0];
            den1 = den1*den1;
            den1 = den1 / (options.twoSample.n[0] - 1);

            var den2 = options.twoSample.sampleStdDev[1]*options.twoSample.sampleStdDev[1] / options.twoSample.n[1];
            den2 = den2*den2;
            den2 = den2 / (options.twoSample.n[1] - 1);

            var den = den1 + den2;

            var df = num / den;
        } else {
            var df = options.oneSample.n - 1;
        }

        return df;
    },
    confidenceInterval: function(options) {
        if (!options.usingTwoSamples) {
            var pointEstimate = options.oneSample.xBar;

            if (options.usingZ) {
                var se = options.oneSample.popStdDev / Math.sqrt(options.oneSample.n);
            } else {
                var se = options.oneSample.sampleStdDev / Math.sqrt(options.oneSample.n);
            }
        } else {
            var pointEstimate = options.twoSample.xBar[0] - options.twoSample.xBar[1];

            if (options.usingZ) {
                var sd = options.twoSample.popStdDev;
            } else {
                var sd = options.twoSample.sampleStdDev;
            }

            var se1 = sd[0]*sd[0];
            se1 = se1 / options.twoSample.n[0];

            var se2 = sd[1]*sd[1];
            se2 = se2 / options.twoSample.n[1];

            var se = se1 + se2;
            se = Math.sqrt(se);
        }

        var zt = this.ciZTValue(options);

        return {
            min: pointEstimate - zt * se,
            center: pointEstimate,
            max: pointEstimate + zt * se
        };
    },
    propConfidenceInterval: function(options) {
        if (!options.usingTwoSamples) {
            var phat = options.oneSample.x / options.oneSample.n
            var pointEstimate = phat;
            var se = phat * (1 - phat) / options.oneSample.n;
            se = Math.sqrt(se);
        } else {
            var n1 = options.twoSample.n[0];
            var n2 = options.twoSample.n[1];
            var phat1 = options.twoSample.x[0] / n1;
            var phat2 = options.twoSample.x[1] / n2;

            var pointEstimate = phat1 - phat2;
            var se = phat1 * (1 - phat1) / n1;
            se += phat2 * (1 - phat2) / n2;
            se = Math.sqrt(se);
        }

        var zt = this.ciZTValue(options);

        var minimum = pointEstimate - zt * se;
        var middle = pointEstimate;
        var maximum = pointEstimate + zt * se;

        /*if (minimum < 0) {
            minimum = 0;
        }
        if (maximum > 1) {
            maximum = 1;
        }*/

        return {
            min: minimum,
            center: middle,
            max: maximum
        };
    },
    ciZTValue: function(options) {
        var p = 1 - options.alpha / 2;
        if (options.usingZ) {
            return jStat.normal.inv(p, 0, 1);
        } else {
            var df = this.df(options);
            return jStat.studentt.inv(p, df);
        }
    },
    testGOF: function(options) {
        var obs = options.observedFrequencies;

        if (options.usingExpectedFrequencies) {
            var exp = options.expectedFrequencies;
        } else {
            var exp = [];
            for (var i=0; i < options.numberOfGroups; i++) {
                exp.push(options.expectedPercentages[i] * options.totalFrequency / 100);
            }
        }

        var chisq = 0;
        for (var i=0; i < options.numberOfGroups; i++) {
            chisq += (obs[i] - exp[i]) * (obs[i] - exp[i]) / exp[i];
        }

        var dof = options.numberOfGroups - 1;

        var pVal = 1 - jStat.chisquare.cdf(chisq, dof);

        return {
            chiSquare: chisq,
            df: dof,
            p: pVal
        }
    },

    testResults: function(test, input, options) {
        /*
        test: which test to carry out
        options: object {

        }
        input: {
          type: data or stats,
          values: array for data, object for stats
        }
        */
        return this.oneMeanZTest(input);
    },

    oneMeanZTest: function(inputStats) {
        // inputStats: value, mean, sd, sides (one or two), n, confidenceLevel
        // returns {z: Number, p: Number, ci: array [lower, upper]}
        var se = inputStats.sd / Math.sqrt(inputStats.n)
        var zscore = jStat.zscore(inputStats.value, inputStats.mean, se);
        var pvalue = jStat.ztest(inputStats.value, inputStats.mean, se, inputStats.sides);
        var alpha = 1 - inputStats.confidenceLevel;
        var interval = jStat.normalci(inputStats.mean, alpha, inputStats.sd, inputStats.n);

        return {
            testStatistic: {
                type: "z",
                value: this.rounded(zscore)
            },
            p: this.rounded(pvalue),
            se: this.rounded(se),
            ci: [this.rounded(interval[0]), this.rounded(interval[1])],
            comparisonValue: inputStats.value
        }
    },

    rounded: function(num) {
        return Number(num.toFixed(4));
    }
}



