<template>
    <div>
        <h3 v-if="!options.usingTwoSamples">Population Standard Deviation</h3>
        <table align="center" class="parameters" v-if="!options.usingTwoSamples">
            <tr>
                <td><span v-html='sigma'></span></td>
                <td>
                    <input id="sigma-input"
                        v-on:keyup="$emit('refreshMath')" v-model.number="options.oneSample.popStdDev"
                        type="number" step="0.01" />
                </td>
            </tr>
        </table>

        <h3 v-if="options.usingTwoSamples">Population Standard Deviations</h3>
        <table align="center" class="parameters"v-if="options.usingTwoSamples">
            <tr>
                <td><span v-html="sigma1"></span></td>
                <td>
                    <input id="sigma-input-1"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.popStdDev[0]"
                        type="number" step="0.01" />
                </td>
            </tr>
            <tr>
                <td><span v-html="sigma2"></span></td>
                <td>
                    <input id="sigma-input-2"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.popStdDev[1]"
                        type="number" step="0.01" />
                </td>
            </tr>
        </table>

        <!--<form v-if="!options.usingTwoSamples">
            <label for="sigma-input">
                <span v-html='sigma'></span>
            </label>
            <input id="sigma-input"
                v-on:keyup="$emit('refreshMath')" v-model.number="options.oneSample.popStdDev"
                type="number" step="0.01" /><br />
        </form>

        <h3 v-if="options.usingTwoSamples">Population Standard Deviations</h3>
        <form v-if="options.usingTwoSamples">
            <span>Population standard deviations:</span><br />
            <label for="sigma-input-1">
                <span v-html="sigma1"></span>
            </label>
            <input id="sigma-input-1"
                v-on:keyup="$emit('refreshMath')"
                v-model.number="options.twoSample.popStdDev[0]"
                type="number" step="0.01" /><br />
            <label for="sigma-input-2">
                <span v-html="sigma2"></span>
            </label>
            <input id="sigma-input-2"
                v-on:keyup="$emit('refreshMath')"
                v-model.number="options.twoSample.popStdDev[1]"
                type="number" step="0.01" /><br />
        </form>-->
    </div>
</template>

<script>

export default {
    data: function() {
        return {
            sigma: '\\(\\sigma\\)',
            sigma1: '\\(\\sigma_1\\)',
            sigma2: '\\(\\sigma_2\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testMean;
            },
            set: function(newValue) {
                this.$store.dispatch('updateMeanTestOptions', newValue);
            }
        }
    }
}

</script>



