<template>
    <div class="main-panel">
        <div v-if="variables !== undefined && variables.length > 0">
        <div class="nav-tabs">
            <ul>
                <li :class="{active: activeTab === 0}"
                    @click="tabNav(0)">
                    Descriptive Statistics
                </li>
                <li :class="{active: activeTab === 1}"
                    @click="tabNav(1)">
                    Separated by Category
                </li>
                <!--<li :class="{active: activeTab === 3}"
                    @click="tabNav(3)">
                    Summary Report
                </li>-->
                <li :class="{active: activeTab === 2}"
                    @click="tabNav(2)">
                    Correlations
                </li>
            </ul>
        </div>
        <SingleVariableStatistics v-if="activeTab === 0">
        </SingleVariableStatistics>
        <StatisticsByCategory v-if="activeTab === 1">
        </StatisticsByCategory>
        <!--<SummaryReportForVariable v-if="activeTab === 3">
        </SummaryReportForVariable>-->
        <MultivariateStatistics v-if="activeTab === 2">
        </MultivariateStatistics>
        </div>
    </div>
</template>

<script>
import SingleVariableStatistics from './stats/SingleVariableStatistics.vue';
import MultivariateStatistics from './stats/MultivariateStatistics.vue';
import SummaryReportForVariable from './stats/SummaryReportForVariable';
import StatisticsByCategory from './stats/StatisticsByCategory.vue';

export default {
    mounted: function() {
        MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
    },
    methods: {
        tabNav: function(tabId) {
            this.activeTab = tabId;
            this.$nextTick(function() {
                MathJax.Hub.Queue(["Typeset", MathJax.Hub]);
            });
        }
    },
    computed: {
        activeTab: {
            get: function() {
                return this.$store.getters.options.statisticsPage.activeTab;
            },
            set: function(newValue) {
                this.$store.dispatch('updateStatisticsPageOptions', {
                    activeTab: newValue
                });
            }
        },
        variables: function() {
            return this.$store.getters.variables;
        },
    },
    components: {
        SingleVariableStatistics,
        MultivariateStatistics,
        StatisticsByCategory,
        SummaryReportForVariable
    }
}

</script>



