<template>
    <div>
        <h3>Data Entry</h3>

        <table align="center" class="parameters" v-if="!options.usingTwoSamples">
            <tr>
                <td>Number of elements in a category, <span v-html="x"></span></td>
                <td>
                    <input id="x-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.oneSample.x"
                        type="number" />
                </td>
            </tr>
            <tr>
                <td>Total number of elements, <span v-html="n"></span></td>
                <td>
                    <input id="n-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.oneSample.n"
                        type="number" />
                </td>
            </tr>
        </table>

        <table align="center" class="parameters" v-if="options.usingTwoSamples">
            <tr>
                <td></td>
                <td style="text-align:center;">Group 1</td>
                <td style="text-align:center;">Group 2</td>
            </tr>
            <tr>
                <td><span v-html="x"></span></td>
                <td>
                    <input id="x1-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.x[0]"
                        type="number" />
                </td>
                <td>
                    <input id="x2-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.x[1]"
                        type="number" />
                </td>
            </tr>
            <tr>
                <td><span v-html="n"></span></td>
                <td>
                    <input id="n1-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.n[0]"
                        type="number" />
                </td>
                <td>
                    <input id="n2-input"
                        v-on:keyup="$emit('refreshMath')"
                        v-model.number="options.twoSample.n[1]"
                        type="number" />
                </td>
            </tr>
        </table>
        <div class="error-message" v-if="invalidData">
            x cannot be greater than n
        </div>
    </div>
</template>

<script>

export default {
    data: function() {
        return {
            x: '\\(x\\)',
            n: '\\(n\\)'
        }
    },
    computed: {
        options: {
            get: function() {
                return this.$store.getters.options.testProportion;
            },
            set: function(newValue) {
                this.$store.dispatch('updateProportionTestOptions', newValue);
            }
        },
        invalidData: function() {
            if (!this.options.usingTwoSamples) {
                if (this.options.oneSample.x > this.options.oneSample.n) {
                    return true;
                }
            } else {
                if (this.options.twoSample.x[0] > this.options.twoSample.n[0]) {
                    return true;
                }
                if (this.options.twoSample.x[1] > this.options.twoSample.n[1]) {
                    return true;
                }
            }
            return false;
        },
    }
}

</script>



