<template>
    <div class="popup-explanation" v-if="show">
        <h1>Five Number Summary</h1>
    </div>
</template>

<script>
export default {
    props: [
        'activePopup'
    ],
    computed: {
        fiveNumbers: function() {
            return ['fiveNumberSummary', 'min', 'q1', 'median-five', 'q3', 'max', 'interquartileRange'];
        },
        show: function() {
            if (this.fiveNumbers.indexOf(this.$props.activePopup) > -1) {
                return true;
            }
            return false;
        }
    }
}
</script>



