<template>
    <div class="sl-holder">
        <h2>{{ stemAndLeafData.variableName }}</h2>
        <table>
            <tr>
                <th>Stems</th><th style="text-align:left; padding-left: 20px">Leaves (level: {{ leafLevel }})</th>
            </tr>
            <tr v-for="row in stemAndLeafTable">
                <td style="text-align:center">{{ row.stem }}</td>
                <td style="padding-left:20px">
                    <span v-for="leaf in row.leaves">
                        {{ leaf }}
                    </span>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
import barplots from '@/helpers/graphs/barplots.js';
import jStat from 'jstat';

export default {
    props: {
        dataColumn: {
            type: Array,
            required: false
        },
        options: {
            type: Object,
            required: true
        }
    },
    data: function() {
        return {
            graphWidth: 600,
            graphHeight: 400
        }
    },
    computed: {
        leafLevel: function() {
            if (this.$props.options.powerOfTen !== undefined) {
                return 10**this.$props.options.powerOfTen;
            }
        },
        stemAndLeafData: function() {
            if (this.$props.dataColumn !== undefined) {
                return barplots.stemAndLeaf(this.$props.dataColumn, this.$props.options.powerOfTen);
            }
        },
        stemAndLeafTable: function() {
            if (this.stemAndLeafData !== undefined) {
                var table = [];

                var stems = this.stemAndLeafData.stems;
                var leaves = this.stemAndLeafData.leaves;

                var minStem = jStat.min(stems);
                var maxStem = jStat.max(stems);

                for (var i=minStem; i <= maxStem; i++) {
                    var index = stems.indexOf(i.toString());
                    if (index < 0) {
                        table.push({
                            stem: i,
                            leaves: ['']
                        });
                    } else {
                        table.push({
                            stem: i,
                            leaves: leaves[index]
                        });
                    }
                }

                return table;
            }

        }
    }
}

</script>



