<template>
    <div class="popup-explanation popup-checklist">
        <h1>Select Statistics to View</h1>
        <div class="data-btn-holder btn-group">
            <button class="btn" @click="showAllStatistics">
                Show All
            </button>
        </div>
        <div class="variable-checklist-holder">
            <div v-for="(statistic, index) in availableStatistics"
                class="variable-selector">
                <input type='checkbox' :id="'variable-' + index"
                        :value='statistic.id'
                        v-model='visibleStatistics'>
                <label :for="'variable-' + index">{{ statistic.displayName }}</label>
            </div>
            <button class="close-btn" @click="closeForm">Done</button>
        </div>
    </div>
</template>

<script>

export default {
    methods: {
        closeForm() {
            this.$emit('close');
            console.log(this.hiddenStatistics);
        },
        showAllStatistics() {
            this.hiddenStatistics = [];
        }
    },
    computed: {
        options: function() {
            return this.$store.getters.options.statisticsPage;
        },
        availableStatistics: function() {
            return this.options.availableStatistics;
        },
        hiddenStatistics: {
            get: function() {
                return this.options.hiddenStatistics;
            },
            set: function(hidden) {
                this.$store.dispatch('updateStatisticsPageOptions', {
                    hiddenStatistics: hidden
                });
            }
        },
        visibleStatistics: {
            get: function() {
                var hidden = this.hiddenStatistics;
                var visible = [];
                for (var i=0; i < this.availableStatistics.length; i++) {
                    var statisticId = this.availableStatistics[i].id;
                    var statisticDisplayName = this.availableStatistics[i].displayName;
                    if (hidden.indexOf(statisticId) < 0) {
                        visible.push(statisticId);
                    }
                }
                return visible;
            },
            set: function(visible) {
                var hidden = [];
                for (var i=0; i < this.availableStatistics.length; i++) {
                    var statisticId = this.availableStatistics[i].id;
                    var statisticDisplayName = this.availableStatistics[i].displayName;
                    if (visible.indexOf(statisticId) < 0) {
                        hidden.push(statisticId);
                    }
                }
                this.hiddenStatistics = hidden;
                /*this.$store.dispatch('updateStatisticsPageOptions', {
                    hiddenStatistics: hidden
                });*/
            }
        }
    }
}

</script>



